import React, { useState } from 'react'
import { flex } from 'styles/traits'
import styled from 'styled-components'
import { useApolloClient } from '@apollo/react-hooks'
import { ADD_SITE_TO_USER, REMOVE_SITE_FROM_USER } from 'graphql/mutations'
import { GET_SITE_NAV, GET_SIDEBAR_SITES_LIST } from 'graphql/queries'
import Tooltip from 'components/Tooltip'
import { Spinner } from 'components'
import { Star } from 'react-feather'

const AddRemoveButton = styled.button`
  ${flex.center_both};
  margin: 0 0 0 0.5rem;
  padding: none;
  background: none;
  border: none;
  cursor: pointer;

  svg {
    fill: ${({ theme, starred }) => (starred ? theme.special.alt : 'none')};
    color: ${({ theme }) => theme.special.alt};
  }
  &:hover {
    svg {
      fill: ${({ theme, starred }) =>
        starred ? theme.special.default : 'none'};
      color: ${({ theme }) => theme.special.default};
    }
  }
`

export default function AddRemoveSite({ sitename, starred }) {
  const client = useApolloClient()
  const [loading, setLoading] = useState(false)

  const addSite = () => {
    setLoading(true)
    client
      .mutate({
        mutation: ADD_SITE_TO_USER,
        variables: { name: sitename },
        refetchQueries: [
          { query: GET_SITE_NAV, variables: { name: sitename } },
          { query: GET_SIDEBAR_SITES_LIST },
        ],
      })
      .then(() => {
        setLoading(false)
      })
  }

  const removeSite = () => {
    setLoading(true)
    client
      .mutate({
        mutation: REMOVE_SITE_FROM_USER,
        variables: { name: sitename },
        refetchQueries: [
          { query: GET_SITE_NAV, variables: { name: sitename } },
          { query: GET_SIDEBAR_SITES_LIST },
        ],
      })
      .then(() => {
        setLoading(false)
      })
  }

  const tooltipText = starred
    ? `Remove ${sitename} from Sites list`
    : `Add ${sitename} to Sites list`

  const onClickAction = starred ? removeSite : addSite

  return (
    <Tooltip
      content={tooltipText}
      arrow={true}
      placement="top"
      animation="scale"
      animateFill={false}
      duration={[250, 175]}
      delay={[150, 0]}
      distance={8}
    >
      <AddRemoveButton starred={starred} onClick={onClickAction}>
        {loading ? <Spinner /> : <Star />}
      </AddRemoveButton>
    </Tooltip>
  )
}
