import React from 'react'
import { Text } from 'components'
import styled, { css, keyframes } from 'styled-components'
import { flex, spacing } from 'styles/traits'
import { Clock, Loader, Check, X, ChevronUp, ChevronDown } from 'react-feather'
import { Transition } from 'styles/globals'

const animation = keyframes`
  0% {transform: rotate(0);}
  100% {transform: rotate(360deg);}
`

const statusIcons = {
  queued: <Clock />,
  pending: <Loader />,
  success: <Check />,
  failed: <X />,
}

export const Container = styled.header`
  cursor: pointer;
  ${flex.justify_space_btw};
  ${spacing.p1};
  border-radius: 0.25rem 0.25rem 0 0;
  span {
    color: ${({ theme }) => theme.text.reverse};
  }

  ${props =>
    props.status === 'queued' &&
    css`
      background: ${({ theme }) => theme.special.default};
    `};
  ${props =>
    props.status === 'success' &&
    css`
      background: ${({ theme }) => theme.success.default};
    `};
  ${props =>
    props.status === 'pending' &&
    css`
      background: ${({ theme }) => theme.special.default};
    `};
  ${props =>
    props.status === 'failed' &&
    css`
      background: ${({ theme }) => theme.warn.default};
    `};

  transition: ${Transition.hover.off};

  > * + * {
    ${spacing.ml1};
  }
`

const IconContainer = styled.div`
  ${({ status }) =>
    status === 'pending' &&
    css`
      animation-name: ${animation};
      animation-duration: 2000ms;
      animation-iteration-count: infinite;
      animation-timing-function: linear;
      transform-origin: 12px 12px;
    `};
  svg {
    color: ${({ theme }) => theme.text.reverse};
  }
`

export const Content = styled.div`
  ${flex.align_center};
  > * + * {
    ${spacing.ml05};
  }
`

export const ToggleButton = styled.button`
  background: none;
  border: none;
  cursor: pointer;
`

const Header = ({ expanded, status, description, toggleExpanded }) => {
  return (
    <Container status={status} onClick={toggleExpanded}>
      <Content>
        <IconContainer status={status}>{statusIcons[status]}</IconContainer>
        <Text weight="bold"> - </Text>
        <Text weight="bold">{status}</Text>
        <Text weight="bold"> - </Text>
        <Text weight="bold">{description}</Text>
      </Content>
      <Content>
        <ToggleButton>
          <IconContainer>
            {expanded ? <ChevronUp /> : <ChevronDown />}
          </IconContainer>
        </ToggleButton>
      </Content>
    </Container>
  )
}

export default Header
