import styled, { css } from 'styled-components'
import { border } from 'styles/traits'

export const TOP_BAR_HEIGHT = '4rem'
export const SIDE_BAR_HEADER_HEIGHT = '4rem'
export const SIDE_BAR_TABLET_HEADER_HEIGHT = '4.5rem'

export const Wrapper = styled.div`
  grid-area: sidebar;
  position: sticky;
  max-height: calc(100vh - ${TOP_BAR_HEIGHT});
  top: ${TOP_BAR_HEIGHT};

  display: grid;
  grid-template-rows: ${SIDE_BAR_HEADER_HEIGHT} auto;

  @media only screen and (min-width: 993px) and (max-width: 1170px) {
    grid-template-rows: ${SIDE_BAR_TABLET_HEADER_HEIGHT} auto;
  }

  grid-template-areas:
    'side-bar-header'
    'side-bar-content';

  ${border.br};
  box-sizing: border-box;
  background: ${({ theme }) => theme.bg.wash};
  border-color: ${({ theme }) => theme.bg.border};
  overflow: hidden;

  @media only screen and (max-width: 992px) {
    transform: translateX(${({ show }) => (show ? 0 : -16)}rem);
    position: absolute;
    z-index: 99;
    width: 16rem;
    height: 100vh;
    transition: 500ms ease all;
    box-shadow: 0 0 ${({ show }) => (show ? 2 : 0)}rem 0 black;
  }
`

export const Body = styled.div`
  grid-area: side-bar-content;
`

export const ClickOutside = styled.div`
  pointer-events: none;
  position: absolute;
  z-index: 98;
  height: 100vh;
  width: 100%;
  background: none;
  transition: 500ms ease all;

  @media only screen and (max-width: 992px) {
    ${props =>
      props.show &&
      css`
        display: block;
        pointer-events: auto;
        background: black;
        opacity: 0.5;
      `};
  }
`
