import React from 'react'
import SiteCard from 'views/SiteCard'
import styled from 'styled-components'

const Grid = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 1rem;

  @media only screen and (max-width: 1459px) {
    grid-template-columns: 1fr 1fr;
  }

  @media only screen and (max-width: 811px) {
    grid-gap: 0.5rem;
    grid-template-columns: 1fr;
  }
`

const sortByLastCommit = (a, b) => {
  const getLastCommit = item => item?.commitsFeed?.commits[0]?.timestamp
  const timeStampA = getLastCommit(a) || 0
  const timeStampB = getLastCommit(b) || 0
  return parseInt(timeStampB) - parseInt(timeStampA)
}

const mapToSiteCards = (site, index) => {
  const {
    repository_name: repositoryName,
    created_at: siteCreated,
    name: sitename,
    starred,
  } = site
  const lastCommit = site?.commitsFeed?.commits[0]?.timestamp
  return (
    <SiteCard
      key={index}
      mode="link"
      starred={starred}
      sitename={sitename}
      repositoryName={repositoryName}
      lastCommit={lastCommit}
      siteCreated={siteCreated}
    />
  )
}

export default function SitesList({ loading, sites }) {
  if (loading) {
    return (
      <Grid>
        <SiteCard loading />
        <SiteCard loading />
        <SiteCard loading />
        <SiteCard loading />
      </Grid>
    )
  }

  return <Grid>{sites.sort(sortByLastCommit).map(mapToSiteCards)}</Grid>
}
