import React, { Component } from 'react'
import styled, { css } from 'styled-components'
import { flex, spacing } from 'styles/traits'
import { NavLink } from 'react-router-dom'
import SiteLogo from 'components/SiteLogo'
import { Text } from 'components'
import { ImagePlaceholder, TextPlaceholder } from 'components/Placeholders'

const activeClassName = 'active-link'

const defaultStyle = css`
  ${flex.align_center};
  ${spacing.pv1.ph1};
  text-decoration: none;
  box-sizing: border-box;
  max-width: 16rem;

  > * + * {
    ${spacing.ml1};
  }
  section {
    > span {
      color: hsl(214, 10%, 55%);
    }
  }
`

const activeStyle = css`
  &.${activeClassName} {
    background: ${({ theme }) => theme.bg.default};
    box-shadow: ${({ theme }) => theme.bg.border} 0px -1px 0px inset,
      ${({ theme }) => theme.bg.border} 0px -1px 0px;
    section {
      span {
        color: ${({ theme }) => theme.text.default};
      }
    }
  }
`

const hoverStyle = css`
  &:hover {
    background: ${({ theme }) => theme.bg.default};
    box-shadow: ${({ theme }) => theme.bg.border} 0px -1px 0px inset,
      ${({ theme }) => theme.bg.border} 0px -1px 0px;
    section {
      span {
        color: ${({ theme }) => theme.text.default};
      }
    }
  }
`

const ExpandedContainerPlaceholder = styled.div`
  ${defaultStyle};
  ${hoverStyle};

  @media only screen and (max-width: 1170px) {
    display: none;
  }
  @media only screen and (max-width: 992px) {
    display: flex;
  }
`

const ExpandedContainer = styled(({ ...props }) => (
  <NavLink {...props} />
)).attrs({
  activeClassName,
})`
  ${defaultStyle};
  ${activeStyle};
  ${hoverStyle};

  @media only screen and (max-width: 1170px) {
    display: none;
  }
  @media only screen and (max-width: 992px) {
    display: flex;
  }
`

const ContractedContainerPlaceholder = styled.div`
  ${defaultStyle};
  ${hoverStyle};

  display: none;

  @media only screen and (max-width: 1170px) {
    display: flex;
  }
  @media only screen and (max-width: 992px) {
    display: none;
  }
`

const ContractedContainer = styled(({ ...props }) => (
  <NavLink {...props} />
)).attrs({
  activeClassName,
})`
  ${defaultStyle};
  ${activeStyle};
  ${hoverStyle};
  display: none;

  @media only screen and (max-width: 1170px) {
    display: flex;
  }
  @media only screen and (max-width: 992px) {
    display: none;
  }
`

const Left = styled.div``

const Right = styled.section`
  ${flex.vertical};
  white-space: nowrap;
  min-width: 0;
  max-width: 15rem;
  > * + * {
    ${spacing.mt025};
  }
`

class Link extends Component {
  render() {
    const { loading, setShow, sitename, repositoryName, ...rest } = this.props
    if (loading) {
      return (
        <>
          <ExpandedContainerPlaceholder>
            <Left>
              <ImagePlaceholder />
            </Left>
            <Right>
              <TextPlaceholder />
              <TextPlaceholder />
            </Right>
          </ExpandedContainerPlaceholder>
          <ContractedContainerPlaceholder>
            <ImagePlaceholder />
          </ContractedContainerPlaceholder>
        </>
      )
    }
    return (
      <>
        <ExpandedContainer
          {...rest}
          title={sitename}
          onClick={() => setShow(false)}
        >
          <Left>
            <SiteLogo name={sitename} />
          </Left>
          <Right>
            <Text weight="medium" truncate lineHeight={1.2}>
              {sitename}
            </Text>
            <Text scale={6} tone="secondary" truncate lineHeight={1.2}>
              {repositoryName}
            </Text>
          </Right>
        </ExpandedContainer>
        <ContractedContainer
          {...rest}
          title={sitename}
          onClick={() => setShow(false)}
        >
          <SiteLogo name={sitename} />
        </ContractedContainer>
      </>
    )
  }
}

export default Link
