import React from 'react'
import { Wrapper, StyledSection } from './style'
import Toggle from './components/Toggle'
import Brand from './components/Brand'
import { AdminBadge, BetaBadge, DemoBadge } from './components/Badges'
import Links from './components/Links'
import UserControls from './components/UserControls'
import config from 'config'

export default function TopBar(props) {
  const { toggleSideBar } = props
  const isDemo = config.DEMO_MODE
  const isAdmin = config.ADMIN_MODE
  return (
    <Wrapper>
      <StyledSection direction="row">
        <Toggle toggleSideBar={toggleSideBar} />
        <Brand />
        {isDemo ? <DemoBadge /> : isAdmin ? <AdminBadge /> : <BetaBadge />}
      </StyledSection>
      <StyledSection />
      <StyledSection style={{ justifyContent: 'flex-end' }} direction="row">
        <Links />
        <UserControls />
      </StyledSection>
    </Wrapper>
  )
}
