import React from 'react'
import PropTypes from 'prop-types'
import {
  StyledTextButton,
  StyledSolidButton,
  StyledOutlineButton,
  Label,
} from './style'
import { Spinner } from 'components'

const buttonProps = {
  loading: PropTypes.bool,
  disabled: PropTypes.bool,
  color: PropTypes.string,
  children: PropTypes.oneOfType([
    PropTypes.object,
    PropTypes.array,
    PropTypes.string,
  ]),
  large: PropTypes.bool,
}

export const Button = props => (
  <StyledSolidButton disabled={props.loading} {...props}>
    {props.loading && (
      <Spinner color="monochrome" size={props.large ? 2 : 0.6} />
    )}
    {!props.loading && <Label>{props.children}</Label>}
  </StyledSolidButton>
)
Button.propTypes = buttonProps

export const TextButton = props => (
  <StyledTextButton disabled={props.loading} {...props}>
    {props.loading && <Spinner size={props.large ? '18' : 0.6} />}
    {!props.loading && <Label>{props.children}</Label>}
  </StyledTextButton>
)
TextButton.propTypes = buttonProps

export const OutlineButton = props => (
  <StyledOutlineButton disabled={props.loading} {...props}>
    {props.loading && (
      <Spinner color="monochrome" size={props.large ? 2 : 0.6} />
    )}
    {!props.loading && <Label>{props.children}</Label>}
  </StyledOutlineButton>
)
OutlineButton.propTypes = buttonProps
