import React from 'react'
import styled from 'styled-components'
import { useQuery } from '@apollo/react-hooks'

import { useDocumentTitle } from 'hooks'

import { Form, FormBody, FormHeader } from 'components/FormElements'
import { Text, StatusMessage } from 'components'
import NullState from 'components/NullState'
import Stack from 'components/Stack'

import { GET_DATA } from './graphql'

import Block from '../Block'
import Placeholder from '../Placeholder'
import AddCollaborator from './components/AddCollaborator'
import Collaborator from './components/Collaborator'

const StyledNullState = styled(NullState)`
  padding: 1rem;
`

const Collaborators = ({ collaborators, sitename, isDisabled }) =>
  collaborators.map(item => (
    <Collaborator
      key={item.user_id}
      sitename={sitename}
      collaborator={item}
      isDisabled={isDisabled}
    />
  ))

export default function AccessControl({ sitename }) {
  useDocumentTitle(`Access control`)

  const { loading, error, data } = useQuery(GET_DATA, {
    variables: { name: sitename },
  })

  if (error) {
    return (
      <StatusMessage status="critical">
        <Text>{error}</Text>
      </StatusMessage>
    )
  }

  if (loading) {
    return <Placeholder />
  }

  const site = data?.view?.site
  if (site) {
    const access_control = data?.view?.site?.settings?.access_control
    const collaborators = access_control?.collaborators || []

    const collaboratorsExcludingCurrentUser = collaborators.filter(
      item => item.is_current_user === false
    )

    const isDisabled = !site.access.admin

    return (
      <Block
        blockTitle="Access Control"
        blockSubtitle="Grant other users access to your site"
        blockForm={
          <Form>
            <FormHeader>
              <Text>Collaborators</Text>
            </FormHeader>
            <FormBody>
              {!isDisabled && <AddCollaborator sitename={sitename} />}
              {collaboratorsExcludingCurrentUser.length > 0 ? (
                <Stack gap={0}>
                  <Collaborators
                    collaborators={collaboratorsExcludingCurrentUser}
                    isDisabled={isDisabled}
                    sitename={sitename}
                  />
                </Stack>
              ) : (
                <StyledNullState>
                  <Text tone="secondary">
                    This site does not have any collaborators yet
                  </Text>
                </StyledNullState>
              )}
            </FormBody>
          </Form>
        }
      />
    )
  } else {
    return (
      <StatusMessage status="critical">
        <Text>Error: unable to retrieve site settings data</Text>
      </StatusMessage>
    )
  }
}
