import { observable, toJS } from 'mobx'
import {
  APP_FLAGS_LS_KEY,
  getItemFromLocalStorage,
  saveItemToLocalStorage,
} from 'helpers/localStorage'

const INITIAL_APP_FLAGS_DATA = {
  name: 'LincAppFlags',
  version: '0.0.1',
  sites: {},
}
const INITIAL_SITE_FLAGS_DATA = {
  showDeploymentConfigPrompt: true,
}

const initialStoreData =
  getItemFromLocalStorage(APP_FLAGS_LS_KEY) || INITIAL_APP_FLAGS_DATA

const store = observable(initialStoreData)

export const setSiteFlags = (sitename, flags) => {
  store.sites[sitename] = flags
  updateLocalStorage()
}

export const updateLocalStorage = () => {
  const item = toJS(store)
  saveItemToLocalStorage(APP_FLAGS_LS_KEY, item)
}

export const getSiteFlags = sitename => {
  const siteFlags = store.sites[sitename]
  if (siteFlags) {
    return siteFlags
  } else {
    setSiteFlags(sitename, INITIAL_SITE_FLAGS_DATA)
    return store.sites[sitename]
  }
}

export const setSiteFlag = (sitename, flag, value) => {
  store.sites[sitename][flag] = value
  updateLocalStorage()
}
