import React, { Fragment } from 'react'
import styled from 'styled-components'
import { typography, flex, spacing } from 'styles/traits'
import { Text } from 'components'
import { NavLink, Link } from 'react-router-dom'
import { GitHub } from 'react-feather'
import { useQuery } from '@apollo/react-hooks'
import AddRemoveSite from './AddRemoveSite'
import { GET_SITE_NAV } from 'graphql/queries'
import { useParams } from 'react-router'

const Wrapper = styled.div`
  display: grid;
  grid-template-columns: auto auto;
  @media only screen and (max-width: 1170px) {
    grid-template-columns: auto;
    grid-template-rows: auto auto;
    grid-gap: 2rem;
  }
`

const StyledLink = styled(Link)`
  text-decoration: none;
  color: inherit;
  &:hover {
    text-decoration: underline;
    color: ${({ theme }) => theme.space.default};
    > span {
      color: ${({ theme }) => theme.space.default};
    }
  }
`

const StyledAnchor = styled.a`
  color: inherit;
  &:hover {
    color: ${({ theme }) => theme.space.default};
  }
`

const activeClassName = 'active-header-link'
const SitePageLink = styled(NavLink).attrs({
  activeClassName,
})`
  text-decoration: none;
  color: inherit;
  &:hover {
    text-decoration: underline;
    color: ${({ theme }) => theme.space.default};
    > span {
      color: ${({ theme }) => theme.space.default};
    }
  }
  &.${activeClassName} {
    text-decoration: underline;
  }
`

const Links = styled.div`
  display: flex;
  align-items: center;
  justify-content: flex-end;
  > * + * {
    ${spacing.ml1};
  }

  @media only screen and (max-width: 1170px) {
    justify-content: flex-start;
  }
`

const Middot = styled.div`
  ${flex};
  ${typography.f3};
`

const GithubLink = styled.span`
  ${flex.center_both};
  ${spacing.ml1};
  svg {
    margin-top: 2px;
    width: 1.2rem;
  }
`

const Left = styled.div`
  display: flex;
  align-items: center;
`

function NavMeta({ sitename }) {
  const { loading, error, data } = useQuery(GET_SITE_NAV, {
    variables: { name: sitename },
    fetchPolicy: 'cache-first',
  })

  if (loading) {
    return ''
  }

  if (error) {
    // console.log(error)
  }

  const site = data?.view?.site
  if (site) {
    const { repository_name: repositoryName, starred } = site
    return (
      <Fragment>
        <AddRemoveSite sitename={sitename} starred={starred} />
        <StyledAnchor
          href={`https://github.com/${repositoryName}`}
          target="_blank"
          rel="noopener noreferrer"
        >
          <GithubLink>
            <GitHub />
          </GithubLink>
        </StyledAnchor>
      </Fragment>
    )
  } else {
    return ''
  }
}

export default function Nav() {
  const { sitenameSlug } = useParams()
  const ROOT = `/sites/${sitenameSlug}`
  const FEEDBACK = `${ROOT}/feedback`
  const ENVIRONMENTS = `${ROOT}/environments`
  const SETTINGS = `${ROOT}/settings`
  return (
    <Wrapper>
      <Left internalSpacing={0.5}>
        <StyledLink to={ROOT}>
          <Text scale={2} weight="bold">
            {sitenameSlug}
          </Text>
        </StyledLink>
        <NavMeta sitename={sitenameSlug} />
      </Left>
      <Links>
        <SitePageLink exact to={ROOT}>
          <Text weight="medium">Overview</Text>
        </SitePageLink>
        <Middot>&middot;</Middot>
        <SitePageLink exact to={FEEDBACK}>
          <Text weight="medium">Feedback</Text>
        </SitePageLink>
        <Middot>&middot;</Middot>
        <SitePageLink exact to={ENVIRONMENTS}>
          <Text weight="medium">Environments</Text>
        </SitePageLink>
        <Middot>&middot;</Middot>
        <SitePageLink exact to={SETTINGS}>
          <Text weight="medium">Settings</Text>
        </SitePageLink>
      </Links>
    </Wrapper>
  )
}
