import { useState } from 'react'

const setPropertyOnArray = (arr, index, property, value) => {
  const newArr = [...arr]
  newArr[index][property] = value
  return newArr
}

export const useBuildEnvVariables = initialState => {
  const [variables, setVariables] = useState(initialState)

  const addVariable = () =>
    setVariables([...variables, { name: '', value: '' }])

  const deleteVariable = index => {
    const newVariables = [...variables]
    newVariables.splice(index, 1)
    setVariables(newVariables)
  }

  const setVariableName = (value, index) => {
    const newVariables = setPropertyOnArray(variables, index, 'name', value)
    setVariables(newVariables)
  }

  const setVariableValue = (value, index) => {
    const newVariables = setPropertyOnArray(variables, index, 'value', value)
    setVariables(newVariables)
  }

  const reset = () => setVariables(initialState)

  const isValid = variables.every(
    item => item.name.length > 0 && item.value.length > 0
  )

  return {
    all: variables,
    addVariable,
    deleteVariable,
    setVariableName,
    setVariableValue,
    reset,
    isValid,
  }
}
