import styled from 'styled-components'
import PropTypes from 'prop-types'
import { spacing } from 'styles/traits'

const resolveValue = {
  '0': '0',
  '0.25': '025',
  '0.5': '05',
  '1': '1',
  '1.5': '15',
  '2': '2',
  '3': '3',
  '4': '4',
}

const FlexSectionPropTypes = {
  padding: PropTypes.oneOf([0, 0.25, 0.5, 1, 1.5, 2, 3, 4]),
  internalSpacing: PropTypes.oneOf([0, 0.25, 0.5, 1, 1.5, 2, 3, 4]),
}
const FlexSectionDefaultProps = {
  padding: 0,
  internalSpacing: 1,
}

export const FlexRow = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: stretch;
  ${({ padding }) => spacing[`p${resolveValue[padding]}`]};
  > * + * {
    ${({ internalSpacing }) => spacing[`ml${resolveValue[internalSpacing]}`]}
  }
`
FlexRow.propTypes = FlexSectionPropTypes
FlexRow.defaultProps = FlexSectionDefaultProps

export const FlexCol = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
  ${({ padding }) => spacing[`p${resolveValue[padding]}`]};
  > * + * {
    ${({ internalSpacing }) => spacing[`mt${resolveValue[internalSpacing]}`]}
  }
`
FlexCol.propTypes = FlexSectionPropTypes
FlexCol.defaultProps = FlexSectionDefaultProps
