import React, { useState, useEffect } from 'react'
import { Text, TextField, StatusMessage } from 'components'
import { TextButton, Button } from 'components/Buttons'
import { GET_DATA } from './graphql'
import { GET_RELEASE_STATUS_CARD } from 'graphql/queries'
import { Form, FormHeader, FormBody, FormFooter } from 'components/FormElements'
import Inline from 'components/Inline'
import Stack from 'components/Stack'
import { useMutation } from '@apollo/react-hooks'

const Row = ({
  heading,
  subHeading,
  value,
  readOnly,
  onChange,
  masked,
  canBeRevealed,
}) => {
  return (
    <Stack gap={0.5}>
      <Text weight="medium">{heading}</Text>
      {subHeading && (
        <Text scale={6} tone="secondary">
          {subHeading}
        </Text>
      )}
      <TextField
        masked={masked}
        canBeRevealed={canBeRevealed}
        inputProps={{
          readOnly: readOnly,
          isRequired: true,
          value: value,
          onChange: onChange,
        }}
      />
    </Stack>
  )
}

const ConfigForm = props => {
  const { sitename } = props
  const [updating, setUpdating] = useState(false)
  const [deleting, setDeleting] = useState(false)
  const [error, setError] = useState(null)
  const [fields, setFields] = useState(props.initialState)
  const [dirty, setDirty] = useState(false)
  const [readOnly, setReadOnly] = useState(true)
  const { admin } = props.siteAccess

  useEffect(() => {
    // Mutations will trigger a query refetch which
    // results in new props, specifically initialState.
    // When initialState prop changes, set fields to new
    // initialState prop automatically
    setFields(props.initialState)
  }, [props.initialState])

  const updateField = (field, value) => {
    const newFields = { ...fields }
    newFields[field] = value
    setFields(newFields)
  }

  const resetForm = () => {
    setUpdating(false)
    setDeleting(false)
    setDirty(false)
    setReadOnly(true)
  }

  const isValid = Object.values(fields).every(field => field.length > 0)
  const isNullState = Object.values(fields).every(field => field.length === 0)

  const refetchQueries = [
    { query: GET_DATA, variables: { name: sitename } },
    { query: GET_RELEASE_STATUS_CARD, variables: { name: sitename } },
  ]

  const [updateDeployConfigMutation] = useMutation(props.updateMutation, {
    refetchQueries,
    onCompleted: resetForm,
    onError: e => {
      setUpdating(false)
      setError(e)
    },
  })
  const [deleteDeployConfigMutation] = useMutation(props.deleteMutation, {
    refetchQueries,
    onCompleted: resetForm,
    onError: e => {
      setDeleting(false)
      setError(e)
    },
  })

  const updateDeployConfig = () => {
    setUpdating(true)
    const variables = {
      sitename: sitename,
      input: { ...fields },
    }
    updateDeployConfigMutation({ variables })
  }
  const deleteDeployConfig = () => {
    setDeleting(true)
    const variables = {
      sitename: sitename,
      deploy_target_name: props.deploy_target_name,
    }
    deleteDeployConfigMutation({ variables })
  }

  return (
    <Form
      onSubmit={e => {
        updateDeployConfig()
        e.preventDefault()
      }}
    >
      <FormHeader>
        <Text scale={4} weight="medium">
          {props.heading}
        </Text>
      </FormHeader>
      <FormBody>
        {isNullState && readOnly ? (
          <Text>{props.nullStateMessage}</Text>
        ) : (
          Object.keys(fields).map((fieldKey, index) => (
            <Row
              key={index}
              heading={props.fieldDefs[fieldKey].label}
              value={fields[fieldKey]}
              readOnly={readOnly}
              masked={readOnly && !props.fieldDefs[fieldKey].public}
              canBeRevealed={admin}
              onChange={e => {
                setDirty(true)
                updateField(fieldKey, e.target.value)
              }}
            />
          ))
        )}

        {error && (
          <StatusMessage status="critical">
            <Text>{error.message}</Text>
          </StatusMessage>
        )}
      </FormBody>
      {admin && (
        <FormFooter>
          <Inline gap={1}>
            {readOnly ? (
              <Button type="button" onClick={() => setReadOnly(false)}>
                {isNullState ? `Create ${props.heading} config` : 'Edit'}
              </Button>
            ) : (
              <>
                <TextButton
                  type="button"
                  onClick={deleteDeployConfig}
                  disabled={updating || deleting}
                  loading={deleting}
                >
                  Delete
                </TextButton>
                <TextButton
                  type="button"
                  onClick={resetForm}
                  disabled={updating || deleting}
                >
                  Cancel
                </TextButton>
                <Button
                  type="submit"
                  disabled={!isValid || !dirty || updating || deleting}
                  loading={updating}
                >
                  Save
                </Button>
              </>
            )}
          </Inline>
        </FormFooter>
      )}
    </Form>
  )
}

export default ConfigForm
