import React, { useState, Fragment } from 'react'
import { Text, TextField, StatusMessage } from 'components'
import { TextButton, Button } from 'components/Buttons'
import { UPDATE_ENVIRONMENT, DELETE_ENVIRONMENT } from 'graphql/mutations'
import { GET_ENVIRONMENTS } from 'graphql/queries'
import {
  Form,
  FormHeader,
  FormBody,
  FormFooter,
  CheckboxRowStyle,
} from 'components/FormElements'
import { useEnvironmentSettings } from './utils'
import { X, PlusCircle, CheckSquare, Square } from 'react-feather'
import {
  updateEnvironmentVariables,
  deleteEnvironmentVariables,
} from 'graphql/variableFormatters'
import { AddSettingButton, DeleteSettingButton, RowGrid } from './style'
import ColorDot from 'components/ColorDot'
import Inline from 'components/Inline'
import Stack from 'components/Stack'
import { useApolloClient } from '@apollo/react-hooks'

const EnvironmentForm = props => {
  const client = useApolloClient()
  const { environment } = props
  const [error, setError] = useState(null)
  const [saving, setSaving] = useState(false)
  const [deleting, setDeleting] = useState(false)
  const [autoDeploy, setAutoDeploy] = useState(
    typeof environment.auto_deploy === 'boolean'
      ? environment.auto_deploy
      : true
  )
  const settings = useEnvironmentSettings(environment.settings || [])
  const [dirty, setDirty] = useState(false)
  const { write } = props.siteAccess

  const updateEnvironment = () => {
    setSaving(true)
    const { environment } = props
    client
      .mutate({
        mutation: UPDATE_ENVIRONMENT,
        variables: updateEnvironmentVariables(
          environment.sitename,
          environment.env_name,
          autoDeploy,
          settings.getAll()
        ),
        refetchQueries: [
          {
            query: GET_ENVIRONMENTS,
            variables: { name: environment.sitename },
          },
        ],
      })
      .then(() => {
        setSaving(false)
        setDirty(false)
      })
      .catch(e => {
        setSaving(false)
        setError(e)
      })
  }

  const deleteEnvironment = () => {
    setDeleting(true)
    const { environment } = props
    client
      .mutate({
        mutation: DELETE_ENVIRONMENT,
        variables: deleteEnvironmentVariables(
          environment.sitename,
          environment.env_name
        ),
        refetchQueries: [
          {
            query: GET_ENVIRONMENTS,
            variables: { name: environment.sitename },
          },
        ],
      })
      .catch(e => {
        setDeleting(false)
        setError(e)
      })
  }

  const reset = () => {
    const { auto_deploy } = props.environment
    settings.reset()
    setAutoDeploy(typeof auto_deploy === 'boolean' ? auto_deploy : true)
    setError(null)
    setDirty(false)
  }

  return (
    <Form
      onSubmit={e => {
        updateEnvironment()
        e.preventDefault()
      }}
    >
      <FormHeader>
        <Inline gap={0.5}>
          <ColorDot color={props.color} />
          <Text weight="medium" scale={4}>
            {props.environment.env_name.toUpperCase()}
          </Text>
        </Inline>
      </FormHeader>
      <FormBody>
        <Text weight="medium">Environment variables</Text>
        <Text scale={6} tone="secondary">
          These values override the production settings specified in your bundle
        </Text>
        {settings.getAll().length === 0 && (
          <Text scale={6} tone="secondary">
            No environment variables set
          </Text>
        )}
        <RowGrid>
          {settings.getAll().length > 0 &&
            settings.getAll().map((setting, index) => {
              return (
                <Fragment key={index}>
                  <TextField
                    size={16}
                    inputProps={{
                      required: true,
                      placeholder: 'name',
                      value: setting.name,
                      readOnly: !write,
                      onChange: e => {
                        settings.setSettingName(e.target.value, index)
                        setDirty(true)
                      },
                    }}
                  />
                  <TextField
                    inputProps={{
                      required: true,
                      placeholder: 'value',
                      value: setting.value,
                      readOnly: !write,
                      onChange: e => {
                        settings.setSettingValue(e.target.value, index)
                        setDirty(true)
                      },
                    }}
                  />

                  {write && (
                    <DeleteSettingButton
                      onClick={() => {
                        settings.delete(index)
                        setDirty(true)
                      }}
                      type="button"
                    >
                      <X />
                    </DeleteSettingButton>
                  )}
                </Fragment>
              )
            })}
        </RowGrid>
        {write && (
          <AddSettingButton
            onClick={() => {
              settings.add()
              setDirty(true)
            }}
            type="button"
          >
            <PlusCircle /> <Text>Add settings</Text>
          </AddSettingButton>
        )}
        <Stack gap={0.5}>
          <Text weight="medium">Auto deployments</Text>
          <div>
            <CheckboxRowStyle
              type="button"
              onClick={() => {
                setAutoDeploy(!autoDeploy)
                setDirty(true)
              }}
              disabled={!write}
            >
              {autoDeploy === true && <CheckSquare />}
              {autoDeploy === false && <Square />}
              <Text>
                Generate deployments from this environment automatically
              </Text>
            </CheckboxRowStyle>
          </div>
        </Stack>
        {error && (
          <StatusMessage status="critical">
            <Text>{error.message}</Text>
          </StatusMessage>
        )}
      </FormBody>
      {write && (
        <FormFooter>
          <Inline gap={1}>
            {dirty && (
              <TextButton
                type="button"
                onClick={reset}
                disabled={saving || deleting}
              >
                Cancel
              </TextButton>
            )}
            <TextButton
              disabled={saving || deleting}
              loading={deleting}
              type="button"
              onClick={deleteEnvironment}
            >
              Delete
            </TextButton>
            <Button
              loading={saving}
              type="submit"
              disabled={saving || deleting || !settings.isValid || !dirty}
            >
              Save
            </Button>
          </Inline>
        </FormFooter>
      )}
    </Form>
  )
}

export default EnvironmentForm
