import React from 'react'
import Header from './components/Header'
import SiteLinks from './components/SiteLinks'
import NoSites from './components/NoSites'
import { Wrapper, Body, ClickOutside } from './style'
import { useQuery } from '@apollo/react-hooks'
import { GET_SIDEBAR_SITES_LIST } from 'graphql/queries'

export default function SideBar({ show, setShow, node }) {
  const { loading, error, data } = useQuery(GET_SIDEBAR_SITES_LIST, {
    fetchPolicy: 'cache-first',
  })

  if (loading) {
    return (
      <Wrapper show={show} ref={node}>
        <Header />
        <SiteLinks loading />
      </Wrapper>
    )
  }
  if (error) {
    return (
      <Wrapper show={show} ref={node}>
        <Header />
        <Body>{error.message}</Body>
      </Wrapper>
    )
  }

  if (data) {
    const { sites } = data.view
    return (
      <>
        <Wrapper show={show} ref={node}>
          <Header />
          {sites.length > 0 ? (
            <SiteLinks setShow={setShow} sites={sites} />
          ) : (
            <NoSites />
          )}
        </Wrapper>
        <ClickOutside show={show} />
      </>
    )
  }
}
