import React from 'react'
import styled, { css } from 'styled-components'
import { flex, media } from 'styles/traits'
import { Text } from 'components'
import stringToHex from 'helpers/stringToHex'
import { useToggle } from 'hooks'

const sharedStyles = css`
  min-height: 1.3rem;
  max-height: 1.3rem;
  min-width: 1.3rem;
  max-width: 1.3rem;
  border-radius: 50%;
  ${media.desktop} {
    height: 1.2rem;
    width: 1.2rem;
  }

  ${media.tablet} {
    height: 1rem;
    width: 1rem;
  }
`

const Container = styled.div`
  ${flex.center_both};
  background: ${({ color }) => color};
  span {
    color: ${({ theme }) => theme.text.reverse};
  }
  text-decoration: none;
  ${sharedStyles}
`

const StyledImage = styled.img`
  position: absolute;
  ${sharedStyles}
`

const Avatar = props => {
  const [imageError, toggleImageError] = useToggle(false)
  const { username, name } = props
  const color = stringToHex(name || 'unkown')
  const avatarSrc = `https://avatars2.githubusercontent.com/${username}?v=4&s=96`
  return (
    <Container color={color}>
      <Text weight="bold" tone="reverse">
        {name ? name.slice(0, 1) : '?'}
      </Text>
      {imageError === false && username !== null && (
        <StyledImage alt="" onError={toggleImageError} src={avatarSrc} />
      )}
    </Container>
  )
}

export default Avatar
