import stringToHex from 'helpers/stringToHex'

const colors = [
  '#B500B5',
  '#7023B7',
  '#3455DB',
  '#007A7C',
  '#008000',
  '#8D6708',
  '#BC3E31',
]

export default function getEnvColor(env_name, index) {
  if (index > colors.length - 1) {
    return stringToHex(env_name)
  } else {
    return colors[index]
  }
}
