import React from 'react'
import { Text } from 'components'
import relativeDate from 'relative-date'
import styled from 'styled-components'
import { flex, spacing, border } from 'styles/traits'
import SiteLogo from 'components/SiteLogo'
import Inline from 'components/Inline'
import { TextButton } from 'components/Buttons'
import Stack from 'components/Stack'

const Wrapper = styled.button`
  ${flex.justify_space_btw.align_center};
  ${spacing.p1};
  ${border.bn.br2};
  background: ${({ theme }) => theme.bg.default};
  &:hover {
    background: ${({ theme }) => theme.brand.wash};
  }
  &:nth-child(even) {
    background: ${({ theme }) => theme.bg.wash};

    &:hover {
      background: ${({ theme }) => theme.brand.wash};
    }
  }
  cursor: pointer;
`

const Header = styled.div`
  ${flex.align_center};
  > * + * {
    ${spacing.ml1};
  }
`

export default function SiteItem({
  select,
  sitename,
  repositoryName,
  createdAt,
  creator,
}) {
  return (
    <Wrapper onClick={select}>
      <Stack>
        <Header>
          <SiteLogo name={sitename} />
          <Stack style={{ alignItems: 'flex-start' }} gap={0.5}>
            <Text scale={5} weight="medium" tone="secondary">
              {sitename}
            </Text>
            <Text scale={6} tone="secondary">
              {repositoryName}
            </Text>
          </Stack>
        </Header>
        <Inline>
          <Text tone="secondary" scale={6}>
            Created {relativeDate(createdAt)} by {creator}
          </Text>
        </Inline>
      </Stack>
      <TextButton onClick={select}>Select</TextButton>
    </Wrapper>
  )
}
