import { useState } from 'react'

const formatInstallations = installations => {
  const formattedRepos = []
  for (const install of installations) {
    const { account, repositories } = install
    const newRepositories = repositories.map(item => ({
      ...item,
      avatar_url: account.avatar_url,
    }))
    formattedRepos.push(...newRepositories)
  }
  return formattedRepos
}

export const useFilterableRepos = installations => {
  const repoArray = formatInstallations(installations)
  const [repoFilter, setRepoFilter] = useState('')
  const [orgFilter, setOrgFilter] = useState('')
  const [sortBy, setSortBy] = useState('')

  const filtered = () =>
    repoArray
      .sort((a, b) => {
        if (sortBy === 'name') {
          return a.name.toLowerCase() > b.name.toLowerCase() ? 1 : -1
        }
        return new Date(a.pushed_at) > new Date(b.pushed_at) ? -1 : 1
      })
      .filter(
        ({ full_name }) =>
          full_name.toLowerCase().includes(repoFilter) &&
          full_name.toLowerCase().includes(orgFilter)
      )

  return {
    repoFilter: repoFilter,
    repositories: filtered(),
    setOrgFilter: option => setOrgFilter(option.value.toLowerCase()),
    setRepoFilter: event => setRepoFilter(event.target.value.toLowerCase()),
    setSortBy: option => setSortBy(option.value),
  }
}
