import { useState, useEffect } from 'react'
import { useDebouncedCallback } from 'use-debounce'
import { useApolloClient } from '@apollo/react-hooks'
import gql from 'graphql-tag'

import convertToLegalSiteName from 'helpers/convertToLegalSitename.js'

const CHECK_UNIQUENESS = gql`
  query checkUniqueness($name: String) {
    siteExists(name: $name)
  }
`

const FIELD_STATUSES = {
  LOADING: 'loading',
  POSITIVE: 'positive',
  CRITICAL: 'critical',
}

const getFieldStatus = (value, isSearching, unique) => {
  if (value.length > 0) {
    if (isSearching === true) {
      return FIELD_STATUSES.LOADING
    }
    if (unique === true) {
      return FIELD_STATUSES.POSITIVE
    }
    if (unique === false) {
      return FIELD_STATUSES.CRITICAL
    }
  }
}

export default function useSiteName(initialValue) {
  const client = useApolloClient()

  const [value, setValue] = useState(initialValue)
  const [unique, setUnique] = useState()
  const [loading, setLoading] = useState(false)

  const [debounceSetValue] = useDebouncedCallback(value => setValue(value), 500)

  useEffect(() => {
    if (value.length > 0) {
      setLoading(true)
      client
        .query({
          query: CHECK_UNIQUENESS,
          variables: { name: convertToLegalSiteName(value) },
        })
        .then(({ data }) => {
          setLoading(false)
          if (data.siteExists === true) {
            setUnique(false)
          }
          if (data.siteExists === false) {
            setUnique(true)
          }
        })
    }
  }, [value, client])

  const legalValue = convertToLegalSiteName(value)
  const status = getFieldStatus(value, loading, unique)
  const isValid = legalValue.length > 0 && unique === true && loading === false

  return {
    unique,
    status,
    legalValue,
    setValue: debounceSetValue,
    loading,
    value,
    isValid,
  }
}
