import React from 'react'
import styled from 'styled-components'
import { Route } from 'react-router-dom'
import { grid } from 'styles/traits'
import Stack from 'components/Stack'
import { Text, Card } from 'components'
import Providers from './components/Providers'
import GitHubConf from './components/GitHubConf'
import OtherConf from './components/OtherConf'
import Steps from './components/Steps'
import RepoSelect from 'views/RepoSelect'

const Container = styled.div`
  display: grid;
  margin-top: 4rem;
  grid-template-columns: auto 48rem auto;
  grid-template-areas: 'left-gutter content right-gutter';

  @media only screen and (max-width: 992px) {
    margin-top: 2rem;
    grid-template-columns: 2rem auto 2rem;
  }
  margin-bottom: 16rem;
`

const StyledCard = styled(Card)`
  grid-area: content;
  ${grid.auto_rows.g2};
`

export default function Creator() {
  return (
    <Container>
      <StyledCard padding={2}>
        <Stack>
          <Text scale={3} weight="bold">
            Create new site
          </Text>
          <Route path="/create/github" component={Steps} />
        </Stack>

        {/* Start route */}
        <Route exact path="/create">
          <Providers />
        </Route>

        {/* GitHub Provider routes */}
        <Route exact path="/create/github">
          <RepoSelect />
        </Route>
        <Route exact path="/create/github/:repositorySlug/:branchSlug">
          <GitHubConf />
        </Route>

        {/* Other Provider routes */}
        <Route exact path="/create/other">
          <OtherConf />
        </Route>
      </StyledCard>
    </Container>
  )
}
