import React, { useState } from 'react'
import Text from 'components/Text'
import { CopyToClipboard } from 'react-copy-to-clipboard'
import { Clipboard, Check } from 'react-feather'
import styled from 'styled-components'
import { flex, spacing, border } from 'styles/traits'

// eslint-disable-next-line
const SANATISE_BRANCHES_REGEX = /[^a-zA-Z0-9\-]/g

const BranchLinkWrapper = styled.div`
  background: ${({ theme, copied }) =>
    copied ? theme.space.border : theme.bg.wash};
  ${flex.fg1.justify_space_btw.align_center};
  ${spacing.p025};
  ${border.br1};
  transition: ${({ copied }) => (copied ? 'all 100ms ease' : 'all 600ms ease')};
`

const EnvName = styled.em`
  font-weight: bold;
  color: ${({ color }) => color};
`

const Anchor = styled.a`
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
`

const IconWrapper = styled.div`
  cursor: ${({ copied }) => (copied ? 'auto' : 'pointer')};
  svg {
    height: 1.2rem;
    margin-bottom: -0.1rem;
  }
  ${spacing.ml1};
  &:hover {
    svg {
      color: ${({ theme }) => theme.space.default};
    }
  }
  &:active {
    transform: scale(0.95, 0.95);
  }
`

export default function BranchLink({ sitename, environment, branch, color }) {
  const [copied, setCopied] = useState(false)

  const handleClick = () => {
    setCopied(true)

    setTimeout(() => {
      setCopied(false)
    }, 750)
  }

  const sanitisedBranch = branch
    .replace(SANATISE_BRANCHES_REGEX, '')
    .toLowerCase()

  const url = `https://${sitename}--${environment}--${sanitisedBranch}.branch.linc-preview.sh/`

  return (
    <BranchLinkWrapper copied={copied}>
      <Anchor
        key={environment + branch}
        rel="noopener noreferrer"
        target="_blank"
        href={url}
      >
        <Text scale={6}>
          https://{sitename}--
          <EnvName color={color}>{environment}</EnvName>--
          {branch}.branch.linc-preview.sh/
        </Text>
      </Anchor>
      <CopyToClipboard text={url} onCopy={handleClick}>
        {copied ? (
          <IconWrapper copied={copied}>
            <Check />
          </IconWrapper>
        ) : (
          <IconWrapper copied={copied} title="copy to clipboard">
            <Clipboard />
          </IconWrapper>
        )}
      </CopyToClipboard>
    </BranchLinkWrapper>
  )
}
