export const RELEASED = 'RELEASED'
export const SUCCESSFUL_BUILD = 'SUCCESSFUL_BUILD'
export const ATTEMPTED_BUILD = 'ATTEMPTED_BUILD'
export const NO_BUILDS = 'NO_BUILDS'
export const NO_COMMITS = 'NO_COMMITS'

export const commitsFeedOnboardingLevel = commits => {
  if (commits.length === 0) return NO_COMMITS

  let build_attempted = false
  for (const commit of commits) {
    const status = commit.build?.status
    if (status === 'success') return SUCCESSFUL_BUILD
    if (status !== 'ignored') build_attempted = true
  }
  return build_attempted ? ATTEMPTED_BUILD : NO_BUILDS
}
