import styled, { css } from 'styled-components'

const Anchor = styled.a`
  text-decoration: none;
  color: inherit;
  &:hover {
    ${({ styleHover }) =>
      styleHover &&
      css`
        color: ${({ theme }) => theme.brand.default};
        text-decoration: underline;
      `};
  }
`

export default Anchor
