import React from 'react'
import styled, { css } from 'styled-components'
import { media, flex, spacing, border } from 'styles/traits'
import Text from 'components/Text'
import { GitCommit, Calendar, MessageSquare } from 'react-feather'
import Avatar from 'components/Avatar'
import { TextPlaceholder } from 'components/Placeholders'
import { withShadow } from '../style'
import DeploymentStatusIcon from './DeploymentStatusIcon'
import relativeDate from 'relative-date'
import { GradientOverlay, hexa } from 'styles/globals'

const Anchor = styled.a`
  ${flex.align_center};
  > * + * {
    ${spacing.ml05};
  }
  text-decoration: none;

  &:hover {
    span {
      color: ${({ theme }) => theme.space.default};
      text-decoration: underline;
    }
    svg {
      color: ${({ theme }) => theme.space.default};
    }
  }
`

const baseStyle = css`
  display: grid;
  z-index: 1;
  border-radius: 0 0.25rem 0.25rem 0;
  border-color: ${({ theme }) => theme.bg.border};
  background: ${({ theme }) => theme.bg.default};
  background: ${({ theme }) => theme.bg.default};
  background: var(--row-bg);
  --row-bg: ${({ theme }) => theme.bg.default};
  overflow: hidden;
  ${withShadow};
  box-shadow: 0 2px 3px 0 ${({ theme }) => hexa(theme.bg.reverse, 0.2)};
  ${border.bt.br.bb};
  svg {
    color: ${({ theme }) => theme.text.secondary};
    max-width: 1rem;
    min-width: 1rem;
  }
`

const defaultLayout = css`
  height: 3.5rem;
  grid-template-columns: 1fr 1fr 2fr 1fr minmax(15rem, 24%);
  grid-template-areas: 'commit-sha author message timestamp deployment-status';
`

const giant = css`
  height: auto;
  grid-template-columns: 1fr 2fr minmax(15rem, 24%);
  grid-template-areas:
    'commit-sha timestamp deployment-status'
    'author message deployment-status';
`

const tablet = css`
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 3rem auto 3rem;
  grid-template-areas:
    'author timestamp commit-sha'
    'message message message'
    'deployment-status deployment-status deployment-status';
`

const Wrapper = styled.div`
  ${baseStyle};
  ${defaultLayout};
  ${media.giant} {
    ${giant};
  }
  ${media.tablet} {
    ${tablet};
  }
`

const Cell = styled.div`
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  padding: 0.5rem;
  ${flex.align_center};
  position: relative;
  > * + * {
    ${spacing.ml05};
  }

  ${media.desktop} {
    svg {
      max-height: 0.8rem;
      min-height: 0.8rem;
    }
  }

  @media only screen and (max-width: 768px) {
    white-space: normal;
  }

  @media only screen and (min-width: 768px) {
    ${GradientOverlay};
  }

  ${({ gridArea }) => gridArea && `grid-area: ${gridArea};`};
`

const DeploymentCell = styled(Cell)`
  @media only screen and (max-width: 768px) {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
`

export default function Content({ loading, release_status }) {
  if (loading) {
    return (
      <Wrapper>
        <Cell gridArea="commit-sha">
          <TextPlaceholder />
        </Cell>
        <Cell gridArea="author">
          <TextPlaceholder />
        </Cell>
        <Cell gridArea="message">
          <TextPlaceholder />
        </Cell>
        <Cell gridArea="timestamp">
          <TextPlaceholder />
        </Cell>
        <Cell gridArea="deployment-status">
          <TextPlaceholder />
        </Cell>
      </Wrapper>
    )
  } else {
    const { commit } = release_status
    const { message, commit_sha, author, timestamp, repo_url } = commit
    const url = `${repo_url}/commit/${commit_sha}`
    return (
      <Wrapper>
        <Cell gridArea="commit-sha">
          {commit_sha && (
            <Anchor target="_blank" rel="noopener noreferrer" href={url}>
              <GitCommit />
              <Text scale={6}>{commit_sha.slice(0, 9)}</Text>
            </Anchor>
          )}
        </Cell>
        <Cell gridArea="author">
          {author && (
            <>
              <Avatar name={author.name} username={author.username} />
              <Text scale={6}>{author.name || author.username}</Text>
            </>
          )}
        </Cell>
        <Cell gridArea="message">
          {message && (
            <>
              <MessageSquare />
              <Text weight="medium" scale={6}>
                {message}
              </Text>
            </>
          )}
        </Cell>
        <Cell gridArea="timestamp">
          {timestamp && (
            <>
              <Calendar />
              <Text scale={6}>{relativeDate(timestamp)}</Text>
            </>
          )}
        </Cell>
        <DeploymentCell gridArea="deployment-status">
          <Text scale={6}>Deployment Status:</Text>
          <DeploymentStatusIcon release_status={release_status} />
        </DeploymentCell>
      </Wrapper>
    )
  }
}
