import React, { Fragment, Component } from 'react'
import { useQuery } from '@apollo/react-hooks'
import { Spinner, Text, TextField, StatusMessage } from 'components'
import CommitsTable from 'views/CommitsTable'
import { GET_COMMITS } from 'graphql/queries'
import { NetworkStatus } from 'apollo-client'
import { FetchMoreButton } from './style'
import Messages from './messages'
import Inline from 'components/Inline'
import Stack from 'components/Stack'
import { insertNewCommits } from 'helpers/updateQueryMethods'
import { useParams } from 'react-router'

class Commits extends Component {
  state = {
    filter: '',
  }

  setFilter = val => {
    this.setState({
      filter: val,
    })
  }

  get filtered() {
    const { filter } = this.state
    const { commits } = this.props
    if (filter.length > 0) {
      const resultSet = commits.filter(commit => {
        const { message, author } = commit.head_commit
        const matchSet = [message, author.name, author.username]
        return matchSet.some(item =>
          item.toLowerCase().includes(filter.toLowerCase())
        )
      })
      return resultSet
    } else {
      return commits
    }
  }

  render() {
    const { filter } = this.state
    const {
      loading,
      onLoadMore,
      branch,
      sitename,
      environments,
      hasNextPage,
    } = this.props
    return (
      <Stack gap={1}>
        <TextField
          inputProps={{
            placeholder: 'Filter commits',
            onChange: event => this.setFilter(event.target.value),
          }}
        />
        {this.filtered.length === 0 && (
          <Text>{Messages.noMatchesFound({ filter })}</Text>
        )}
        {this.filtered.length > 0 && (
          <Fragment>
            <CommitsTable
              commits={this.filtered}
              branch={branch}
              environments={environments}
              sitename={sitename}
              limit={1000}
            />
            {hasNextPage === true && (
              <FetchMoreButton onClick={onLoadMore} disabled={loading}>
                {loading === true && (
                  <Inline gap={1}>
                    <Text>Load more</Text>
                    <Spinner />
                  </Inline>
                )}
                {loading === false && <Text>Load More</Text>}
              </FetchMoreButton>
            )}
            {hasNextPage === false && (
              <Text tone="secondary">
                {Messages.noOlderCommitsFound({ branch, sitename })}
              </Text>
            )}
          </Fragment>
        )}
      </Stack>
    )
  }
}

export default function CommitsWithData() {
  const { sitenameSlug, branchSlug } = useParams()
  const branchName = decodeURIComponent(branchSlug)
  const variables = {
    sitename: sitenameSlug,
    limit: 20,
    branch: branchName,
  }

  const { data, error, fetchMore, networkStatus } = useQuery(GET_COMMITS, {
    variables,
    notifyOnNetworkStatusChange: true,
  })

  if (error) {
    return (
      <Stack gap={3}>
        <Text weight="bold" scale={3}>
          Commits by branch
        </Text>
        <StatusMessage>{error.message}</StatusMessage>
      </Stack>
    )
  }

  if (networkStatus === NetworkStatus.loading) {
    return (
      <Stack gap={3}>
        <Text weight="bold" scale={3}>
          Commits by branch
        </Text>
        <Spinner />
      </Stack>
    )
  }

  const {
    environments,
    commitsFeed: { commits, cursor, hasNextPage },
  } = data.view.site

  if (commits.length === 0) {
    return (
      <Stack gap={3}>
        <Text weight="bold" scale={3}>
          Commits by branch
        </Text>
        <StatusMessage status="warning">
          <Text>
            {Messages.noCommitsFound({
              branchName,
              sitename: sitenameSlug,
            })}
          </Text>
        </StatusMessage>
      </Stack>
    )
  }

  return (
    <Stack gap={3}>
      <Text weight="bold" scale={3}>
        Commits by branch
      </Text>
      <Commits
        branch={branchName}
        loading={networkStatus === NetworkStatus.fetchMore}
        commits={commits || []}
        environments={environments}
        sitename={sitenameSlug}
        onLoadMore={() => {
          fetchMore({
            query: GET_COMMITS,
            variables: {
              ...variables,
              cursor,
            },
            updateQuery: (previousResult, { fetchMoreResult }) =>
              insertNewCommits(previousResult, fetchMoreResult),
          })
        }}
        hasNextPage={hasNextPage}
      />
    </Stack>
  )
}
