import React from 'react'
import { Container, Content, Placeholder } from './style'
import Header from './components/Header'
import Block from './components/Block'
import { useToggle } from 'hooks'

const BuildLog = props => {
  const [logVisible, toggle] = useToggle(true)
  const { commit_sha, message, author, build } = props
  const { status, logs } = build
  return (
    <Container id="build_log">
      <Header
        status={status}
        commit_sha={commit_sha}
        message={message}
        author={author}
        toggle={toggle}
        logVisible={logVisible}
        toggleLogVisibility={toggle}
      />
      <Content expanded={logVisible}>
        {!logs && <Placeholder>No build log</Placeholder>}
        {logs &&
          logs.map((block, index) => <Block key={index} block={block} />)}
      </Content>
    </Container>
  )
}

export default BuildLog
