import React from 'react'
import { useDocumentTitle } from 'hooks'
import ActiveReleases from './components/ActiveReleases'
import CommitsFeed from './components/CommitsFeed'
import { useParams } from 'react-router'

export default function Overview({ fetchPolicy }) {
  const { sitenameSlug } = useParams()
  useDocumentTitle(`Overview | ${sitenameSlug}`)
  return (
    <>
      <ActiveReleases fetchPolicy={fetchPolicy} />
      <CommitsFeed fetchPolicy={fetchPolicy} />
    </>
  )
}
