import React from 'react'
import { Text } from 'components'
import { Form, FormHeader, FormBody } from 'components/FormElements'
import SettingsForm from './SettingsForm'

export default function ExternalCI(props) {
  const { sitename, siteAccess, settings } = props
  if (siteAccess.write) {
    return (
      <SettingsForm
        sitename={sitename}
        settings={settings}
        siteAccess={siteAccess}
      />
    )
  } else {
    return (
      <Form>
        <FormHeader>
          <Text scale={4} weight="medium">
            External CI settings
          </Text>
        </FormHeader>
        <FormBody>
          <Text.P>
            You must have repository write access to view or edit{' '}
            <code>{sitename}</code> External CI settings
          </Text.P>
        </FormBody>
      </Form>
    )
  }
}
