import React, { Fragment } from 'react'
import styled, { css } from 'styled-components'
import { flex, typography, spacing } from 'styles/traits'

const Line = styled.li`
  ${typography.f6};
  ${flex.align_center};
  ${spacing.ph1};
  height: 1.2rem;
  font-family: monospace;
  color: hsl(0, 0%, 90%);
  &:hover {
    background: hsl(216, 8%, 16%);
  }

  ${props =>
    props.type === 'cmd' &&
    css`
      color: ${({ theme }) => theme.special.default};
    `};
  ${props =>
    props.type === 'log' &&
    css`
      color: ${({ theme }) => theme.text.reverse};
    `};
  ${props =>
    props.type === 'time' &&
    css`
      color: ${({ theme }) => theme.success.default};
    `};
`

const LogBlock = ({ block }) => {
  const { cmd, log, time } = block
  const timeStamp = time && `${Math.round((time / 1000) * 100) / 100} sec`
  return (
    <Fragment>
      {cmd && <Line type="cmd">{cmd}</Line>}
      {log &&
        log.split('\n').map((test, index) => (
          <Line type="log" key={`${test}-${index}`}>
            {test}
          </Line>
        ))}
      {time && <Line type="time">{timeStamp}</Line>}
    </Fragment>
  )
}

export default LogBlock
