import styled from 'styled-components'
import PropTypes from 'prop-types'
import Box from 'components/Box'

import { Shadow, hexa } from 'styles/globals'

const Card = styled(Box)`
  background: ${({ theme }) => theme.bg.default};
  border: solid 1px ${({ theme }) => theme.bg.border};
  border-radius: ${({ borderRadius }) => borderRadius}rem;

  box-shadow: ${({ boxShadow, theme }) =>
    boxShadow === 'none'
      ? 'none'
      : `${Shadow[boxShadow]} ${hexa(theme.bg.reverse, 0.2)}`};
`

Card.defaultProps = {
  padding: 1,
  boxShadow: 'low',
  borderRadius: 0.25,
}

Card.propTypes = {
  boxShadow: PropTypes.oneOf(['none', 'low', 'mid', 'high']),
  borderRadius: PropTypes.oneOf([0, 0.125, 0.25, 0.5, 1, 2]),
}

export default Card
