import React from 'react'
import Stack from 'components/Stack'
import { Text } from 'components'
import styled from 'styled-components'
import { Link } from 'react-router-dom'
import Inline from 'components/Inline'

const StepStripe = styled.div`
  color: ${({ active, theme }) =>
    active ? theme.text.default : theme.text.placeholder};
  background: ${({ theme, complete, active }) =>
    complete || active ? theme.brand.default : theme.brand.wash};
  height: 0.5rem;
  width: 12rem;
`

const StyledLink = styled(Link)`
  text-decoration: none;
`

const Step = ({ active, complete, text }) => (
  <Stack gap={0.5}>
    <StepStripe complete={complete} active={active} />
    <Text
      scale={6}
      tone={active ? 'default' : 'placeholder'}
      weight={active ? 'medium' : 'regular'}
    >
      {text}
    </Text>
  </Stack>
)

export default function Steps({ location }) {
  const { pathname } = location
  const stepTwoActive = pathname === '/create/github'
  const stepThreeActive = pathname.includes('/create/github/')
  return (
    <Inline gap={0.5}>
      <StyledLink to="/create">
        <Step complete={true} active={false} text="1: Select Git Provider" />
      </StyledLink>
      {stepTwoActive ? (
        <Step
          complete={false}
          active={true}
          text="2: Select Repo"
        />
      ) : (
        <StyledLink to="/create/github">
          <Step
            complete={true}
            active={false}
            text="2: Select Repo"
          />
        </StyledLink>
      )}
      <Step
        complete={stepTwoActive ? false : true}
        active={stepThreeActive}
        text="3: Configure"
      />
    </Inline>
  )
}
