import React from 'react'
import immutable from 'object-path-immutable'
import { useMutation } from '@apollo/react-hooks'
import { UPDATE_SITE_COLLABORATOR, GET_DATA } from '../../graphql'
import { Wrapper, OptionStyle } from './style'

const updateCache = (cache, sitename, user_id, access_level) => {
  const cachedData = cache.readQuery({
    query: GET_DATA,
    variables: { name: sitename },
  })

  // pull out collaborator from cached data
  const { settings } = cachedData.view.site
  const { collaborators } = settings.access_control
  const targetIndex = collaborators.findIndex(item => item.user_id === user_id)
  const newColab = { ...collaborators[targetIndex] }

  // update collaborator access_level
  newColab.access_level = access_level

  // update cached data with modified collaborator
  const path = `view.site.settings.access_control.collaborators.${targetIndex}`
  const newData = immutable.set(cachedData, path, newColab)

  // write updated cache data back to cache
  cache.writeQuery({
    query: GET_DATA,
    variables: { name: sitename },
    data: { ...newData },
  })
}

const Option = ({
  sitename,
  user_id,
  current_access,
  new_access,
  isDisabled,
}) => {
  const [updateSiteCollaborator, { loading }] = useMutation(
    UPDATE_SITE_COLLABORATOR,
    {
      variables: {
        sitename,
        user_id,
        access_level: new_access,
      },
      update: cache => updateCache(cache, sitename, user_id, new_access),
    }
  )
  const optionDisabled = isDisabled || loading || current_access === new_access

  return (
    <OptionStyle
      loading={loading}
      isDisabled={isDisabled}
      disabled={optionDisabled}
      onClick={updateSiteCollaborator}
      active={current_access === new_access}
    >
      {new_access}
    </OptionStyle>
  )
}

const AccessToggles = ({ sitename, user_id, access_level, isDisabled }) => {
  const accessOptions = ['read', 'write', 'admin']
  return (
    <Wrapper>
      {accessOptions.map(option => (
        <Option
          current_access={access_level}
          isDisabled={isDisabled}
          new_access={option}
          sitename={sitename}
          user_id={user_id}
          key={option}
        />
      ))}
    </Wrapper>
  )
}

export default AccessToggles
