import React from 'react'
import styled from 'styled-components'
import { Text } from 'components'
import { flex, border } from 'styles/traits'
import stringToHex from 'helpers/stringToHex'

const Container = styled.div`
  ${flex.center_both};
  ${border.br2};
  min-height: 2rem;
  min-width: 2rem;
  background: ${({ color }) => color};
  span {
    color: ${({ theme }) => theme.text.reverse};
  }
`

const formatToInitials = name => {
  let initials
  // Splits sitename such as "linc-front-end" ...
  // on dashes into the array: ["linc", "front", "end"]
  const parts = name.split('-').filter(str => str !== '')
  // Return first letter of first and second string element
  // in parts array
  if (parts.length > 1) {
    const first = parts[0].slice(0, 1)
    const last = parts[1].slice(0, 1)
    initials = first + last
  }
  // If parts contains less than 2 items, just ...
  // return first letter of first element
  else {
    initials = name.slice(0, 1) + name.slice(-1)
  }
  return initials.toUpperCase()
}

const SiteLogo = ({ name }) => {
  const color = name === 'linc-front-end' ? "#6C63FF" : stringToHex(name)
  const initials = formatToInitials(name)
  return (
    <Container color={color}>
      <Text weight="bold" tone="reverse">
        {initials}
      </Text>
    </Container>
  )
}

export default SiteLogo
