import React, { Fragment, useState, useEffect, useRef } from 'react'
import Text from 'components/Text'
import Modal from 'components/Modal'
import { ModalContainer } from 'components/Modal/style'
import getEnvColors from 'helpers/envColors'
import { X, ExternalLink } from 'react-feather'
import BranchLink from './BranchLink'
import Stack from 'components/Stack'
import {
  OpenButton,
  ModalBox,
  ModalHeader,
  ModalBody,
  CloseButton,
} from './style'

const BranchLinks = ({ environments, sitename, branch }) =>
  environments.map((item, index) => {
    const color = getEnvColors(item.env_name, index)
    return (
      <BranchLink
        key={index}
        sitename={sitename}
        environment={item.env_name}
        branch={branch}
        color={color}
      />
    )
  })

function Dialogue({ branch, environments, sitename, closeModal }) {
  const node = useRef()

  const handleClick = e => {
    if (node && node.current && node.current.contains(e.target)) {
      return
    }
    closeModal()
  }

  const handleKeyDown = e => {
    const ESCAPE_KEYCODE = 27
    if (e.keyCode === ESCAPE_KEYCODE) {
      closeModal()
    }
  }

  useEffect(() => {
    document.addEventListener('mousedown', handleClick)
    document.addEventListener('keydown', handleKeyDown)
    return () => {
      document.removeEventListener('mousedown', handleClick)
      document.removeEventListener('keydown', handleKeyDown)
    }
  })

  return (
    <ModalBox ref={node}>
      <ModalHeader>
        <Text>
          Latest on <strong>{branch}</strong> branch
        </Text>
        <CloseButton onClick={closeModal}>
          <X />
        </CloseButton>
      </ModalHeader>
      <ModalBody>
        <Stack gap={0.5}>
          <BranchLinks
            environments={environments}
            sitename={sitename}
            branch={branch}
          />
        </Stack>
      </ModalBody>
    </ModalBox>
  )
}

const BranchLinksModal = ({ sitename, environments, branch }) => {
  const [showModal, setShowModal] = useState(false)
  const openModal = () => setShowModal(true)
  const closeModal = () => setShowModal(false)
  return (
    <Fragment>
      <OpenButton onClick={openModal}>
        <Text scale={6}>Latest</Text> <ExternalLink />
      </OpenButton>
      {showModal && (
        <Modal>
          <ModalContainer>
            <Dialogue
              branch={branch}
              sitename={sitename}
              closeModal={closeModal}
              environments={environments}
            />
          </ModalContainer>
        </Modal>
      )}
    </Fragment>
  )
}

export default BranchLinksModal
