import { split } from 'apollo-link'
import { HttpLink } from 'apollo-link-http'
import { WebSocketLink } from 'apollo-link-ws'
import { getMainDefinition } from 'apollo-utilities'
import { ApolloClient } from 'apollo-client'
import { InMemoryCache } from 'apollo-cache-inmemory'
import { onError } from 'apollo-link-error'
import config from './config'
import { setLoggedIn } from 'stores/user'

const httpLink = new HttpLink({
  // define graphql endpoint
  uri: config.GRAPHQL_ENDPOINT,
  credentials: config.NO_CREDENTIALS_NEEDED ? false : 'include',
})

// Create a WebSocket link:
const wsLink = new WebSocketLink({
  uri: config.SUBSCRIPTION_ENDPOINT,
  // uri: `ws://localhost:4000/graphql`,
  options: {
    // connectionParams: {
    //   authToken: GOOD_TOKEN,
    // },
    reconnect: true,
  },
})

// using the ability to split links, you can send data to each link
// depending on what kind of operation is being sent
const link = split(
  // split based on operation type
  ({ query }) => {
    const { kind, operation } = getMainDefinition(query)
    return kind === 'OperationDefinition' && operation === 'subscription'
  },
  wsLink,
  httpLink
)

const errorHandler = onError(({ networkError }) => {
  if (networkError && networkError.statusCode === 401) {
    setLoggedIn(false)
  }
})

const client = new ApolloClient({
  link: errorHandler.concat(link),
  cache: new InMemoryCache(),
})

export default client
