import React, { Fragment } from 'react'
import { BrowserRouter } from 'react-router-dom'
import { ApolloProvider } from '@apollo/react-hooks'
import styled, { ThemeProvider } from 'styled-components'
import { observer } from 'mobx-react'

import client from './ApolloClient'

import { theme } from 'styles/theme'
import Globals from 'styles/globals'
import ScrollToTop from 'components/ScrollToTop'

import AppContainer from 'components/Layouts/AppLayouts'
import useSideBarOverlay from 'hooks/useSideBarOverlay'

import TopBar from 'views/TopBar'
import SideBar from 'views/SideBar'
import Routes from './Routes'

import { getLoggedIn } from 'stores/user'
import Login from 'views/Login'

const Content = styled.div`
  grid-area: content;
  ${({ show }) => show && `height: calc(100vh - 4rem)`};
`

function Main() {
  const { show, setShow, toggleSideBar, node } = useSideBarOverlay()
  return (
    <AppContainer>
      <TopBar toggleSideBar={toggleSideBar} />
      <SideBar node={node} show={show} setShow={setShow} />
      <Content show={show}>
        <Routes />
      </Content>
    </AppContainer>
  )
}

const App = observer(() => {
  const loggedIn = getLoggedIn()
  return (
    <BrowserRouter>
      <ScrollToTop>
        <ApolloProvider client={client}>
          <ThemeProvider theme={theme}>
            <Fragment>
              <Globals />
              {loggedIn ? <Main /> : <Login />}
            </Fragment>
          </ThemeProvider>
        </ApolloProvider>
      </ScrollToTop>
    </BrowserRouter>
  )
})

export default App
