const lookupEnvVar = name => {
  if (typeof window.FAB_SETTINGS === 'object') {
    return window.FAB_SETTINGS[name]
  }
}

const LOCAL_GRAPHQL = window.location.host.match(/127\.0\.0\.1/)

export default {
  GRAPHQL_ENDPOINT: LOCAL_GRAPHQL
    ? 'http://localhost:3001/graphql'
    : lookupEnvVar('GRAPHQL_ENDPOINT') || 'https://graphql.linc.sh/graphql',
  SUBSCRIPTION_ENDPOINT:
    lookupEnvVar('SUBSCRIPTION_ENDPOINT') || 'wss://subserver.linc.sh/graphql',
  NO_CREDENTIALS_NEEDED: lookupEnvVar('NO_CREDENTIALS_NEEDED') || false,
  DEMO_MODE: lookupEnvVar('DEMO_MODE'),
  ADMIN_MODE: lookupEnvVar('ADMIN_MODE'),
}
