import gql from 'graphql-tag'

/* RELEASE CONFIG MUTATIONS */

export const UPDATE_LAMBDA_CONFIG = gql`
  mutation($sitename: String!, $input: LambdaInput!) {
    updateLambdaConfig(sitename: $sitename, input: $input)
  }
`

export const UPDATE_CLOUDFLARE_CONFIG = gql`
  mutation($sitename: String!, $input: CloudflareInput!) {
    updateCloudflareConfig(sitename: $sitename, input: $input)
  }
`

export const DELETE_DEPLOY_CONFIG = gql`
  mutation($sitename: String!, $deploy_target_name: String!) {
    deleteDeployConfig(
      sitename: $sitename
      deploy_target_name: $deploy_target_name
    )
  }
`

/* SITE MUTATIONS */

export const CREATE_NEW_SITE = gql`
  mutation createNewSite($input: SiteInput!) {
    createNewSite(input: $input)
  }
`

/* USER MUTATIONS */

export const ADD_SITE_TO_USER = gql`
  mutation addSiteToUser($name: String!) {
    addSiteToUser(name: $name) {
      newSites
    }
  }
`

export const REMOVE_SITE_FROM_USER = gql`
  mutation removeSiteFromUser($name: String!) {
    removeSiteFromUser(name: $name) {
      newSites
    }
  }
`

/* ENVIRONMENT MUTATIONS */

export const CREATE_NEW_ENVIRONMENT = gql`
  mutation createNewEnvironment($input: EnvironmentInput) {
    createNewEnvironment(input: $input)
  }
`
export const UPDATE_ENVIRONMENT = gql`
  mutation updateEnvironment($input: EnvironmentInput) {
    updateEnvironment(input: $input)
  }
`
export const DELETE_ENVIRONMENT = gql`
  mutation deleteEnvironment($sitename: String!, $env_name: String!) {
    deleteEnvironment(sitename: $sitename, env_name: $env_name)
  }
`

