import { css, createGlobalStyle } from 'styled-components'
import reset from 'styled-reset'
import { media } from './traits'
import { theme } from './theme'

export const RadialGradient = (g1, g2) =>
  css`radial-gradient(ellipse farthest-corner at top left, ${g1} 0%, ${g2} 100%)`

export const Truncate = width => css`
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  min-width: 0;
`

export const Transition = {
  hover: {
    on: 'all 200ms ease-in',
    off: 'all 200ms ease-out',
  },
}

export const Shadow = {
  low: '0 2px 3px',
  mid: '0 4px 6px',
  high: '0 8px 12px',
}

export const GradientOverlay = css`
  &:before {
    pointer-events: none;
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background: linear-gradient(to left, white 0%, rgba(255, 255, 255, 0) 10%);
    /* background: linear-gradient(to left, var(--row-bg) 0%, rgba(255,255,255,0.0) 10%); */
  }
  mix-blend-mode: multiply;
`

export const hexa = (hex, alpha) => {
  const r = parseInt(hex.slice(1, 3), 16)
  const g = parseInt(hex.slice(3, 5), 16)
  const b = parseInt(hex.slice(5, 7), 16)
  if (alpha >= 0) {
    return `rgba(${r}, ${g}, ${b}, ${alpha})`
  } else {
    return `rgb(${r}, ${g}, ${b})`
  }
}

export const tooltipStyle = {
  style: {
    background: theme.bg.reverse,
    padding: '0.75rem 1rem',
    boxShadow: `${Shadow.low} ${hexa(theme.bg.reverse, 0.2)}`,
    color: theme.text.reverse,
    transition: 'none',
    zIndex: 1000,
    maxWidth: 360,
    lineHeight: 1.4,
    fontSize: 15,
    whiteSpace: 'pre-line',
  },
  arrowStyle: {
    color: theme.bg.reverse,
    borderColor: false,
    transition: 'none',
  },
}

const Globals = createGlobalStyle`
  ${reset};
  html {
    color: ${({ theme }) => theme.text.default};
    background-color: hsl(220,35%,99%);    
    ${media.desktop} {
      font-size: 16px;
    }
    ${media.tablet} {
      font-size: 14px;
    }
    ${media.phone} {
      font-size: 12px;
    }
  }
  button {
    font: inherit;
  }
`

export default Globals
