import React from 'react'
import PropTypes from 'prop-types'
import styled, { css } from 'styled-components'
import { spacing, typography } from 'styles/traits'
import { Shadow, hexa } from 'styles/globals'
import Text from 'components/Text'
import Box from 'components/Box'

const baseStyle = css`
  border-style: solid;
  border-radius: 3px;
  overflow-x: auto;
  box-shadow: ${Shadow.low} ${({ theme }) => hexa(theme.bg.reverse, 0.2)};
  ${props =>
    props.status &&
    css`
      span,
      i,
      p,
      h2,
      h3,
      h4,
      a {
        color: inherit;
      }
    `};

  a {
    ${typography.medium};
  }
  
  background-color: ${({ theme }) => theme.bg.default};
  border-left-color: ${({ theme }) => theme.brand.alt};
  border-left-width: ${({ leftBorder }) => leftBorder}rem;
`

const StatusMessage = styled(Box)`
  ${baseStyle};

  ${props =>
    props.status === 'positive' &&
    css`
      background: ${({ theme }) => theme.success.wash};
      border-left-color: ${({ theme }) => theme.success.border};
      color: ${({ theme }) => theme.success.dark};
    `};

  ${props =>
    props.status === 'critical' &&
    css`
      background: ${({ theme }) => theme.warn.wash};
      border-left-color: ${({ theme }) => theme.warn.border};
      color: ${({ theme }) => theme.warn.dark};
    `};

  ${props =>
    props.status === 'info' &&
    css`
      background: ${({ theme }) => theme.space.wash};
      border-left-color: ${({ theme }) => theme.space.border};
      color: ${({ theme }) => theme.space.dark};
    `};

  ${props =>
    props.status === 'warning' &&
    css`
      background: ${({ theme }) => theme.special.wash};
      border-color: ${({ theme }) => theme.special.border};
      color: ${({ theme }) => theme.special.dark};
    `};
`

StatusMessage.defaultProps = {
  padding: 1,
  leftBorder: 0.5,
}

StatusMessage.propTypes = {
  status: PropTypes.oneOf(['positive', 'critical', 'warning', 'info']),
  leftBorder: PropTypes.oneOf([0, 0.125, 0.25, 0.5, 1]),
  children: PropTypes.oneOfType([PropTypes.object, PropTypes.array]).isRequired,
}

StatusMessage.ErrorMessageFromServer = styled(Text).attrs({
  children: props => <code>{props.message}</code>,
})`
  ${spacing.mh2};
  code {
    font-weight: normal;
    line-height: 1.2;
  }
`

export default StatusMessage
