import gql from 'graphql-tag'

export const HeadCommit = gql`
  fragment HeadCommit on Commit {
    head_commit {
      author {
        name
        username
      }
      message
      timestamp
      url
      id
    }
  }
`

export const Build = gql`
  fragment Build on Commit {
    build {
      sitename
      bundle_id
      finished_at
      started_at
      status
      tree_id
      logs {
        cmd
        log
        time
      }
    }
  }
`

export const Tests = gql`
  fragment Tests on Commit {
    tests {
      sitename
      started_at
      tree_id
      test_results {
        command
        description
        started_at
        status
        type
        logs {
          err
          cmd
          log
          time
        }
      }
    }
  }
`

export const ReleaseStatus = gql`
  fragment ReleaseStatus on Site {
    releaseStatusV2 {
      modified_at
      release_branch
      current_release {
        bundle_id
        deployed_at
      }
      commit {
        repo_url
        timestamp
        author {
          email
          name
          username
        }
        commit_sha
        branch
        message
      }
      deployment_statuses {
        deploy_target_name
        message
        status
        timestamp
      }
    }
  }
`

export const Environments = gql`
  fragment Environments on Site {
    environments {
      auto_deploy
      sitename
      env_name
      created_at
      settings {
        name
        value
      }
    }
  }
`
