import React, { useContext } from 'react'
import PropTypes from 'prop-types'
import FlipMove from 'react-flip-move'
import Link from './Link'
import { SideNavContext } from 'helpers/contexts'
import styled, { css } from 'styled-components'
import {
  SIDE_BAR_HEADER_HEIGHT,
  SIDE_BAR_TABLET_HEADER_HEIGHT,
  TOP_BAR_HEIGHT,
} from '../style'

const baseStyle = css`
  overflow-y: auto;
  height: ${`calc(100vh - ${TOP_BAR_HEIGHT} - ${SIDE_BAR_HEADER_HEIGHT})`};
  @media only screen and (min-width: 993px) and (max-width: 1170px) {
    height: ${`calc(100vh - ${TOP_BAR_HEIGHT} - ${SIDE_BAR_TABLET_HEADER_HEIGHT})`};
  }
  padding-top: 1px;
`

const LoadingContainer = styled.div`
  ${baseStyle};
`

const StyledFlipMove = styled(FlipMove)`
  ${baseStyle};
`

const sortByLastCommit = (a, b) => {
  const getLastCommit = item => item?.commitsFeed?.commits[0]?.timestamp
  const timeStampA = getLastCommit(a) || 0
  const timeStampB = getLastCommit(b) || 0
  return timeStampB - timeStampA
}

const mapToSiteLinks = (site, isExpanded, setShow) =>
  site ? (
    <Link
      key={site.name}
      setShow={setShow}
      sitename={site.name}
      isExpanded={isExpanded}
      to={`/sites/${site.name}`}
      repositoryName={site.repository_name}
    />
  ) : null

const SiteLinks = ({ loading, setShow, sites }) => {
  const { isExpanded } = useContext(SideNavContext)
  if (loading) {
    return (
      <LoadingContainer>
        <Link loading />
        <Link loading />
        <Link loading />
      </LoadingContainer>
    )
  }
  return (
    <StyledFlipMove
      duration={200}
      easing="ease-out"
      staggerDelayBy={40}
      appearAnimation="fade"
      enterAnimation="elevator"
      leaveAnimation="elevator"
    >
      {sites
        .sort(sortByLastCommit)
        .map(site => mapToSiteLinks(site, isExpanded, setShow))}
    </StyledFlipMove>
  )
}

SiteLinks.propTypes = {
  sites: PropTypes.array,
}

export default SiteLinks
