import React, { Fragment } from 'react'
import { Link } from 'react-router-dom'

import { Wrapper } from './style'
import styled from 'styled-components'
import { typography, flex, spacing } from 'styles/traits'

import { standardPreviewLink } from 'helpers/previewLinks'
import { formatBuildTime } from 'helpers/dateFormatters'
import getEnvColor from 'helpers/envColors'

import { TextPlaceholder } from 'components/Placeholders'
import EnvLink from 'components/EnvLink'
import Text from 'components/Text'

import TableLoadingPlaceholder from './components/TableLoadingPlaceholder'
import HeaderCell from './components/HeaderCell'
import PullRequestBadge from './components/PullRequestBadge'
import CommitMetaData from './components/CommitMetaData'
import StatusCell from './components/StatusCell'
import StatusSquare from './components/StatusSquare'

import BranchLinksModal from 'views/BranchLinksModal'

export const Middot = styled.div`
  ${flex.fg1.align_center};
  ${spacing.pl05};
  text-decoration: none;
  ${typography.f2};
  color: hsl(0, 0%, 85%);
`
const StyledLink = styled(Link)`
  text-decoration: none;
  &:hover {
    span {
      color: ${({ theme }) => theme.space.default};
    }
  }
`

export default class CommitsTable extends React.Component {
  render() {
    const {
      limit,
      loading,
      branch,
      commits,
      sitename,
      pullRequest,
      environments,
    } = this.props

    if (loading) {
      return <TableLoadingPlaceholder />
    }

    const shouldDislpayBranchLinks = commits.some(commit => {
      const buildStatus = commit?.build?.status
      const buildPassed = buildStatus === 'success'
      return buildPassed ? true : false
    })

    return (
      <Wrapper>
        <HeaderCell gridArea="branch">
          {loading ? (
            <TextPlaceholder />
          ) : (
            <Fragment>
              <PullRequestBadge pullRequest={pullRequest} />
              <StyledLink
                to={`/sites/${sitename}/commits/${encodeURIComponent(branch)}`}
              >
                <Text weight="bold">{branch}</Text>
              </StyledLink>
              {shouldDislpayBranchLinks && (
                <BranchLinksModal
                  sitename={sitename}
                  environments={environments}
                  branch={branch}
                />
              )}
            </Fragment>
          )}
        </HeaderCell>
        <HeaderCell gridArea="build-header">
          <Text scale={6} weight="bold">
            builds
          </Text>
        </HeaderCell>
        <HeaderCell gridArea="tests-header">
          <Text scale={6} weight="bold">
            tests
          </Text>
        </HeaderCell>
        <HeaderCell gridArea="preview-header">
          <Text scale={6} weight="bold">
            preview
          </Text>
        </HeaderCell>
        {commits.slice(0, limit).map(commit => {
          const {
            commit_sha,
            head_commit: { author, message, timestamp, url },
            build,
            tests,
          } = commit
          return (
            <Fragment key={commit_sha}>
              <CommitMetaData
                author={author}
                timestamp={timestamp}
                commit_sha={commit_sha}
                message={message}
                url={url}
              />
              <StatusCell gridArea="build">
                {build ? (
                  <>
                    <Text scale={6} tone="secondary">
                      build
                    </Text>
                    {build.status ? (
                      <StatusSquare
                        title={
                          build.status === 'ignored'
                            ? `Ignored - missing "build:fab" script.`
                            : `Build ${build.status} - ${formatBuildTime({
                                started_at: build.started_at,
                                finished_at: build.finished_at,
                              })}`
                        }
                        status={build.status}
                        to={`/sites/${sitename}/commit/${commit_sha}/#build_log`}
                      />
                    ) : (
                      <Middot>&middot;</Middot>
                    )}
                  </>
                ) : (
                  <>
                    <Text scale={6} tone="secondary">
                      build
                    </Text>
                    <Middot>&middot;</Middot>
                  </>
                )}
              </StatusCell>
              <StatusCell gridArea="tests">
                {tests && (
                  <>
                    <Text scale={6} tone="secondary">
                      tests
                    </Text>
                    {!tests.test_results || tests.test_results.length === 0 ? (
                      <Middot>&middot;</Middot>
                    ) : (
                      tests.test_results.map((result, index) => (
                        <StatusSquare
                          title={`${result.type} - ${result.status}`}
                          key={index}
                          status={result.status}
                          to={`/sites/${sitename}/commit/${commit_sha}/#test_log`}
                        />
                      ))
                    )}
                  </>
                )}
              </StatusCell>
              <StatusCell gridArea="preview">
                {build && build.bundle_id ? (
                  <>
                    <Text scale={6} tone="secondary">
                      preview
                    </Text>
                    {environments.map(({ env_name }, index) => {
                      return (
                        <EnvLink
                          key={env_name}
                          color={getEnvColor(env_name, index)}
                          env_name={env_name}
                          url={standardPreviewLink({
                            sitenameSlug: sitename,
                            bundleIdSlug: build.bundle_id,
                            environmentSlug: env_name,
                          })}
                        />
                      )
                    })}
                  </>
                ) : (
                  <>
                    <Text scale={6} tone="secondary">
                      preview
                    </Text>
                    {environments.map(({ env_name }, index) => {
                      return (
                        <EnvLink
                          key={env_name}
                          nullState={true}
                          env_name={env_name}
                          color={getEnvColor(env_name, index)}
                        />
                      )
                    })}
                  </>
                )}
              </StatusCell>
            </Fragment>
          )
        })}
      </Wrapper>
    )
  }
}

CommitsTable.defaultProps = {
  limit: 5,
}
