import React, { useState } from 'react'
import styled from 'styled-components'
import AsyncSelect from 'react-select/async'
import { useDebouncedCallback } from 'use-debounce'
import { useMutation } from '@apollo/react-hooks'

import Inline from 'components/Inline'
import { Text } from 'components'
import { Button } from 'components/Buttons'

import { ADD_SITE_COLLABORATOR, GET_DATA } from '../graphql'

const RowGrid = styled.div`
  display: grid;
  grid-template-columns: 1fr auto;
  grid-gap: 1rem;
`

const SelectOptionLabel = ({ login, avatar_url }) => (
  <Inline gap={0.5}>
    <img alt="" style={{ height: '1rem' }} src={avatar_url} />
    <Text>{login}</Text>
  </Inline>
)

const selectOption = item => ({
  value: item.id,
  label: <SelectOptionLabel login={item.login} avatar_url={item.avatar_url} />,
})

const mapTopOptions = items => items.map(selectOption)

export default function AddCollaborator({ sitename }) {
  const [selected, setSelected] = useState()
  const [addSiteCollaborator, { loading }] = useMutation(
    ADD_SITE_COLLABORATOR,
    {
      refetchQueries: [{ query: GET_DATA, variables: { name: sitename } }],
    }
  )

  const debounceTime = 500 // in milliseconds
  const [debouncedLoadOptions] = useDebouncedCallback(
    (searchTerm, callback) => {
      if (searchTerm) {
        fetch('https://api.github.com/search/users?q=' + searchTerm)
          .then(res => res.json())
          .then(({ items }) => callback(mapTopOptions(items)))
          .catch(e => console.log(e))
      } else {
        callback([])
      }
    },
    debounceTime
  )

  const handleChange = ({ value }) => setSelected(value)

  const generateVariables = () => ({
    sitename,
    user_id: `github:${selected}`,
    access_level: 'read',
  })

  const handleSubmit = () =>
    addSiteCollaborator({ variables: generateVariables() })

  return (
    <RowGrid>
      <AsyncSelect
        cacheOptions
        placeholder="Search..."
        loadOptions={debouncedLoadOptions}
        onChange={handleChange}
      />
      <Button
        onClick={handleSubmit}
        disabled={loading ? true : !selected ? true : false}
        loading={loading}
      >
        Add Collaborator
      </Button>
    </RowGrid>
  )
}
