export const updateEnvironmentVariables = (
  sitename,
  env_name,
  auto_deploy,
  settings
) => ({
  input: {
    sitename,
    env_name: env_name.toLowerCase(),
    auto_deploy,
    settings,
  },
})

export const deleteEnvironmentVariables = (sitename, env_name) => ({
  sitename,
  env_name: env_name.toLowerCase(),
})

export const generalSettingsVariables = (
  sitename,
  release_branch,
  path,
  fab_path
) => ({
  sitename,
  input: {
    release_branch,
    path,
    fab_path,
  },
})

export const githubSettingsVariables = (
  sitename,
  pr_comments,
  commit_statuses,
  deployments
) => ({
  sitename,
  input: {
    pr_comments,
    commit_statuses,
    deployments,
  },
})
