import React from 'react'
import styled, { css } from 'styled-components'
import Tooltip from 'components/Tooltip'
import Stack from 'components/Stack'
import { Text } from 'components'
import { typography, flex, media } from 'styles/traits'

const baseStyle = css`
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 0.25rem;
  border-style: solid;
  border-width: 2px;
  margin-left: 0.5rem;
  text-decoration: none;

  &:first-of-type {
    margin-left: 0;
  }

  ${media.tablet} {
    height: 0.9rem;
    width: 1rem;
    margin: 0 0 0 0.5rem;

    &:first-of-type {
      margin-left: 0.5rem;
    }
  }
`

const Wrapper = styled.a`
  ${baseStyle};
  ${flex.center_both};
  ${typography.black};
  color: ${({ color }) => color};
  border-color: ${({ color }) => color};
  text-transform: capitalize;
  font-size: 10px;

  ${media.tablet} {
    border-width: 2px;
    font-size: 8px;
  }
`

const NullStateWrapper = styled.div`
  ${baseStyle};
  border-color: hsl(0, 0%, 85%);
`

const EmEnv = styled.em`
  background: ${props => props.color};
  padding: 0.1rem 0.15rem;
  border-radius: 0.25rem;
  color: white;
  ${typography.f6.bold};
`

export default function EnvLink(props) {
  if (props.nullState) {
    const content = (
      <Text tone="reverse">
        Preview in <em style={{ color: props.color }}>{props.env_name}</em>{' '}
        environment unavailable
      </Text>
    )
    return (
      <Tooltip
        content={content}
        arrow={true}
        placement="top"
        animation="fade"
        animateFill={false}
        duration={[250, 175]}
        delay={[150, 0]}
        distance={8}
      >
        <NullStateWrapper />
      </Tooltip>
    )
  }
  const content = (
    <Stack padding={0.25}>
      <Text tone="reverse">
        Preview change in <EmEnv color={props.color}>{props.env_name}</EmEnv>{' '}
        environment
      </Text>
    </Stack>
  )
  return (
    <Tooltip
      content={content}
      arrow={true}
      placement="top"
      animation="fade"
      animateFill={false}
      duration={[250, 175]}
      delay={[150, 0]}
      distance={8}
    >
      <Wrapper
        target="_blank"
        href={props.url}
        color={props.color}
        rel="noopener noreferrer"
      >
        {props.env_name.slice(0, 2)}
      </Wrapper>
    </Tooltip>
  )
}
