import { flex, spacing, border, typography } from 'styles/traits'
import styled, { css } from 'styled-components'
import { Button } from 'components/Buttons'
import { Link } from 'react-router-dom'
import { GradientOverlay } from 'styles/globals'
import { FlexRow } from 'components/FlexSections'

export const baseButtonStyle = css`
  ${flex.fg1};
  ${border.br3};
  ${border.ba};
  ${typography.f6};
  min-width: 100%;
  border-color: ${({ theme }) => theme.bg.border};
`

export const JoinButton = styled(Button)`
  ${baseButtonStyle};
  ${({ theme, starred }) =>
    starred &&
    css`
      background: ${theme.bg.wash};
      color: ${theme.text.default};
      &:hover {
        box-shadow: none;
        background: ${theme.bg.wash};
      }
    `}
`

export const ViewButton = styled(Link)`
  ${border.br3};
  ${border.ba};
  ${typography.f6.bold};
  ${flex.center_both};
  ${spacing.pv05.ph1};
  border-color: ${({ theme }) => theme.bg.border};
  text-decoration: none;
  color: ${({ theme }) => theme.text.default};
  background: ${({ theme }) => theme.bg.wash};

  &:hover {
    border-color: ${({ theme }) => theme.space.default};
  }
  min-height: 0.8rem;
`

export const Header = styled.div`
  ${flex.align_center.justify_space_btw};
  > * + * {
    ${spacing.ml1};
  }
`

export const CardTitle = styled(Link)`
  ${flex.align_center};
  text-decoration: none;
  position: relative;
  line-height: 1.5;
  min-width: 16rem;
  max-width: 16rem;
  white-space: nowrap;
  overflow: hidden;

  > span {
    ${GradientOverlay}
  }
  > * + * {
    ${spacing.ml1};
  }
`

export const RepoLink = styled.a`
  color: ${({ theme }) => theme.text.default};
  ${flex.align_center};
  text-decoration: none;
  svg {
    width: 0.8rem;
  }
  > * + * {
    ${spacing.ml05};
  }
  &:hover {
    span {
      color: ${({ theme }) => theme.space.default};
    }
    svg {
      color: ${({ theme }) => theme.space.default};
    }
  }

  span {
    line-height: 1.5;
    max-width: 15rem;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }
`

export const Row = styled.div`
  ${flex.justify_space_btw.align_center};
`

export const StarButton = styled.button`
  ${flex.center_both}
  margin: none;
  padding: none;
  background: none;
  border: none;
  cursor: pointer;

  svg {
    fill: ${({ theme, starred }) => (starred ? theme.special.alt : 'none')};
    color: ${({ theme, starred }) =>
      starred ? theme.special.alt : theme.special.border};
  }
  &:hover {
    svg {
      fill: ${({ theme, starred }) =>
        starred ? theme.special.default : 'none'};
      color: ${({ theme }) => theme.special.default};
    }
  }
`

export const CenteredFlexRow = styled(FlexRow)`
  ${flex.align_center};
  > * + * {
    ${spacing.ml1};
  }
`
