import React, { useState } from 'react'
import styled, { keyframes, css } from 'styled-components'
import immutable from 'object-path-immutable'
import { CopyToClipboard } from 'react-copy-to-clipboard'
import { Clipboard, Check, RefreshCw } from 'react-feather'
import { useMutation } from '@apollo/react-hooks'

import Stack from 'components/Stack'
import { Text, TextField, StatusMessage } from 'components'

import { GET_DATA, UPDATE_EXTERNAL_CI_SETTINGS } from '../../graphql'

const spin = keyframes`
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
`

const GridRow = styled.div`
  display: grid;
  grid-template-columns: 1fr auto auto;
  gap: 0.25rem;
`

const IconWrapper = styled.button`
  margin: 0;
  background: none;
  border: none;
  color: ${({ theme }) => theme.text.secondary};
  cursor: ${({ copied }) => (copied ? 'auto' : 'pointer')};
  border-radius: 0.25rem;

  svg {
    height: 1.2rem;
    width: 1.2rem;
  }

  &:hover {
    background: ${({ theme }) => theme.bg.wash};
    color: ${({ theme }) => theme.brand.default};
  }

  ${({ loading }) =>
    loading &&
    css`
      cursor: disabled;
      svg {
        animation: ${spin} 0.7s linear infinite;
      }
    `}
`

const updateCachedApiKey = (cache, sitename, new_api_key) => {
  // read GET_DATA query from cache
  const cachedData = cache.readQuery({
    query: GET_DATA,
    variables: { name: sitename },
  })

  // pull out external_ci_settins object
  const { external_ci_settings } = cachedData.view.site.settings

  // destructure it into object
  const new_external_ci_settings = { ...external_ci_settings }

  // update new object with new API key
  new_external_ci_settings.fab_upload_api_key = new_api_key

  // use immutable to update the cachedData object we read from the cache
  const path = `view.site.settings.external_ci_settings`
  const newData = immutable.set(cachedData, path, new_external_ci_settings)

  // then write the updated cachedData object back to the cache
  cache.writeQuery({
    query: GET_DATA,
    variables: { name: sitename },
    data: { ...newData },
  })
}

const KeyBox = ({ apiKey, sitename }) => {
  const [copied, setCopied] = useState(false)

  const handleCopyClick = () => {
    setCopied(true)
    setTimeout(() => {
      setCopied(false)
    }, 750)
  }

  const [regenerateToken, { loading, error }] = useMutation(
    UPDATE_EXTERNAL_CI_SETTINGS,
    {
      variables: {
        sitename,
        external_ci: true,
      },
      update: (cache, { data }) => {
        const new_api_key = data.updateExternalCISettings
        updateCachedApiKey(cache, sitename, new_api_key)
      },
    }
  )

  return (
    <Stack>
      <Text weight="bold">API Key</Text>
      <Text scale={6} tone="secondary">
        The{' '}
        <a
          href="https://github.com/bitgenics/fab-upload-cli"
          rel="noopener noreferrer"
          target="_blank"
        >
          fab-upload
        </a>{' '}
        package uses this key to upload FABs to Linc from an external CI
        service.
      </Text>
      <GridRow>
        <TextField
          inputProps={{
            value: apiKey,
            disabled: true,
            readOnly: loading,
          }}
        />
        <CopyToClipboard text={apiKey} onCopy={handleCopyClick}>
          {copied ? (
            <IconWrapper copied={copied}>
              <Check />
            </IconWrapper>
          ) : (
            <IconWrapper copied={copied} title="copy to clipboard">
              <Clipboard />
            </IconWrapper>
          )}
        </CopyToClipboard>
        <IconWrapper
          onClick={regenerateToken}
          title="Regenerate token"
          loading={loading}
          disabled={loading}
        >
          <RefreshCw />
        </IconWrapper>
      </GridRow>
      {error && (
        <StatusMessage>
          <Text>{error}</Text>
        </StatusMessage>
      )}
    </Stack>
  )
}

export default KeyBox
