import React from 'react'
import StatusSquare from 'views/CommitsTable/components/StatusSquare'
import { getDeploymentName } from 'helpers/constants'

// Used to combine two statuses into a single icon
const status_priority = ['none', 'queued', 'success', 'failed', 'running']

const getDeploymentStatus = (deployment_statuses, deployed_at) => {
  if (!deployment_statuses || deployment_statuses.length === 0)
    return [
      'none',
      'You must configure a production Release Config to trigger releases',
    ]

  const statuses = deployment_statuses.map(
    ({ deploy_target_name, status, timestamp }) => {
      const deployment_name = getDeploymentName(deploy_target_name)
      switch (status) {
        case 'pending':
          return ['queued', `${deployment_name} awaiting deployment.`]
        case 'deploying':
          return ['running', `${deployment_name} currently deploying.`]
        case 'success':
          return ['success', `${deployment_name} deployed successfully.`]
        case 'failed':
          return ['failed', `${deployment_name} failed to deploy.`]
        default:
          return ['none', `An unknown error occurred with ${deployment_name}.`]
      }
    }
  )

  const max_status = Math.max(
    ...statuses.map(pair => status_priority.indexOf(pair[0]))
  )
  const status = status_priority[max_status]
  return [status, statuses.map(pair => pair[1]).join('\n')]
}

export default function DeploymentStusIcon({ release_status }) {
  const { deployment_statuses, current_release } = release_status
  const [status, tooltipText] = getDeploymentStatus(
    deployment_statuses,
    current_release.deployed_at
  )
  return <StatusSquare title={tooltipText} status={status} />
}
