import React, { useState } from 'react'
import { CheckSquare, Square } from 'react-feather'
import { useMutation } from '@apollo/react-hooks'

import { GET_DATA, UPDATE_EXTERNAL_CI_SETTINGS } from '../../graphql'

import Inline from 'components/Inline'
import { TextButton, Button } from 'components/Buttons'
import { Text } from 'components'
import {
  Form,
  FormHeader,
  FormBody,
  FormFooter,
  CheckboxRowStyle,
} from 'components/FormElements'

import KeyBox from './KeyBox'

const Row = ({ subHeading, value, setValue, disabled }) => (
  <div>
    <CheckboxRowStyle type="button" onClick={setValue} disabled={disabled}>
      {value === true && <CheckSquare />}
      {value === false && <Square />}
      <Text>{subHeading}</Text>
    </CheckboxRowStyle>
  </div>
)

export default function SettingsForm(props) {
  const { sitename } = props
  const { external_ci_enabled, fab_upload_api_key } = props.settings
  const [dirty, setDirty] = useState(false)

  const [CIEnabled, setCIEnabled] = useState(external_ci_enabled)
  const toggleCIEnabled = () => {
    setCIEnabled(!CIEnabled)
    setDirty(true)
  }
  const { admin } = props.siteAccess

  const reset = () => {
    setCIEnabled(external_ci_enabled)
    setDirty(false)
  }

  const mutationVariables = {
    sitename,
    external_ci: CIEnabled,
  }

  const [mutation, { loading }] = useMutation(UPDATE_EXTERNAL_CI_SETTINGS, {
    variables: mutationVariables,
    refetchQueries: [{ query: GET_DATA, variables: { name: sitename } }],
    onCompleted: () => setDirty(false),
  })

  const submit = e => {
    mutation(mutationVariables)
    e.preventDefault()
  }

  return (
    <Form onSubmit={submit}>
      <FormHeader>
        <Text scale={4} weight="medium">
          External CI settings
        </Text>
      </FormHeader>
      <FormBody>
        <Row
          heading="Toggle External CI"
          subHeading="Use external CI service"
          disabled={!admin || loading}
          setValue={toggleCIEnabled}
          value={CIEnabled}
        />
        {external_ci_enabled && fab_upload_api_key && (
          <KeyBox sitename={sitename} apiKey={fab_upload_api_key} />
        )}
      </FormBody>
      {admin && (
        <FormFooter>
          <Inline gap={1}>
            {dirty && (
              <TextButton type="button" onClick={reset} disabled={loading}>
                Cancel
              </TextButton>
            )}
            <Button
              disabled={loading || !dirty}
              loading={loading}
              type="submit"
            >
              Save
            </Button>
          </Inline>
        </FormFooter>
      )}
    </Form>
  )
}
