import styled, { css, keyframes } from 'styled-components'
import { border } from 'styles/traits'

const ANIMATION_DURATION = 750 // ms

const basePlaceholderStyle = css`
  background: hsla(210, 40%, 95%, 0.75);
  transition: all 0.5s;
  ${border.br2};
`

const animateGradient = keyframes`
  0% {
    background-position: 100% 0%
  }
  100% {
    background-position: 15% 100%
  }
`

export const TextPlaceholderStyle = styled.div`
  ${basePlaceholderStyle};
  background: hsla(210, 40%, 95%, 0.75);
  background: linear-gradient(
    45deg,
    hsla(210, 40%, 95%, 0.75) 0%,
    hsla(220, 29%, 90%, 0.75) 33%,
    hsla(210, 40%, 95%, 0.75) 66%,
    hsla(220, 29%, 90%, 0.75) 100%
  );
  background-size: 400% 400%;
  line-height: 1;
  height: 0.5rem;
  width: ${({ width }) => width}rem;
  animation: ${animateGradient} ${ANIMATION_DURATION}ms linear infinite;
`

export const ImagePlaceholderStyle = styled.div`
  ${basePlaceholderStyle};
  background: hsla(210, 40%, 95%, 0.75);
  background: linear-gradient(
    45deg,
    hsla(210, 40%, 95%, 0.75) 0%,
    hsla(220, 29%, 90%, 0.75) 33%,
    hsla(210, 40%, 95%, 0.75) 66%,
    hsla(220, 29%, 90%, 0.75) 100%
  );
  background-size: 400% 400%;
  height: ${({ height }) => height}rem;
  width: ${({ width }) => width}rem;
  animation: ${animateGradient} ${ANIMATION_DURATION}ms linear infinite;
`
