import React from 'react'
import { useQuery } from '@apollo/react-hooks'
import { GET_COMMIT } from 'graphql/queries'
import { Text, StatusMessage } from 'components'
import CommitPage from './components/CommitPage'
import FullPageLoader from 'components/Loaders/FullPageLoader'
import { useParams } from 'react-router'

export default function Commit() {
  const { sitenameSlug, commitShaSlug } = useParams()
  const { loading, error, data, subscribeToMore } = useQuery(GET_COMMIT, {
    variables: { sitename: sitenameSlug, commit_sha: commitShaSlug },
  })

  if (loading) {
    return <FullPageLoader />
  }

  if (error) {
    return `${error.message}`
  }

  const commit = data?.view?.site?.commit
  const environments = data?.view?.site?.environments

  if (commit && environments) {
    return (
      <CommitPage
        subscribeToMore={subscribeToMore}
        environments={environments}
        sitename={sitenameSlug}
        commit={commit}
      />
    )
  } else {
    return (
      <StatusMessage status="critical">
        <Text>ERROR: Unable to render Commit page</Text>
      </StatusMessage>
    )
  }
}
