import React from 'react'
import { Text, StatusMessage } from 'components'
import { useQuery } from '@apollo/react-hooks'
import Stack from 'components/Stack'
import Select from 'react-select'
import gql from 'graphql-tag'

const GET_PROJECTS = gql`
  query($authentication_token: String!) {
    view {
      bugherd(authentication_token: $authentication_token) {
        projects {
          name
          id
        }
      }
    }
  }
`

const mapProjectsTpOptions = projects =>
  projects.map(project => ({
    value: project.id,
    label: project.name,
  }))

const Projects = ({ authToken, selectProject }) => {
  const { loading, error, data } = useQuery(GET_PROJECTS, {
    variables: { authentication_token: authToken },
  })

  if (loading) {
    return 'Loading projects...'
  }

  if (error) {
    return (
      <StatusMessage status="critical">
        <Text>{error.message}</Text>
      </StatusMessage>
    )
  }

  const projects = data?.view?.bugherd?.projects || []
  const options = mapProjectsTpOptions(projects)
  return (
    <Stack>
      <Text weight="bold">Project</Text>
      <Text>Your BugHerd project</Text>
      <Select options={options} onChange={selectProject} />
    </Stack>
  )
}

export default Projects
