import React from 'react'
import { useLocation } from 'react-router'
import styled from 'styled-components'
import { typography, spacing } from 'styles/traits'
import { HashLink } from 'react-router-hash-link'

export const SectionContainer = styled(HashLink)`
  ${spacing.pv066.ph15};

  border-width: 1px 0 0 0;
  border-style: solid;
  text-decoration: none;

  color: ${({ theme }) => theme.text.secondary};
  background: ${({ theme }) => theme.bg.wash};
  border-color: ${({ theme }) => theme.bg.border};

  ${({ isActive }) => (isActive ? typography.f6.bold : typography.f6)};

  &:hover {
    color: ${({ theme }) => theme.text.default};
  }
`

export default function Section({ title, hashlink }) {
  const { hash } = useLocation()
  const isActive = hash === hashlink
  return (
    <SectionContainer smooth to={hashlink} isActive={isActive}>
      {title}
    </SectionContainer>
  )
}
