import gql from 'graphql-tag'

export const FEEDBACK_OVERVIEW = gql`
  query($name: String!) {
    view {
      site(name: $name) {
        access {
          admin
          read
          write
          no_access
        }
        settings {
          feedback_settings {
            feedback_enabled
          }
        }
        allSiteFeedback {
          author_name
          bundle_id
          comment
          created_at
          current_url
          screenshot_url
          sitename
          uuid
          associated_commits
          associated_branches
          browser_metadata {
            height
            innerHeight
            innerWidth
            scrollX
            scrollY
            userAgent
            parsedUserAgent
            width
          }
        }
      }
    }
  }
`
