import React from 'react'
import { StatusMessage, Text } from 'components'
import { RepoListStyle } from './style'
import { useQuery } from '@apollo/react-hooks'
import { GET_INSTALLATIONS } from 'graphql/queries'
import RepoCard from './components/RepoCard'
import RepoList from './components/RepoList'
import NoRepos from './components/NoRepos'

export default function RepoSelect() {
  const { loading, error, data } = useQuery(GET_INSTALLATIONS)

  if (loading) {
    return (
      <RepoListStyle>
        <RepoCard />
        <RepoCard />
        <RepoCard />
        <RepoCard />
      </RepoListStyle>
    )
  }

  if (error) {
    return (
      <StatusMessage status="critical">
        <Text tone="reverse">{error.message}</Text>
      </StatusMessage>
    )
  }

  const installations = data?.view?.github?.installations
  if (installations && installations.length > 0) {
    return <RepoList view={data.view} />
  } else {
    return (
      <NoRepos
        message="No connected repositories"
        buttonText="Connect GitHub repositories"
      />
    )
  }
}
