import PropTypes from 'prop-types'
import styled, { css } from 'styled-components'
import { flex, typography, border, media } from 'styles/traits'

const Badge = styled.div`
  ${flex.align_center};
  ${typography.f6.bold};
  ${border.br2};
  color: white;
  text-decoration: none;
  padding: 4px 6px;
  margin-left: 1rem;
  text-transform: uppercase;
  font-weight: bold;
  max-height: 1rem;

  ${media.tablet} {
    padding: 4px 6px;
    font-size: .7rem;
  }

  ${props =>
    props.color === 'red' &&
    css`
      background: ${props.theme.warn.default};
    `}

  ${props =>
    props.color === 'yellow' &&
    css`
      color: black;
      background: ${props.theme.special.alt};
    `}

  ${props =>
    props.color === 'green' &&
    css`
      color: black;
      background: ${props.theme.success.default};
    `}

  ${props =>
    props.color === 'blue' &&
    css`
      background: ${props.theme.space.default};
    `}
`

Badge.defaultProps = {
  color: 'blue',
}

Badge.propTypes = {
  color: PropTypes.oneOf(['red', 'yellow', 'green', 'blue']),
}

export default Badge
