import React, { useState } from 'react'
import { Text, StatusMessage } from 'components'
import { TextButton, Button } from 'components/Buttons'
import { useMutation } from '@apollo/react-hooks'
import { GET_DATA, UPDATE_GITHUB_SETTINGS } from '../graphql'
import { githubSettingsVariables } from 'graphql/variableFormatters'
import { CheckSquare, Square } from 'react-feather'
import {
  Form,
  FormHeader,
  FormBody,
  FormFooter,
  CheckboxRowStyle,
} from 'components/FormElements'
import Inline from 'components/Inline'

const Row = ({ subHeading, value, setValue, setDirty, readOnly }) => {
  return (
    <div>
      <CheckboxRowStyle
        type="button"
        disabled={readOnly}
        onClick={() => {
          setDirty(true)
          setValue(!value)
        }}
      >
        {value === true && <CheckSquare />}
        {value === false && <Square />}
        <Text>{subHeading}</Text>
      </CheckboxRowStyle>
    </div>
  )
}

const GithubSettings = props => {
  const { sitename, settings } = props
  const [prComments, setPrComments] = useState(settings.pr_comments)
  const [commitStatuses, setCommitStatuses] = useState(settings.commit_statuses)
  const [deployments, setDeployments] = useState(settings.deployments)
  const [dirty, setDirty] = useState(false)
  const { admin } = props.siteAccess

  const resetForm = () => {
    const settings = props.github_settings
    setPrComments(settings.pr_comments)
    setCommitStatuses(settings.commit_statuses)
    setDeployments(settings.deployments)
    setDirty(false)
  }

  const refetchQueries = [{ query: GET_DATA, variables: { name: sitename } }]

  const mutationVariables = githubSettingsVariables(
    sitename,
    prComments,
    commitStatuses,
    deployments
  )

  const [updateGithubSettings, { loading, error }] = useMutation(
    UPDATE_GITHUB_SETTINGS,
    {
      variables: mutationVariables,
      refetchQueries: refetchQueries,
      onCompleted: () => setDirty(false),
    }
  )

  return (
    <Form
      onSubmit={e => {
        updateGithubSettings(mutationVariables)
        e.preventDefault()
      }}
    >
      <FormHeader>
        <Text scale={4} weight="medium">
          Github Preferences
        </Text>
      </FormHeader>
      <FormBody gap={2}>
        <Row
          heading="PR Comments"
          subHeading="Allow Linc bot to comment on pull requests"
          value={prComments}
          setValue={setPrComments}
          setDirty={setDirty}
          readOnly={!admin}
        />
        <Row
          heading="Linc Commit Status"
          subHeading="Allow Linc to apply a Linc status to your commits"
          value={commitStatuses}
          setValue={setCommitStatuses}
          setDirty={setDirty}
          readOnly={!admin}
        />
        <Row
          heading="Deployments"
          subHeading="Enable Linc preview deployment urls in pull requests"
          value={deployments}
          setValue={setDeployments}
          setDirty={setDirty}
          readOnly={!admin}
        />
        {error && (
          <StatusMessage status="critical">
            <Text>{error.message}</Text>
          </StatusMessage>
        )}
      </FormBody>
      {admin && (
        <FormFooter>
          <Inline gap={1}>
            {dirty && (
              <TextButton type="button" onClick={resetForm} disabled={loading}>
                Cancel
              </TextButton>
            )}
            <Button type="submit" loading={loading} disabled={!dirty}>
              Save
            </Button>
          </Inline>
        </FormFooter>
      )}
    </Form>
  )
}

export default GithubSettings
