export const formatCommitsByBranch = commits => {
  const commitsByBranch = []
  for (const commit of commits) {
    const branch = commitsByBranch.find(item => item.branch === commit.branch)
    if (!branch) {
      commitsByBranch.push({
        branch: commit.branch,
        commits: [commit],
        pullRequest: commit.pull_request,
      })
    } else {
      branch.commits.push(commit)
      if (!branch.pullRequest && commit.pull_request) {
        branch.pullRequest = true
      }
    }
  }
  return commitsByBranch
}
