import React, { Fragment } from 'react'
import { Form, FormHeader, FormBody } from 'components/FormElements'
import { Text } from 'components'

export default function AddToSlack({ sitename, siteAccess }) {
  const { admin } = siteAccess
  const CLIENT_ID = '123528931698.665028345747'
  const SCOPE = 'chat:write:bot,channels:read'
  const href = `https://slack.com/oauth/authorize?scope=${SCOPE}&client_id=${CLIENT_ID}&state=${sitename}`
  return (
    <Form id="slack">
      <FormHeader>
        <Text scale={4} weight="medium">
          Slack integration
        </Text>
      </FormHeader>
      <FormBody>
        {admin ? (
          <Fragment>
            <Text>
              Click the button below to install the Linc Slack app on your Slack
              workspace:
            </Text>
            <a href={href}>
              <img
                alt="Add to Slack"
                height="40"
                width="139"
                src="https://platform.slack-edge.com/img/add_to_slack.png"
                srcSet="https://platform.slack-edge.com/img/add_to_slack.png 1x, https://platform.slack-edge.com/img/add_to_slack@2x.png 2x"
              />
            </a>
          </Fragment>
        ) : (
          <Text.P>
            You must be an admin of <code>{sitename}</code> to view or edit
            Slack integration settings
          </Text.P>
        )}
      </FormBody>
    </Form>
  )
}
