import gql from 'graphql-tag'

export const GET_DATA = gql`
  query($name: String!) {
    view {
      site(name: $name) {
        access {
          admin
          write
          read
          no_access
        }
        settings {
          general_settings {
            release_branch
            path
            fab_path
          }
          build_config {
            variables {
              name
              value
            }
          }
          external_ci_settings {
            external_ci_enabled
            fab_upload_api_key
          }
        }
      }
    }
  }
`

export const UPDATE_GENERAL_SETTINGS = gql`
  mutation($sitename: String!, $input: GeneralSiteSettingsInput!) {
    updateGeneralSiteSettings(sitename: $sitename, input: $input)
  }
`

export const UPDATE_BUILD_CONFIG = gql`
  mutation($sitename: String!, $input: BuildConfigInput!) {
    updateBuildConfig(sitename: $sitename, input: $input)
  }
`

export const UPDATE_EXTERNAL_CI_SETTINGS = gql`
  mutation($sitename: String!, $external_ci: Boolean!) {
    updateExternalCISettings(sitename: $sitename, external_ci: $external_ci)
  }
`
