import React from 'react'
import styled from 'styled-components'
import { flex, spacing, border, media } from 'styles/traits'
import Text from 'components/Text'
import { TextPlaceholder } from 'components/Placeholders'
import { releaseAccentColor, withShadow } from '../style'
import relativeDate from 'relative-date'
import { GitBranch } from 'react-feather'

const Wrapper = styled.header`
  ${flex.justify_space_btw};
`

const Tab = styled.div`
  ${flex.center_both};
  ${spacing.p05};
  ${border.bt.br};
  border-color: ${releaseAccentColor};
  border-radius: 0 0.25rem 0 0;
  background: ${({ theme }) => theme.bg.default};
  span {
    color: ${releaseAccentColor};
  }
  z-index: 1;
  ${withShadow};
  > * + * {
    ${spacing.ml066};
  }

  ${media.tablet} {
    > span {
      display: none;
    }
    > * + * {
      ${spacing.ml0};
    }
  }
`

const TimeStamp = styled.div`
  ${spacing.p05};
`

const BranchContainer = styled.div`
  ${flex.align_center};
  svg {
    color: ${releaseAccentColor};
    height: 1rem;
  }
`

export default function Header({ loading, release_status }) {
  if (loading) {
    return (
      <Wrapper>
        <Tab>
          <Text scale={6} weight="bold">
            CURRENT RELEASE
          </Text>
        </Tab>
        <TimeStamp>
          <TextPlaceholder />
        </TimeStamp>
      </Wrapper>
    )
  } else {
    const { release_branch, modified_at } = release_status
    return (
      <Wrapper>
        <Tab title={`Release branch: ${release_branch}`}>
          <Text scale={6} weight="bold">
            CURRENT RELEASE
          </Text>
          <Text>~</Text>
          <BranchContainer>
            <GitBranch />
            <Text weight="medium">{release_branch}</Text>
          </BranchContainer>
        </Tab>
        <Text scale={6} tone="secondary" />
        <TimeStamp>
          <Text scale={6} tone="secondary">
            Released {relativeDate(modified_at)}
          </Text>
        </TimeStamp>
      </Wrapper>
    )
  }
}
