import React, { useState } from 'react'
import Stack from 'components/Stack'
import { Text, Card } from 'components'
import { MoreHorizontal } from 'react-feather'
import { formatDistanceToNow } from 'date-fns'
import BranchTag from 'components/BranchTag'
import { TextPlaceholder, ImagePlaceholder } from 'components/Placeholders'
import MetaData from './MetaData'
import {
  Header,
  BaselineFlexRow,
  CenteredFlexRow,
  Content,
  Comment,
  Image,
  Footer,
  NoImage,
} from './style'

const LoadingPlaceholder = () => (
  <Card>
    <Stack gap={0.5}>
      <Header>
        <CenteredFlexRow>
          <TextPlaceholder />
          <TextPlaceholder />
        </CenteredFlexRow>
        <CenteredFlexRow>
          <TextPlaceholder />
        </CenteredFlexRow>
      </Header>
      <Content>
        <ImagePlaceholder height={10} width={20} />
        <Stack>
          <TextPlaceholder />
          <Stack>
            <TextPlaceholder />
            <TextPlaceholder />
          </Stack>
        </Stack>
      </Content>
      <Footer>
        <button disabled>
          <MoreHorizontal />
        </button>
      </Footer>
    </Stack>
  </Card>
)

export default function FeedbackCard({ loading, feedback, branchTags }) {
  const [showMetaData, setShowMetaData] = useState(false)
  const toggleShowMetaData = () => setShowMetaData(!showMetaData)
  if (loading) {
    return <LoadingPlaceholder />
  } else {
    const {
      author_name,
      comment,
      created_at,
      current_url,
      screenshot_url,
      associated_branches,
      browser_metadata,
    } = feedback
    return (
      <Card>
        <Stack gap={0.5}>
          <Header>
            <BaselineFlexRow>
              <Text>{author_name}</Text>
              <Text scale={6} tone="secondary">
                {formatDistanceToNow(parseInt(created_at))} ago
              </Text>
            </BaselineFlexRow>
            <CenteredFlexRow>
              {associated_branches.map(branch => (
                <BranchTag
                  key={branch}
                  selected={branchTags && branchTags.includes(branch)}
                >
                  <Text tone="secondary" scale={6}>
                    {branch}
                  </Text>
                </BranchTag>
              ))}
            </CenteredFlexRow>
          </Header>
          <Content>
            {screenshot_url ? (
              <a
                href={screenshot_url}
                target="_blank"
                rel="noreferrer noopener"
              >
                <Image src={screenshot_url} />
              </a>
            ) : (
              <NoImage>
                <Text tone="placeholder">No image</Text>
                <Text tone="placeholder">attached</Text>
              </NoImage>
            )}
            <Stack style={{ alignSelf: 'flex-start' }}>
              <Text scale={6}>
                <a href={current_url} target="_blank" rel="noreferrer noopener">
                  {current_url}
                </a>
              </Text>
              <Comment>{comment}</Comment>
            </Stack>
          </Content>
          {showMetaData && <MetaData metaData={browser_metadata} />}
          <Footer>
            <button onClick={toggleShowMetaData}>
              <MoreHorizontal />
            </button>
          </Footer>
        </Stack>
      </Card>
    )
  }
}
