import React from 'react'
import styled from 'styled-components'
import { Info } from 'react-feather'

import usePackagePath, { PATH_ERRORS } from 'hooks/usePackagePath'
import { useParams, useHistory } from 'react-router'
import { useMutation } from '@apollo/react-hooks'
import useSiteName from 'hooks/useSiteName'

import { GET_SIDEBAR_SITES_LIST } from 'graphql/queries'
import { CREATE_NEW_SITE } from 'graphql/mutations'

import { flex } from 'styles/traits'

import { Text, TextField, StatusMessage } from 'components'
import { Button, TextButton } from 'components/Buttons'
import { FlexRow } from 'components/FlexSections'
import Inline from 'components/Inline'
import Tooltip from 'components/Tooltip'
import Stack from 'components/Stack'

import SiteField from './SiteField'

const CenteredFlexRow = styled(FlexRow)`
  ${flex.align_center};
  svg {
    height: 1rem;
    width: 1rem;
  }
`

const ownerRepoRegex = /(.*?)\/(.*)/

const parseRepo = full_repo => {
  const regexResult = full_repo.match(ownerRepoRegex)
  const owner = regexResult[1]
  const repo = regexResult[2]
  return { owner, repo }
}

export default function GitHubConf() {
  const history = useHistory()
  const params = useParams()
  const repositorySlug = decodeURIComponent(params.repositorySlug)
  const branchSlug = decodeURIComponent(params.branchSlug)

  // use repository name as initial suggested sitename, truncated to 20 characters
  const initalSiteNameValue = parseRepo(repositorySlug).repo.substring(0, 20)

  // set up form state
  const path = usePackagePath('/')
  const sitename = useSiteName(initalSiteNameValue)

  // required fields to generate new site used by mutation
  const mutationVariables = () => ({
    input: {
      name: sitename.legalValue,
      repository: repositorySlug,
      path: path.legalPathName(),
      release_branch: branchSlug,
    },
  })

  const redirectToNewSite = () => history.push(`/sites/${sitename.legalValue}`)

  const goBack = () => history.push('/create/github')

  const isValid = sitename.isValid && repositorySlug.length && path.isValid

  const [createNewSite, { loading, error }] = useMutation(CREATE_NEW_SITE, {
    variables: mutationVariables(),
    onCompleted: redirectToNewSite,
    refetchQueries: [{ query: GET_SIDEBAR_SITES_LIST }],
  })

  return (
    <Stack
      gap={2}
      as="form"
      onSubmit={e => {
        createNewSite(mutationVariables())
        e.preventDefault()
      }}
    >
      <SiteField sitename={sitename} initialValue={initalSiteNameValue} />
      <Stack gap={0.5}>
        <CenteredFlexRow internalSpacing={0.5}>
          <Text weight="bold">Path to package.json</Text>
          <Tooltip
            content="The file path to your repository's package.json"
            arrow={true}
            placement="top"
            animation="scale"
            animateFill={false}
            duration={[250, 175]}
            delay={[150, 0]}
            distance={8}
          >
            <div>
              <Info />
            </div>
          </Tooltip>
        </CenteredFlexRow>
        <TextField
          inputProps={{
            value: path.value,
            placeholder: '/package.json',
            onChange: e => path.setPath(e.target.value),
            type: 'text',
            required: true,
            maxLength: 120,
          }}
          status={path.status}
        />
        {path.errors &&
          path.errors.map(error => {
            if (error === PATH_ERRORS.MISSING_EXTENSION) {
              return (
                <StatusMessage key={error} status="critical">
                  <Text>
                    Path must end in <code>/package.json</code>
                  </Text>
                </StatusMessage>
              )
            } else {
              return null
            }
          })}
      </Stack>
      {error && (
        <StatusMessage status="critical">
          <Text>{error.message}</Text>
        </StatusMessage>
      )}
      <Inline style={{ justifyContent: 'flex-end' }}>
        <TextButton type="button" onClick={goBack}>
          Go back
        </TextButton>
        <Button type="submit" disabled={!isValid} loading={loading}>
          Create site
        </Button>
      </Inline>
    </Stack>
  )
}
