// convert a string to a legal sitename
export default function convertToLegalSiteName(val) {
  const formatted = val
    .replace(/\s+/g, '-') // replace spaces with dashes
    .replace(/[^a-zA-Z0-9-]/g, '') // strip out non alpha / numeric / dashes
    .replace(/^[^a-zA-Z0-9]+/g, '') // strip out leading non alpha-numeric characters
    .replace(/-+$/, '') // strip out trailing dashes
    .replace(/--+/g, '-') // replace consecutive dashes with single dash
    .toLowerCase()
  return formatted
}
