export const standardPreviewLink = ({
  sitenameSlug,
  bundleIdSlug,
  environmentSlug,
}) =>
  `https://${sitenameSlug}--${bundleIdSlug.substr(0, 9)}--${environmentSlug ||
    'production'}.linc-preview.sh`

export const latestBranchCommitPreviewLink = ({
  sitenameSlug,
  environmentSlug,
  branchSlug,
}) =>
  `https://${sitenameSlug}--${environmentSlug}--${branchSlug}.branch.linc-preview.sh/`

export const activeReleasePreviewLink = ({ sitenameSlug, environmentSlug }) =>
  `https://${sitenameSlug}--${environmentSlug}.release.linc-preview.sh/`

export const trialReleaseUrl = ({ sitenameSlug }) =>
  `https://${sitenameSlug}.linc-trial.sh`
