import React from 'react'
import styled, { css, keyframes } from 'styled-components'
import { useMutation } from '@apollo/react-hooks'
import { REBUILD_COMMIT } from 'graphql/mutations'
import { RefreshCw } from 'react-feather'
import { Text } from 'components'
import { flex, spacing, border } from 'styles/traits'
import Tooltip from 'components/Tooltip'
import config from 'config'

const spin = keyframes`
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
`

const Wrapper = styled.button`
  background: ${({ theme }) => theme.bg.wash};
  border-color: ${({ theme }) => theme.bg.border};
  ${flex.center_both};
  ${border.ba.br2};
  ${spacing.ph1.pv05};
  > * + * {
    ${spacing.ml05};
  }
  svg {
    color: ${({ theme }) => theme.text.default};
    height: 1rem;
    width: 1rem;
    ${props =>
      props.loading &&
      css`
        animation: ${spin} 0.7s linear 0s infinite;
      `}
  }
  cursor: pointer;

  &:hover {
    border-color: ${({ theme }) => theme.brand.default};
    svg {
      color: ${({ theme }) => theme.brand.default};
    }
    span {
      color: ${({ theme }) => theme.brand.default};
    }
  }

  &:disabled {
    cursor: not-allowed;
    border-color: ${({ theme }) => theme.bg.hairline};
    svg {
      color: ${({ theme }) => theme.bg.hairline};
    }
    span {
      color: ${({ theme }) => theme.bg.hairline};
    }
  }
`

export default function RebuildButton({ sitename, tree_id, buildStatus }) {
  const isAdminMode = config.ADMIN_MODE

  const [rebuildCommit, { loading, error }] = useMutation(REBUILD_COMMIT, {
    variables: { sitename, tree_id },
  })

  const handleClick = () => {
    rebuildCommit()
  }

  if (error) {
    console.log(error)
  }

  const isDisabledForAdmin =
    buildStatus === 'pending' ||
    buildStatus === 'running' ||
    buildStatus === 'queued' ||
    loading

  const isDisabled =
    buildStatus === 'success' ||
    buildStatus === 'pending' ||
    buildStatus === 'running' ||
    buildStatus === 'queued' ||
    loading

  const adminTooltipContent = isDisabledForAdmin
    ? 'Build in progress...'
    : 'Rebuild commit'

  const tooltipContent = isDisabled
    ? 'You can only rebuild failed builds'
    : 'Rebuild commit'

  return (
    <Tooltip
      content={isAdminMode ? adminTooltipContent : tooltipContent}
      arrow={true}
      placement="top"
      animation="fade"
      animateFill={false}
      duration={[250, 175]}
      delay={[150, 0]}
      distance={8}
    >
      <div>
        <Wrapper
          disabled={isAdminMode ? isDisabledForAdmin : isDisabled}
          loading={loading}
          onClick={handleClick}
        >
          <RefreshCw />
          <Text>{loading ? 'Restarting...' : 'Rebuild'}</Text>
        </Wrapper>
      </div>
    </Tooltip>
  )
}
