import gql from 'graphql-tag'

export const GET_DATA = gql`
  query($name: String!) {
    view {
      site(name: $name) {
        access {
          admin
          write
          read
          no_access
        }
        settings {
          github_settings {
            pr_comments
            commit_statuses
            deployments
          }
          slack_settings {
            slack_app_is_installed
            messages_disabled
            selected_channel {
              id
              name
            }
            channels {
              id
              name
            }
          }
          bugherd_settings {
            authentication_token
            projects {
              name
              id
            }
            selected_project {
              name
              id
            }
          }
        }
      }
    }
  }
`

export const UPDATE_GITHUB_SETTINGS = gql`
  mutation($sitename: String!, $input: GithubSettingsInput!) {
    updateGithubSettings(sitename: $sitename, input: $input)
  }
`

export const UPDATE_SLACK_SETTINGS = gql`
  mutation($sitename: String!, $input: SlackSettingsInput!) {
    updateSlackSettings(sitename: $sitename, input: $input)
  }
`

export const UPDATE_BUGHERD_SETTINGS = gql`
  mutation($sitename: String!, $input: BugHerdSettingsInput!) {
    updateBugHerdSettings(sitename: $sitename, input: $input)
  }
`
