import React, { useRef, useState, useEffect } from 'react'
import styled from 'styled-components'
import { flex, spacing, border } from 'styles/traits'
import { ChevronUp, ChevronDown } from 'react-feather'
import { Shadow, hexa } from 'styles/globals'
import { Text } from 'components'
import { useHistory } from 'react-router'
import config from 'config'
import { useQuery } from '@apollo/react-hooks'
import gql from 'graphql-tag'
import { ImagePlaceholder } from 'components/Placeholders'

const Wrapper = styled.div`
  position: relative;
  ${spacing.ph1.ml2};
`

const Header = styled.div`
  display: flex;
  align-items: center;
  cursor: pointer;
  position: relative;
  svg {
    width: 1rem;
    color: white;
  }
`

const Avatar = styled.img`
  ${spacing.mr05};
  height: 2rem;
  width: 2rem;
  border-radius: 50%;
`

const List = styled.div`
  ${border.ba.br2};
  ${flex.vertical};
  z-index: 10;
  position: absolute;
  width: 15rem;
  margin-top: 1.5rem;
  right: 0.5rem;
  border-color: ${({ theme }) => theme.bg.border};
  background: ${({ theme }) => theme.bg.default};
  box-shadow: ${Shadow.high} ${({ theme }) => hexa(theme.bg.reverse, 0.2)};

  > * + * {
    ${border.bt}
    border-color: ${({ theme }) => theme.bg.border};

    &:hover {
      cursor: pointer;
      background: ${({ theme }) => theme.space.default};
      span {
        color: white;
      }
    }
  }

  &:after, &:before {
    bottom: 100%;
    left: 80%;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
  }

  &:after {
    border-color: rgba(255, 255, 255, 0);
    border-bottom-color: #ffffff;
    border-width: 15px;
    margin-left: -15px;
  }
  &:before {
    border-color: rgba(211, 220, 233, 0);
    border-bottom-color: #D3DCE9;
    border-width: 16px;
    margin-left: -16px;
  }
`

const ListItem = styled.div`
  ${spacing.p1};
  cursor: default;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-decoration: none;
`

const GET_LOGIN_DETAILS = gql`
  {
    view {
      details {
        is_admin
        login
      }
    }
  }
`

export default function DropDown() {
  const history = useHistory()
  const [open, setOpen] = useState(false)
  const { loading, error, data } = useQuery(GET_LOGIN_DETAILS)
  const isDemo = config.DEMO_MODE
  const node = useRef()
  const toggleList = () => setOpen(!open)

  const handleClick = e => {
    if (node && node.current && node.current.contains(e.target)) {
      return
    }
    setOpen(false)
  }

  useEffect(() => {
    document.addEventListener('mousedown', handleClick)
    return () => {
      document.removeEventListener('mousedown', handleClick)
    }
  }, [])

  const selectItem = action => {
    action()
    setOpen(false)
  }

  const redirectToBilling = () => history.push('/billing')

  const redirectToAdminSite = () => {
    const branch = 'master'
    const environment = 'admin'
    const adminLink = `https://linc-front-end--${environment}--${branch}.branch.linc-preview.sh`
    const pathname = window.location.pathname
    window.location = adminLink + pathname
  }

  if (loading) {
    return (
      <Wrapper>
        <ImagePlaceholder height={2} width={2} />
      </Wrapper>
    )
  }
  if (error) {
    return 'error'
  }
  if (data) {
    const { login, admin } = data.view.details
    return (
      <Wrapper ref={node}>
        <Header onClick={toggleList}>
          <Avatar
            id="usercontrols"
            src={`https://avatars2.githubusercontent.com/${login}?v=4&s=96`}
            alt="avatar"
          />
          {open ? <ChevronUp /> : <ChevronDown />}
        </Header>
        {open && (
          <List onClick={e => e.stopPropagation()}>
            <ListItem onClick={selectItem}>
              <Text>
                Logged in as <strong>{login}</strong>
              </Text>
            </ListItem>
            {!isDemo && (
              <>
                <ListItem onClick={() => selectItem(redirectToBilling)}>
                  <Text>Billing</Text>
                </ListItem>
                <ListItem as="a" href="https://graphql.linc.sh/logout">
                  <Text>Sign Out</Text>
                </ListItem>
                {admin && (
                  <ListItem onClick={redirectToAdminSite}>
                    <Text weight="bold" tone="warn">
                      Switch to Admin Mode
                    </Text>
                  </ListItem>
                )}
              </>
            )}
          </List>
        )}
      </Wrapper>
    )
  }
}
