import React from 'react'
import styled from 'styled-components'
import { flex, typography, spacing } from 'styles/traits'
import { GitBranch } from 'react-feather'

const Wrapper = styled.a`
  ${flex.center_both};
  ${typography.f6.medium.no_underline};
  color: ${({ theme }) => theme.text.default};

  svg {
    height: 1.2rem;
    /* margin-right: .5rem; */
    /* stroke: ${({ theme }) => theme.space.default}; */
  }

  &[href]:hover {
    color: ${({ theme }) => theme.space.default};
  }
  > span {
    ${spacing.pr025};
    position: relative;
    top: 1px;
    left: 1px;
    small {
      font-size: 0.9em;
    }
  }
`

const PullRequestBadge = ({ pullRequest }) => {
  return pullRequest ? (
    <Wrapper
      href={pullRequest.url}
      target="_blank"
      rel="noopener noreferrer"
      title="View latest Pull Request on branch"
    >
      <GitBranch />
      <span>
        <small>#</small>
        {pullRequest.number}
      </span>
    </Wrapper>
  ) : (
    <Wrapper as="div">
      <GitBranch />
    </Wrapper>
  )
}

export default PullRequestBadge
