import React from 'react'
import styled from 'styled-components'
import { GitHub, Settings } from 'react-feather'
import { Link } from 'react-router-dom'

import { flex, spacing, border } from 'styles/traits'
import { Shadow, hexa } from 'styles/globals'

import Tooltip from 'components/Tooltip'
import Stack from 'components/Stack'
import { Text } from 'components'

const Wrapper = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 10rem);
  grid-gap: 1rem;
`

const ProviderButton = styled(Link)`
  ${flex.center_both};
  ${border.ba.br2};
  ${spacing.pv1};
  border-color: ${({ theme }) => theme.bg.border};
  background: ${({ theme }) => theme.bg.default};
  cursor: pointer;
  transition: 200ms ease all;
  color: ${({ theme }) => theme.text.default};
  text-decoration: none;

  svg {
    height: 1rem;
    width: 1rem;
  }
  span {
    color: inherit;
  }

  > * + * {
    ${spacing.ml05};
  }
  &:active {
    transform: translateY(0.1rem);
    background: ${({ theme }) => theme.brand.wash};
    box-shadow: none;
  }
  &:hover {
    color: ${({ theme }) => theme.brand.default};
    border-color: ${({ theme }) => theme.brand.default};
  }
  box-shadow: ${Shadow.low} ${({ theme }) => hexa(theme.bg.reverse, 0.2)};
`

export default function Providers() {
  const tipProps = {
    arrow: true,
    placement: 'bottom',
    animation: 'fade',
    animateFill: false,
    duration: [250, 175],
    delay: [150, 0],
    distance: 8,
  }
  const ghText =
    'When you push to GitHub, we will build your commits and deploy the results.'
  const otherText =
    'Build your commits on aother CI platform, then upload the result to Linc.'
  return (
    <Stack gap={1}>
      <Text weight="medium">Select where your frontend code is hosted:</Text>
      <Wrapper>
        <Tooltip content={ghText} {...tipProps}>
          <ProviderButton to="/create/github">
            <GitHub />
            <Text>GitHub</Text>
          </ProviderButton>
        </Tooltip>
        <Tooltip content={otherText} {...tipProps}>
          <ProviderButton to="/create/other">
            <Settings />
            <Text>Other</Text>
          </ProviderButton>
        </Tooltip>
      </Wrapper>
    </Stack>
  )
}
