import React, { useState } from 'react'
import { StatusMessage, Text } from 'components'
import { useDocumentTitle } from 'hooks'
import { useQuery } from '@apollo/react-hooks'
import Inline from 'components/Inline'
import gql from 'graphql-tag'
import { Container, StyledCard } from './style'
import SiteItem from './SiteItem'
import { TextButton } from 'components/Buttons'
import Stripe from './Stripe'
import FullPageLoader from 'components/Loaders/FullPageLoader'
import SiteLogo from 'components/SiteLogo'
import NullState from 'components/NullState'
import Stack from 'components/Stack'

const GET_SITES = gql`
  {
    view {
      allSites {
        name
        repository_name
        created_at
        createdBy {
          name
        }
      }
    }
  }
`

const Billing = () => {
  useDocumentTitle('Billing | Linc')
  const [selectedSite, setSelectedSite] = useState(null)
  const { loading, error, data } = useQuery(GET_SITES)

  const deselectSite = () => setSelectedSite(null)

  if (loading) {
    return (
      <Stack padding={4}>
        <FullPageLoader />
      </Stack>
    )
  }

  if (error) {
    return (
      <Stack padding={4}>
        <StatusMessage>
          <Text>An error occured</Text>
        </StatusMessage>
      </Stack>
    )
  }

  const allSites = data?.view?.allSites
  if (allSites && allSites.length > 0) {
    if (selectedSite) {
      return (
        <Container>
          <Stack gap={2}>
            <Text weight="bold" scale={3}>
              Billing details
            </Text>
            <Stack style={{ minWidth: '32rem' }}>
              <Inline style={{ alignItems: 'center' }}>
                <SiteLogo name={selectedSite} />
                <Text scale={4}>
                  <strong>{selectedSite}</strong>
                </Text>
                <TextButton
                  style={{ alignSelf: 'flex-start' }}
                  onClick={deselectSite}
                >
                  Go back
                </TextButton>
              </Inline>
              <Stripe sitename={selectedSite} />
            </Stack>
          </Stack>
        </Container>
      )
    } else {
      return (
        <Container>
          <Stack gap={2}>
            <Text weight="bold" scale={3}>
              Billing details
            </Text>
            <StyledCard padding={2} borderRadius={0.25}>
              <Stack>
                <Text weight="bold" scale={4}>
                  Select site
                </Text>
                <Text scale={5}>
                  Setup payment for a site by selecting it from the list below:
                </Text>
                <Stack gap={0}>
                  {allSites.map(
                    ({
                      repository_name,
                      name: sitename,
                      created_at,
                      createdBy,
                    }) => (
                      <SiteItem
                        key={sitename}
                        sitename={sitename}
                        createdAt={created_at}
                        creator={createdBy.name}
                        repositoryName={repository_name}
                        select={() => setSelectedSite(sitename)}
                      />
                    )
                  )}
                </Stack>
              </Stack>
            </StyledCard>
          </Stack>
        </Container>
      )
    }
  } else {
    return (
      <Container>
        <Stack gap={2}>
          <Text weight="bold" scale={3}>
            Billing details
          </Text>
          <StyledCard padding={2} borderRadius={0.25}>
            <Stack>
              <Text weight="bold" scale={4}>
                Select site
              </Text>
              <Text scale={5}>
                Setup payment for a site by selecting it from the list below:
              </Text>
              <NullState>
                <Text tone="secondary">No sites found</Text>
              </NullState>
            </Stack>
          </StyledCard>
        </Stack>
      </Container>
    )
  }
}

export default Billing
