import { useState, useEffect } from 'react'

export const PATH_ERRORS = {
  MISSING_EXTENSION: 'missing_extension',
}

export default function usePackagePath(initialValue) {
  const [path, setPath] = useState(initialValue + 'package.json')
  const [isValid, setIsValid] = useState(true)
  const [errors, setErrors] = useState([])

  useEffect(() => {
    const newErrors = []
    if (!path.includes('/package.json')) {
      setIsValid(false)
      newErrors.push(PATH_ERRORS.MISSING_EXTENSION)
    }
    if (path.length > 0 && path.includes('/package.json')) {
      setIsValid(true)
    }
    setErrors(newErrors)
  }, [path])

  const legalPathName = () => {
    if (path.length > 0) {
      if (!path[0].includes('/')) {
        return '/' + path.replace('package.json', '')
      } else {
        return path.replace('package.json', '')
      }
    }
  }

  return {
    value: path,
    setPath,
    errors,
    isValid,
    legalPathName,
  }
}
