import styled, { css } from 'styled-components'

const HeaderCell = styled.div`
  height: 3.5rem;
  background: rgb(244, 247, 252);
  padding: 1rem;
  grid-area: ${({ gridArea }) => gridArea};

  border-style: solid;
  border-width: 0 0 0 1px;
  border-color: ${({ theme }) => theme.bg.border};
  display: flex;
  align-items: center;
  padding: 0 1rem;
  > * + * {
    margin-left: 0.5rem;
  }

  @media only screen and (max-width: 768px) {
    min-height: 3rem;
    display: ${({ gridArea }) => (gridArea !== 'branch' ? 'none' : 'flex')};
    ${({ gridArea }) =>
      gridArea === 'branch' &&
      css`
        grid-column-start: 1;
        grid-column-end: 4;
      `};
  }
`

export default HeaderCell
