import React from 'react'
import { Switch, Route, Redirect } from 'react-router-dom'

import PostInstall from 'views/PostInstall'
import Creator from 'views/Creator'
import AllSites from 'views/AllSites'
import Billing from 'views/Billing'
import Site from 'views/Site'

export default function Routes() {
  return (
    <Switch>
      <Route path="/create">
        <Creator />
      </Route>
      <Route path="/post_install">
        <PostInstall />
      </Route>
      <Route exact path="/billing">
        <Billing />
      </Route>
      <Route exact path="/sites">
        <AllSites />
      </Route>
      <Route path="/sites/:sitenameSlug">
        <Site />
      </Route>

      {/* REDIRECTS */}

      <Route exact path="/">
        <Redirect to="/sites" />
      </Route>
      <Route path="/new">
        <Redirect to="/create" />
      </Route>
      <Route path="/new/site">
        <Redirect to="/create" />
      </Route>
      <Route
        path="/site/:sitenameSlug"
        render={({ match }) => {
          const { sitenameSlug } = match.params
          return <Redirect to={`/sites/${sitenameSlug}`} />
        }}
      />
      <Route path="/add">
        <Redirect to="/sites" />
      </Route>
      <Route path="/add/site">
        <Redirect to="/sites" />
      </Route>
    </Switch>
  )
}
