export const ReleaseConfigs = {
  LAMBDA_EDGE: 'Lambda@Edge',
  CF_WORKERS: 'Cloudflare Workers',
}

export const DeployTargets = {
  lambda_edge: 'LAMBDA_EDGE',
  cloudflare: 'CF_WORKERS',
}

export function getDeploymentName(deploy_target_name) {
  return ReleaseConfigs[DeployTargets[deploy_target_name]]
}
