import React from 'react'
import styled from 'styled-components'
import { flex, spacing, border } from 'styles/traits'
import { Text } from 'components'
import { GitHub } from 'react-feather'
import { Shadow, Transition, hexa } from 'styles/globals'
import Stack from 'components/Stack'

const Wrapper = styled.div`
  background: ${({ theme }) => theme.bg.wash};
  ${spacing.p2};
  ${border.br3.ba};
  border-style: dashed;
  border-width: 0.13rem;
  border-color: ${({ theme }) => theme.bg.border};
`

const Button = styled.button`
  ${spacing.ph2.pv1};
  ${border.br2.ba};
  ${flex.center_both};
  background: ${({ theme }) => theme.bg.default};
  border-color: ${({ theme }) => theme.bg.border};
  cursor: pointer;
  box-shadow: ${props => `${Shadow.low} ${hexa(props.theme.bg.reverse, 0.15)}`};

  &:hover {
    transition: ${Transition.hover.on};
    box-shadow: ${props =>
      props.disabled
        ? 'none'
        : `${Shadow.high} ${hexa(props.theme.bg.reverse, 0.15)}`};
    opacity: ${props => (props.disabled ? '0.5' : '1')};
  }

  > svg {
    height: 1.2rem;
    width: 1.2rem;
    margin-right: 0.66rem;
  }
`

export default function NoRepos({ message, buttonText }) {
  const configureInstallation = () => {
    const TARGET = 'https://github.com/apps/linc/installations/new'
    const REDIRECT = encodeURIComponent(window.location.href + '/createsite')
    window.location = TARGET + '?redirect=' + REDIRECT
  }
  return (
    <Wrapper>
      <Stack>
        <Text scale={5} weight="bold" tone="secondary">
          {message}
        </Text>
        <Button onClick={configureInstallation}>
          <GitHub /> <Text weight="medium">{buttonText}</Text>
        </Button>
      </Stack>
    </Wrapper>
  )
}
