import {
  formatDistance,
  formatDistanceToNow,
  differenceInSeconds,
} from 'date-fns'

export const formatBuildTime = ({ started_at, finished_at }) => {
  if (started_at && (finished_at === null || finished_at === undefined)) {
    return formatDistanceToNow(parseInt(started_at))
  }
  if (started_at && finished_at) {
    return formatDistance(parseInt(finished_at), parseInt(started_at))
  } else {
    return 'Build duration unavailable'
  }
}

export const timeDifferenceInSeconds = (started_at, finished_at) => {
  return differenceInSeconds(finished_at, started_at)
}

export const dateToMonthDayYear = timestamp => {
  const dateObj = new Date(parseInt(timestamp))
  const locale = 'en-us'
  const month = dateObj.toLocaleString(locale, {
    month: 'long',
  })
  const day = dateObj.getUTCDate()
  const year = dateObj.getUTCFullYear()
  return `${month} ${day}, ${year}`
}
