import React from 'react'
import FeedbackCard from 'views/FeedbackCard'
import Divider from './Divider'
import Stack from 'components/Stack'

export default function Group({ group, branchTags }) {
  const { date, items } = group
  return (
    <Stack>
      <Divider date={date} />
      {items.map(item => (
        <FeedbackCard key={item.uuid} feedback={item} branchTags={branchTags} />
      ))}
    </Stack>
  )
}
