import React from 'react'
import ChannelsConfig from './components/ChannelsConfig'
import AddToSlack from './components/AddToSlack'

export default function SlackSettings({ sitename, siteAccess, settings }) {
  const { slack_app_is_installed } = settings
  if (slack_app_is_installed) {
    const { channels, selected_channel, messages_disabled } = settings
    if (channels) {
      return (
        <ChannelsConfig
          sitename={sitename}
          siteAccess={siteAccess}
          channels={channels}
          selected_channel={selected_channel}
          messages_disabled={messages_disabled}
        />
      )
    }
  } else {
    return <AddToSlack sitename={sitename} siteAccess={siteAccess} />
  }
}
