import React from 'react'
import styled from 'styled-components'
import { border } from 'styles/traits'

const Frame = styled.div`
  max-width: 640px;
  margin: 2rem auto;
  color: ${props => props.theme.generic.border};
  ${border.ba.br2};
  overflow: hidden;
  box-shadow: 0 12px 48px 0 rgba(0, 0, 0, 0.25);
  position: relative;

  > img:first-child {
    position: absolute;
    top: -1px;
    left: -1px;
    width: calc(100% + 2px);
    height: 4.68%;
  }
  > img:nth-child(2) {
    margin: -1px;
    width: calc(100% + 2px);
  }

  ${props => props.customCss};
`

const Screenshot = ({ image, alt, customCss }) => (
  <Frame customCss={customCss}>
    <img src={require('assets/screenshot_header.svg')} alt={alt} />
    <img src={image} alt="" />
  </Frame>
)

export default Screenshot
