import React from 'react'
import immutable from 'object-path-immutable'
import styled from 'styled-components'
import { X } from 'react-feather'
import { REMOVE_SITE_COLLABORATOR, GET_DATA } from '../graphql'
import { useMutation } from '@apollo/react-hooks'
import { flex, border, spacing } from 'styles/traits'
import Spinner from 'components/Spinner'

const SpinnerContainer = styled.div`
  ${flex.center_both};
  ${spacing.p05};
`

const RemoveButtonStyle = styled.div`
  ${flex.center_both};
  ${border.br2};
  ${spacing.p05};
  color: ${({ theme }) => theme.warn.default};
  cursor: pointer;
  svg {
    height: 1rem;
    width: 1rem;
  }

  &:hover {
    background: ${({ theme }) => theme.warn.wash};
  }
`

const updateCache = (cache, sitename, user_id) => {
  const cachedData = cache.readQuery({
    query: GET_DATA,
    variables: { name: sitename },
  })

  // pull out collaborator from cached data
  const { settings } = cachedData.view.site
  const { collaborators } = settings.access_control
  const targetIndex = collaborators.findIndex(item => item.user_id === user_id)

  const newCollaborators = [...collaborators]
  newCollaborators.splice(targetIndex, 1)

  // update cached data with modified collaborator
  const path = `view.site.settings.access_control.collaborators`
  const newData = immutable.set(cachedData, path, newCollaborators)

  // write updated cache data back to cache
  cache.writeQuery({
    query: GET_DATA,
    variables: { name: sitename },
    data: { ...newData },
  })
}

export default function RemoveCollabortor({ sitename, user_id }) {
  const [removeSiteCollaborator, { loading }] = useMutation(
    REMOVE_SITE_COLLABORATOR,
    {
      update: cache => updateCache(cache, sitename, user_id),
      variables: { sitename, user_id },
    }
  )
  return loading ? (
    <SpinnerContainer>
      <Spinner size={0.5} />
    </SpinnerContainer>
  ) : (
    <RemoveButtonStyle onClick={removeSiteCollaborator} disabled={loading}>
      <X />
    </RemoveButtonStyle>
  )
}
