const Messages = {
  noCommitsFound: ({ branch, sitename }) => {
    return `0 commits found for branch '${branch}' on site '${sitename}'`
  },
  noMatchesFound: ({ filter }) => {
    return `No matches found for '${filter}'`
  },
  noOlderCommitsFound: ({ branch, sitename }) => {
    return `No older commits found for branch '${branch}' on site '${sitename}'`
  },
}

export default Messages
