import React from 'react'
import styled, { css } from 'styled-components'
import { typography, flex, spacing } from 'styles/traits'
import { Link } from 'react-router-dom'
import Section from './Section'

const CategoryContainer = styled.li`
  ${flex.vertical}
  cursor: pointer;
`

const Title = styled(Link)`
  ${spacing.pv066.ph1};
  ${typography.regular};
  text-decoration: none;

  background: ${({ theme }) => theme.bg.default};
  color: ${({ theme }) => theme.text.secondary};

  &:hover {
    background: ${({ theme }) => theme.bg.wash};
    color: ${({ theme }) => theme.text.default};
  }

  ${props =>
    props.selected &&
    css`
      color: ${({ theme }) => theme.text.default};
      ${typography.bold};
      border-width: 0 0 0 2px;
      border-color: ${({ theme }) => theme.brand.default};
      border-style: solid;
    `}
`

const SectionsContainer = styled.ul`
  ${flex.vertical};
`

export default function Category({ category, to, selected, sections }) {
  return (
    <CategoryContainer key={category}>
      <Title to={to} selected={selected}>
        {category}
      </Title>
      {selected && (
        <SectionsContainer>
          {sections.map(({ title, hashlink }) => (
            <Section key={title} title={title} hashlink={hashlink} />
          ))}
        </SectionsContainer>
      )}
    </CategoryContainer>
  )
}
