import React, { useState } from 'react'
import styled from 'styled-components'

import BranchTag from 'components/BranchTag'
import Stack from 'components/Stack'
import { Text } from 'components'

import { itemsByDay, getBranches } from '../utils'
import Group from './Group'

const TagsContainer = styled.div`
  display: flex;
  flex-wrap: wrap;

  button {
    margin-right: 0.25rem;
    margin-bottom: 0.25rem;
  }
`

export default function FeedbackList(props) {
  const [branchTags, setBranchTags] = useState([])
  const clearSelectedTags = () => setBranchTags([])

  const handleClick = branch => {
    if (branchTags.includes(branch)) {
      const targetIndex = branchTags.indexOf(branch)
      const newTags = [...branchTags]
      newTags.splice(targetIndex, 1)
      setBranchTags(newTags)
    } else {
      const newTags = [...branchTags, branch]
      setBranchTags(newTags)
    }
  }

  const filtered = () => {
    const { allSiteFeedback } = props
    if (branchTags.length > 0) {
      const resultSet = allSiteFeedback.filter(feedback => {
        const { associated_branches } = feedback
        return associated_branches.some(item => branchTags.includes(item))
      })
      return resultSet
    } else {
      return allSiteFeedback
    }
  }

  const branches = getBranches(props.allSiteFeedback)
  const filteredItems = filtered()
  const filteredItemsByDay = itemsByDay(filteredItems)

  return (
    <Stack>
      <TagsContainer internalSpacing={0.5}>
        <BranchTag as="button" onClick={clearSelectedTags}>
          <Text tone="secondary" scale={6}>
            Show All
          </Text>
        </BranchTag>
        {branches.map(branch => (
          <BranchTag
            as="button"
            onClick={() => handleClick(branch)}
            key={branch}
            selected={branchTags.includes(branch)}
          >
            <Text tone="secondary" scale={6}>
              {branch}
            </Text>
          </BranchTag>
        ))}
      </TagsContainer>
      <Text tone="secondary" scale={6}>
        Showing {filteredItems.length}{' '}
        {filteredItems.length > 1 ? 'cards' : 'card'}
      </Text>
      <Stack gap={2}>
        {filteredItemsByDay.map(group => (
          <Group key={group.date} group={group} branchTags={branchTags} />
        ))}
      </Stack>
    </Stack>
  )
}
