import React from 'react'
import { Text, StatusMessage } from 'components'
import Stack from 'components/Stack'
import { Link } from 'react-router-dom'
import { setSiteFlag } from 'stores/flags'
import styled from 'styled-components'
import { border, spacing, flex } from 'styles/traits'
import { X } from 'react-feather'

const DismissButton = styled.button`
  ${flex.center_both};
  ${spacing.p1};
  ${border.bn.br2};
  background: none;
  color: ${({ theme }) => theme.bg.hairline};
  cursor: pointer;
  &:hover {
    background: ${({ theme }) => theme.bg.wash};
  }
  svg {
    height: 1.5rem;
    width: 1.5rem;
  }
`

const Inner = styled.div`
  ${flex.justify_space_btw};
`

export default function DeploymentConfigPrompt({ sitename, releaseBranch }) {
  const dismissPrompt = () => {
    setSiteFlag(sitename, 'showDeploymentConfigPrompt', false)
  }
  return (
    <StatusMessage>
      <Inner>
        <Stack gap={0.5}>
          <Text.P>
            You’ve got a successful build on your release branch{' '}
            <code>{releaseBranch}</code>, but your app is not configured for
            automatic releases
          </Text.P>
          <Text.P>
            <a
              href="https://linc.sh/knowledge-base/releasing"
              target="_blank"
              rel="noopener noreferrer"
            >
              Read more
            </a>{' '}
            about enabling Continuous Delivery or head to{' '}
            <Link to={`/sites/${sitename}/settings#deploy_config`}>
              deployment configuration
            </Link>{' '}
            to set things up
          </Text.P>
        </Stack>
        <DismissButton onClick={dismissPrompt}>
          <X />
        </DismissButton>
      </Inner>
    </StatusMessage>
  )
}
