import React from 'react'
import { Info } from 'react-feather'
import styled from 'styled-components'

import { flex } from 'styles/traits'

import { Text, TextField, StatusMessage } from 'components'
import { FlexRow } from 'components/FlexSections'
import Tooltip from 'components/Tooltip'
import Stack from 'components/Stack'

const CenteredFlexRow = styled(FlexRow)`
  ${flex.align_center};
  svg {
    margin-bottom: -3px;
    height: 1rem;
    width: 1rem;
  }
`

const CompactStatusMessage = styled(StatusMessage)`
  padding: 1rem;
`

const DuplicateName = ({ value }) => (
  <CompactStatusMessage status="critical">
    <Text scale={6}>
      Site name <code>{value}</code> unavailable
    </Text>
  </CompactStatusMessage>
)

const NameReformated = ({ value }) => (
  <CompactStatusMessage status="warning">
    <Text scale={6}>
      Your new site will be created as <code>{value}</code>
    </Text>
  </CompactStatusMessage>
)

const Messages = ({ sitename }) => {
  const { value, legalValue, loading, unique } = sitename
  const messages = []
  if (value.length > 0) {
    if (value !== legalValue) {
      messages.push(<NameReformated value={legalValue} />)
    }
    if (loading === false && unique === false) {
      messages.push(<DuplicateName value={legalValue} />)
    }
  }
  return messages
}

const LinkExamples = ({ sitenameSlug }) => (
  <Stack gap={0.5}>
    <Text scale={6} tone="secondary">
      https://<strong>{sitenameSlug}</strong>-xyz-production.linc-preview.sh
    </Text>
    <Text scale={6} tone="secondary">
      https://<strong>{sitenameSlug}</strong>-production.release.linc-preview.sh
    </Text>
    <Text scale={6} tone="secondary">
      https://<strong>{sitenameSlug}</strong>.linc-trial.sh
    </Text>
  </Stack>
)

export default function SiteField({ sitename, initialValue }) {
  const handleChange = e => sitename.setValue(e.target.value)
  return (
    <Stack gap={0.5}>
      <CenteredFlexRow internalSpacing={0.5}>
        <Text weight="bold">Site name</Text>
        <Tooltip
          content="This name will be used to generate preview URLs, so choose something
            short and unique"
          arrow={true}
          placement="top"
          animation="scale"
          animateFill={false}
          duration={[250, 175]}
          delay={[150, 0]}
          distance={8}
        >
          <div>
            <Info />
          </div>
        </Tooltip>
      </CenteredFlexRow>
      <TextField
        inputProps={{
          onChange: handleChange,
          defaultValue: initialValue,
          required: true,
          maxLength: 20,
          minLength: 2,
          type: 'text',
        }}
        status={sitename.status}
      />
      <Messages sitename={sitename} />
      {sitename.legalValue && sitename.legalValue.length > 0 && (
        <LinkExamples sitenameSlug={sitename.legalValue} />
      )}
    </Stack>
  )
}
