import styled, { css } from 'styled-components'
import { typography, media } from 'styles/traits'
import PropTypes from 'prop-types'

const visualBounding = css`
  display: block;
  margin-top: -0.17em;
  margin-bottom: -0.18em;
`

const Text = styled.span`
  ${visualBounding};

  font-variant: tabular-nums slashed-zero;
  font-feature-settings: 'calt', 'ss01', 'liga';

  color: ${({ theme, tone }) => theme.text[tone]};
  line-height: ${({ lineHeight }) => lineHeight};

  ${({ scale }) => typography[`f${scale}`]};
  ${({ weight }) => typography[weight]};
  ${({ fontStyle }) => typography[fontStyle]};

  ${({ truncate }) =>
    truncate &&
    css`
      white-space: nowrap;
      overflow-x: hidden;
      text-overflow: ellipsis;
    `}

  code {
    ${typography.mono.bold.normal};
  }
  strong {
    ${typography.bold.normal};
  }
  a {
    text-decoration: none;
    color: ${({ theme }) => theme.space.default};
    &:hover {
      text-decoration: underline;
    }
  }

  ${props =>
    props['hide-below-desktop'] &&
    css`
      ${media.tablet} {
        display: none;
      }
    `};
`

Text.propTypes = {
  scale: PropTypes.oneOf([1, 2, 3, 4, 5, 6]),
  weight: PropTypes.oneOf(['regular', 'medium', 'bold', 'black']),
  fontStyle: PropTypes.oneOf(['normal', 'italic']),
  lineHeight: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  tone: PropTypes.oneOf([
    'default',
    'alt',
    'reverse',
    'placeholder',
    'secondary',
    'warn',
  ]),
  children: PropTypes.node,
}

Text.defaultProps = {
  scale: 5,
  weight: 'regular',
  fontStyle: 'normal',
  lineHeight: 1.1,
  tone: 'default',
}

export default Text

Text.P = styled(Text.withComponent('p'))`
  ${typography.lh16};
  ${({ lineHeight }) => lineHeight && `line-height: ${lineHeight};`}
`
