import React from 'react'
import PropTypes from 'prop-types'

import { Text } from 'components'
import Inline from 'components/Inline'
import Stack from 'components/Stack'

import HashAnchor from './HashAnchor'
import HashLink from './HashLink'

function Block({ blockId, blockTitle, blockSubtitle, blockForm }) {
  return (
    <Stack>
      {blockTitle && (
        <Inline>
          <Text weight="bold" scale={3}>
            {blockTitle}
          </Text>
          {blockId && (
            <>
              <HashLink href={`#${blockId}`} />
              <HashAnchor id={blockId} />
            </>
          )}
        </Inline>
      )}
      {blockSubtitle && (
        <Text.P scale={6} tone="secondary">
          {blockSubtitle}
        </Text.P>
      )}
      {blockForm}
    </Stack>
  )
}

Block.propTypes = {
  blockId: PropTypes.string,
  blockTitle: PropTypes.string,
  blockSubtitle: PropTypes.any,
  blockForm: PropTypes.node.isRequired,
}

export default Block
