import React from 'react'
import PropTypes from 'prop-types'
import StatusIcon from 'components/StatusIcon'
import {
  Container,
  CustomIconStyle,
  InnerContainer,
  MaskToggle,
  Message,
  StyledInput,
} from './style'
import { EyeOff, Eye } from 'react-feather'

export default class TextField extends React.Component {
  state = { revealed: false }

  toggleMask = () => this.setState({ revealed: !this.state.revealed })

  render() {
    const {
      valid,
      canBeRevealed,
      masked,
      message,
      status,
      inputProps,
      size,
    } = this.props
    const { revealed } = this.state
    const redactedProps = {
      ...inputProps,
      type: 'password',
      value: '••••••••••',
    }

    return (
      <Container size={size}>
        <InnerContainer>
          <StyledInput
            valid={valid}
            {...(masked && !revealed ? redactedProps : inputProps)}
          />
          {status && status !== 'none' && (
            <StatusIcon customCss={CustomIconStyle} status={status} />
          )}
          {masked && canBeRevealed && (
            <MaskToggle type="button" onClick={this.toggleMask}>
              {revealed ? <EyeOff /> : <Eye />}
            </MaskToggle>
          )}
        </InnerContainer>
        {message && <Message status={status}>{message}</Message>}
      </Container>
    )
  }
}

TextField.propTypes = {
  valid: PropTypes.bool,
  message: PropTypes.oneOfType([PropTypes.object, PropTypes.string]),
  status: PropTypes.oneOf([
    'none',
    'positive',
    'critical',
    'warning',
    'info',
    'loading',
  ]),
  inputProps: PropTypes.object,
  masked: PropTypes.bool,
  canBeRevealed: PropTypes.bool,
}
