import React from 'react'
import styled from 'styled-components'
import PropTypes from 'prop-types'

const Switch = styled.label`
  position: relative;
  display: inline-block;
  width: 4rem;
  height: 2rem;

  input {
    display: none;
  }
`

const Slider = styled.span`
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 2rem;
  background-color: ${({ theme }) => theme.bg.inactive};
  transition: 300ms transform ease, 300ms background ease;

  &::before {
    position: absolute;
    content: '';
    height: 1.5rem;
    width: 1.5rem;
    left: 4px;
    bottom: 4px;
    border-radius: 50%;
    background-color: ${({ theme }) => theme.bg.default};
    transition: 300ms transform ease, 300ms background ease;
  }
`

const StyledInput = styled.input`
  &:checked + ${Slider} {
    background-color: ${({ theme }) => theme.success.default};
    box-shadow: inset 0 2px 6px 0 hsla(0, 0%, 0%, 0.2);
  }

  &:checked[type='checkbox']:disabled + ${Slider} {
    cursor: not-allowed;
  }

  &:checked[type='checkbox']:disabled + ${Slider} {
    box-shadow: inset 0 2px 6px 0 hsla(0, 0%, 0%, 0.05);
    background-color: ${({ theme }) => theme.success.border};
  }

  &:focus + ${Slider} {
    box-shadow: inset 0 2px 6px 0 hsla(0, 0%, 0%, 0.2);
    box-shadow: 0 0 1px ${({ theme }) => theme.space.default};
  }

  &:checked + ${Slider}:before {
    -webkit-transform: translateX(2rem);
    -ms-transform: translateX(2rem);
    transform: translateX(2rem);
  }
`

const Toggle = ({ inputProps }) => {
  return (
    <Switch>
      <StyledInput type="checkbox" {...inputProps} />
      <Slider />
    </Switch>
  )
}

Toggle.propTypes = {
  inputProps: PropTypes.object,
}

export default Toggle
