import React from 'react'
import styled from 'styled-components'
import { Text } from 'components'
import { flex, spacing, border, typography } from 'styles/traits'

import AccessOptions from './AccessOptions'
import RemoveCollabortor from './RemoveCollaborator'

const Wrapper = styled.div`
  ${typography.f6};
  display: grid;
  grid-template-columns: 10rem auto;
  ${spacing.p05};
  background: ${({ theme }) => theme.bg.default};
  &:nth-child(even) {
    background: ${({ theme }) => theme.bg.wash};
  }
`

const Left = styled.div`
  ${flex.align_center};
  > img {
    ${border.br2};
    height: 2rem;
    ${spacing.mr05};
  }

  > span {
    white-space: nowrap;
    overflow: hidden;
  }
`

const Right = styled.div`
  display: flex;
  align-content: center;
  justify-content: flex-end;
  > * + * {
    ${spacing.ml05};
  }
`

export default function Collaborator({ sitename, collaborator, isDisabled }) {
  const {
    login,
    user_id,
    avatar_url,
    access_level,
    is_current_user,
  } = collaborator
  return (
    <Wrapper foo={is_current_user}>
      <Left>
        <img alt="" src={avatar_url} />
        <Text scale={6}>{login}</Text>
      </Left>
      <Right>
        <AccessOptions
          isDisabled={isDisabled}
          access_level={access_level}
          sitename={sitename}
          user_id={user_id}
        />
        {!isDisabled && (
          <RemoveCollabortor sitename={sitename} user_id={user_id} />
        )}
      </Right>
    </Wrapper>
  )
}
