import React, { Fragment } from 'react'
import { useQuery } from '@apollo/react-hooks'

import { useDocumentTitle } from 'hooks'

import { GET_DATA } from './graphql'

import BuildSettings from './Sections/BuildSettings'
import BuildVariables from './Sections/BuildVariables'
import ExternalCI from './Sections/ExternalCI'

import Placeholder from '../Placeholder'
import Block from '../Block'
import { StatusMessage, Text } from 'components'

const ExternalCISubtitle = () => (
  <Fragment>
    You can use an external CI service to compile FABs then upload them to Linc!
    To learn more, checkout our{' '}
    <a
      href="https://linc.sh/docs/using-external-ci-with-linc"
      rel="noopener noreferrer"
      target="_blank"
    >
      Using external CI with Linc
    </a>{' '}
    docs.
  </Fragment>
)

export default function Build({ sitename }) {
  useDocumentTitle(`Build settings`)

  const { loading, error, data } = useQuery(GET_DATA, {
    variables: { name: sitename },
    errorPolicy: 'all',
  })

  if (loading) {
    return <Placeholder />
  }
  if (error) {
    return (
      <StatusMessage status="critical">
        <Text>An error occured while retrieving settings data</Text>
      </StatusMessage>
    )
  }

  const site = data?.view?.site
  if (site) {
    const { general_settings, build_config, external_ci_settings } =
      data?.view?.site?.settings || {}
    const siteAccess = site?.access
    return (
      <Fragment>
        <Block
          blockId="build-settings"
          blockTitle="General build config"
          blockSubtitle="General build settings for your site"
          blockForm={
            <BuildSettings
              sitename={sitename}
              siteAccess={siteAccess}
              settings={general_settings}
            />
          }
        />
        <Block
          blockId="environment-variables"
          blockTitle="Environment"
          blockSubtitle="Build environment config"
          blockForm={
            <BuildVariables
              sitename={sitename}
              siteAccess={siteAccess}
              variables={build_config.variables || []}
            />
          }
        />
        <Block
          blockId="external-ci"
          blockTitle="External Ci"
          blockSubtitle={<ExternalCISubtitle />}
          blockForm={
            <ExternalCI
              sitename={sitename}
              siteAccess={siteAccess}
              settings={external_ci_settings}
            />
          }
        />
      </Fragment>
    )
  } else {
    return (
      <StatusMessage status="critical">
        <Text>Error: unable to retrieve site settings data</Text>
      </StatusMessage>
    )
  }
}
