import React from 'react'
import { useLocation } from 'react-router'
import { Wrapper } from './style'
import Category from './Category'

export default function Menu({ categories, basePath }) {
  const { pathname } = useLocation()
  return (
    <Wrapper>
      {categories.map(item => {
        const { category, path, sections } = item
        const categoryRoutePath = `${basePath}${path}`
        const selected = pathname === categoryRoutePath
        return (
          <Category
            key={category}
            category={category}
            sections={sections}
            selected={selected}
            to={categoryRoutePath}
          />
        )
      })}
    </Wrapper>
  )
}
