import React from 'react'
import styled from 'styled-components'
import { border } from 'styles/traits'
import { Text } from 'components'
import Stack from 'components/Stack'

const Wrapper = styled(Stack)`
  ${border.br2};
  background: ${({ theme }) => theme.bg.wash};
`

export default function MetaData(props) {
  const {
    parsedUserAgent,
    height,
    width,
    innerHeight,
    innerWidth,
    scrollX,
    scrollY,
  } = props.metaData
  return (
    <Wrapper padding={1} gap={0.5}>
      {parsedUserAgent && (
        <Text tone="secondary" scale={6}>
          <code>userAgent: {parsedUserAgent}</code>
        </Text>
      )}
      {height && (
        <Text tone="secondary" scale={6}>
          <code>height: {height}</code>
        </Text>
      )}
      {width && (
        <Text tone="secondary" scale={6}>
          <code>width: {width}</code>
        </Text>
      )}
      {innerHeight && (
        <Text tone="secondary" scale={6}>
          <code>innerHeight: {innerHeight}</code>
        </Text>
      )}
      {innerWidth && (
        <Text tone="secondary" scale={6}>
          <code>innerWidth: {innerWidth}</code>
        </Text>
      )}
      {scrollX && (
        <Text tone="secondary" scale={6}>
          <code>scrollX: {scrollX}</code>
        </Text>
      )}
      {scrollY && (
        <Text tone="secondary" scale={6}>
          <code>scrollY: {scrollY}</code>
        </Text>
      )}
    </Wrapper>
  )
}
