import React, { Fragment } from 'react'
import { Text } from 'components'
import { flex, spacing } from 'styles/traits'
import styled, { css, keyframes } from 'styled-components'
import {
  X,
  Minus,
  Clock,
  Check,
  Loader,
  ChevronUp,
  ChevronDown,
} from 'react-feather'
import { Transition } from 'styles/globals'

const animationKeyFrames = keyframes`
  0% {transform: rotate(0);}
  100% {transform: rotate(360deg);}
`

const animation = css`
  animation-name: ${animationKeyFrames};
  animation-duration: 2000ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  transform-origin: 12px 12px;
`

const statusIcons = {
  null: <Minus />,
  undefined: <Minus />,
  queued: <Clock />,
  pending: <Loader />,
  running: <Loader />,
  success: <Check />,
  failed: <X />,
}

export const Container = styled.header`
  cursor: pointer;
  overflow-x: hidden;
  ${flex.justify_space_btw};
  ${spacing.p1};
  border-radius: 0.25rem 0.25rem 0 0;
  span {
    color: ${({ theme }) => theme.text.reverse};
  }
  background: hsl(221, 15%, 39%);
  ${props =>
    props.status === 'success' &&
    css`
      background: ${({ theme }) => theme.success.default};
    `};
  ${props =>
    (props.status === 'pending' || props.status === 'running') &&
    css`
      background: ${({ theme }) => theme.special.default};
    `};
  ${props =>
    props.status === 'failed' &&
    css`
      background: ${({ theme }) => theme.warn.default};
    `};

  transition: ${Transition.hover.off};

  > * + * {
    ${spacing.ml1};
  }
`

export const Content = styled.div`
  ${flex.align_center};
  > * + * {
    ${spacing.ml05};
  }
`

const IconContainer = styled.div`
  ${({ status }) => status === 'pending' && animation};
  ${({ status }) => status === 'running' && animation};
  svg {
    color: ${({ theme }) => theme.text.reverse};
  }
`

const CommitMessage = styled(Text)`
  white-space: nowrap;
  min-width: 0;
  max-width: 30rem;
  overflow: hidden;
  text-overflow: ellipsis;
`

export const ToggleButton = styled.button`
  background: none;
  border: none;
  cursor: pointer;
`

const Header = ({
  status,
  commit_sha,
  message,
  author,
  logVisible,
  toggleLogVisibility,
}) => {
  const username = author?.username
  return (
    <Container status={status} onClick={toggleLogVisibility}>
      <Content>
        <IconContainer status={status}>{statusIcons[status]}</IconContainer>
        {status && (
          <Fragment>
            <Text weight="bold"> - </Text>
            <Text weight="bold">{status}</Text>
          </Fragment>
        )}
        {commit_sha && (
          <Fragment>
            <Text weight="bold"> - </Text>
            <Text>{commit_sha.slice(0, 9)}</Text>
          </Fragment>
        )}
      </Content>
      <Content>
        {message && (
          <CommitMessage weight="medium">{message.slice(0, 50)}</CommitMessage>
        )}
        {username && (
          <Fragment>
            <Text weight="bold"> ~ </Text>
            <Text>{username}</Text>
          </Fragment>
        )}
        <ToggleButton>
          <IconContainer>
            {logVisible === true && <ChevronUp />}
            {logVisible === false && <ChevronDown />}
          </IconContainer>
        </ToggleButton>
      </Content>
    </Container>
  )
}

export default Header
