import React from 'react'
import { useParams } from 'react-router'
import { useQuery } from '@apollo/react-hooks'

import { GET_ENVIRONMENTS } from 'graphql/queries'
import { activeReleasePreviewLink } from 'helpers/previewLinks'
import getEnvColor from 'helpers/envColors'

import { useDocumentTitle } from 'hooks'

import { StatusMessage, Text } from 'components'
import Inline from 'components/Inline'
import Stack from 'components/Stack'
import ColorDot from 'components/ColorDot'
import { SiteAccessError } from 'components/Errors'
import FullPageLoader from 'components/Loaders/FullPageLoader'

import NewEnvironment from './components/NewEnvironment'
import EnvironmentForm from './components/EnvironmentForm'

import { Container, EnvLink } from './style'

export default function Environments() {
  const { sitenameSlug } = useParams()
  useDocumentTitle(`Environments | ${sitenameSlug}`)
  const { loading, error, data } = useQuery(GET_ENVIRONMENTS, {
    variables: { name: sitenameSlug },
  })

  if (loading) {
    return <FullPageLoader />
  }

  if (error) {
    return (
      <Container gap={2}>
        <Text scale={3} weight="bold">
          Environments
        </Text>
        <SiteAccessError sitename={sitenameSlug} item="environments" />
      </Container>
    )
  }

  if (data && data.view && data.view.site) {
    const { environments } = data.view.site
    const siteAccess = data.view.site.access
    const { write } = siteAccess
    return (
      <Container gap={2}>
        <Text scale={3} weight="bold">
          Environments
        </Text>
        <Stack gap={2}>
          {environments.length - 1 === 0 && (
            <StatusMessage>
              <Stack>
                <Text.P>
                  Welcome to the Environments page for{' '}
                  <strong>{sitenameSlug}</strong>. Here you can define one or
                  more Environments.
                </Text.P>
                <Text.P>
                  An Environment is a set of custom variables that can be
                  injected into your code at runtime. These variables may be
                  used to override settings defined in your{' '}
                  <code>production-settings.json</code> file located in the same
                  directory as your <code>package.json</code>.
                </Text.P>
                <Text.P>
                  You can run any version of your code in any environment
                  defined here.
                </Text.P>
                <Text.P>
                  See the{' '}
                  <a href="https://linc.sh/docs/environments">
                    environments documentation
                  </a>{' '}
                  to learn more.
                </Text.P>
              </Stack>
            </StatusMessage>
          )}
          <StatusMessage>
            <Stack>
              <Text.P weight="medium">
                The latest release from your <code>master</code> branch is
                available in each environment using these URL aliases:
              </Text.P>
              {environments
                .slice(1, environments.length)
                .map(({ env_name }, index) => {
                  const color = getEnvColor(env_name, index + 1)
                  const deploymentLink = activeReleasePreviewLink({
                    sitenameSlug,
                    environmentSlug: env_name,
                  })
                  return (
                    <EnvLink href={deploymentLink} key={env_name}>
                      <ColorDot color={color} />
                      <Text>
                        https://{sitenameSlug}--<strong>{env_name}</strong>
                        .release.linc-preview.sh
                      </Text>
                    </EnvLink>
                  )
                })}
              <EnvLink
                href={activeReleasePreviewLink({
                  sitenameSlug,
                  environmentSlug: 'production',
                })}
              >
                <ColorDot color={getEnvColor('production', 0)} />
                <Text>
                  https://{sitenameSlug}--<strong>production</strong>
                  .release.linc-preview.sh
                </Text>
              </EnvLink>
              <Text.P tone="secondary">
                <strong>Note:</strong> Changes to your environments do not
                require you to rebuild your code.
              </Text.P>
            </Stack>
          </StatusMessage>
          {environments
            .slice(1, environments.length)
            .map((environment, index) => (
              <EnvironmentForm
                siteAccess={siteAccess}
                color={getEnvColor(environment.env_name, index + 1)}
                environment={environment}
                key={environment.env_name}
              />
            ))}
          <StatusMessage>
            <Stack>
              <Inline style={{ alignItems: 'center' }} internalSpacing={0.5}>
                <ColorDot color={getEnvColor('production', 0)} />
                <Text weight="medium" tone="secondary" scale={4}>
                  PRODUCTION
                </Text>
              </Inline>
              <Text.P tone="secondary">
                You can configure your production environment variables in the{' '}
                <code>production-settings.json</code> file located in your
                repository
              </Text.P>
            </Stack>
          </StatusMessage>
        </Stack>
        {write ? (
          <NewEnvironment sitename={sitenameSlug} environments={environments} />
        ) : (
          <Text.P fontStyle="italic" tone="notice">
            To add or edit environments, you must have <strong>write</strong>{' '}
            permissions on the repository.
          </Text.P>
        )}
      </Container>
    )
  } else {
    return (
      <Container gap={2}>
        <Text scale={3} weight="bold">
          Environments
        </Text>
        <SiteAccessError sitename={sitenameSlug} item="environments" />
      </Container>
    )
  }
}
