import React from 'react'
import { Route, useParams, Redirect } from 'react-router-dom'
import Menu from 'components/Menu'
import routesTable from './RoutesTable'
import { Wrapper, FormsContainer } from './style'

import Build from './Build'
import Deploy from './Deploy'
import Integrations from './Integrations'
import Feedback from './Feedback'
import AccessControl from "./AccessControl"

export default function SiteSettings() {
  const { sitenameSlug } = useParams()
  const basePath = `/sites/${sitenameSlug}/settings`
  return (
    <Wrapper>
      <Route path={basePath}>
        <Menu categories={routesTable} basePath={basePath} />
      </Route>
      <FormsContainer>
        <Route exact path={basePath + '/'}>
          <Redirect to={basePath + '/build'} />
        </Route>
        <Route path={basePath + '/build'}>
          <Build sitename={sitenameSlug} />
        </Route>
        <Route path={basePath + '/deploy'}>
          <Deploy sitename={sitenameSlug} />
        </Route>
        <Route path={basePath + '/integrations'}>
          <Integrations sitename={sitenameSlug} />
        </Route>
        <Route path={basePath + '/feedback'}>
          <Feedback sitename={sitenameSlug} />
        </Route>
        <Route path={basePath + '/access'}>
          <AccessControl sitename={sitenameSlug} />
        </Route>
      </FormsContainer>
    </Wrapper>
  )
}
