import React from 'react'
import { GET_DATA } from './graphql'
import { useQuery } from '@apollo/react-hooks'
import { useDocumentTitle } from 'hooks'
import GitHubSettings from './GitHubSettings'
import SlackSettings from './SlackSettings'
import BugHerdSettings from './BugHerdSettings'

import { StatusMessage, Text } from 'components'
import Placeholder from '../Placeholder'
import Block from '../Block'

export default function Integrations({ sitename }) {
  useDocumentTitle(`Integrations`)

  const { loading, error, data } = useQuery(GET_DATA, {
    variables: { name: sitename },
    errorPolicy: 'all',
  })

  if (loading) {
    return <Placeholder />
  }

  if (error) {
    return (
      <StatusMessage status="critical">
        <Text>An error occured while retrieving settings data</Text>
      </StatusMessage>
    )
  }

  const site = data?.view?.site
  if (site) {
    const siteAccess = site?.access
    const { github_settings, slack_settings, bugherd_settings } = site?.settings || {}
    return (
      <>
        <Block
          blockId="github"
          blockTitle="GitHub"
          blockSubtitle="Configure GitHub Integration"
          blockForm={
            <GitHubSettings
              sitename={sitename}
              siteAccess={siteAccess}
              settings={github_settings}
            />
          }
        />
        <Block
          blockId="slack"
          blockTitle="Slack"
          blockSubtitle="Send preview URLs + build status info to a Slack channel"
          blockForm={
            <SlackSettings
              sitename={sitename}
              siteAccess={siteAccess}
              settings={slack_settings}
            />
          }
        />
        <Block
          blockId="bugherd"
          blockTitle="BugHerd"
          blockSubtitle="Linc + BugHerd integration"
          blockForm={
            <BugHerdSettings
              sitename={sitename}
              siteAccess={siteAccess}
              settings={bugherd_settings}
            />
          }
        />
      </>
    )
  } else {
    return (
      <StatusMessage status="critical">
        <Text>Error: unable to retrieve site settings data</Text>
      </StatusMessage>
    )
  }
}
