import React from 'react'
import { Text, SiteLogo, Spinner } from 'components'
import { useMutation } from '@apollo/react-hooks'
import { ADD_SITE_TO_USER, REMOVE_SITE_FROM_USER } from 'graphql/mutations'
import {
  ViewButton,
  Header,
  RepoLink,
  Row,
  CardTitle,
  StarButton,
  CenteredFlexRow,
} from './style'
import relativeDate from 'relative-date'
import { Star } from 'react-feather'
import { TextPlaceholder, ImagePlaceholder } from 'components/Placeholders'
import Stack from 'components/Stack'
import { addRemoveSiteCacheUpdate } from 'helpers/updateQueryMethods'
import Tooltip from 'components/Tooltip'
import Card from 'components/Card'

const ACTIONS = {
  ADD: 'add',
  REMOVE: 'remove',
}

const AddRemoveSite = ({ sitename, starred }) => {
  const ACTION = starred ? ACTIONS.REMOVE : ACTIONS.ADD
  const mutation = starred ? REMOVE_SITE_FROM_USER : ADD_SITE_TO_USER
  const [mutate, { loading, error }] = useMutation(mutation, {
    update: cache => addRemoveSiteCacheUpdate(cache, sitename, ACTION),
    variables: { name: sitename },
  })

  if (error) {
    console.log(error)
  }

  return (
    <StarButton starred={starred} onClick={mutate}>
      {loading ? <Spinner /> : <Star />}
    </StarButton>
  )
}

const SiteCard = ({
  loading,
  starred,
  sitename,
  siteCreated,
  lastCommit,
  repositoryName,
}) => {
  if (loading) {
    return (
      <Card boxShadow="none">
        <Stack gap={1}>
          <Header as="div">
            <CenteredFlexRow>
              <ImagePlaceholder />
              <TextPlaceholder />
            </CenteredFlexRow>
          </Header>
          <Stack gap={2}>
            <RepoLink as="div">
              <TextPlaceholder maxWidth={9} />
            </RepoLink>
            <Stack gap={0.5}>
              <Row>
                <TextPlaceholder maxWidth={4} />
                <TextPlaceholder maxWidth={4} />
              </Row>
              <Row>
                <TextPlaceholder maxWidth={4} />
                <TextPlaceholder maxWidth={4} />
              </Row>
            </Stack>
          </Stack>
          <ViewButton to="" as="div" />
        </Stack>
      </Card>
    )
  } else {
    const tooltipText = `${
      starred ? 'Remove' : 'Add'
    } ${sitename} to Sites list`
    return (
      <Card boxShadow="none" borderRadius={0.25}>
        <Stack gap={1}>
          <Header>
            <CardTitle to={`/sites/${sitename}`}>
              <SiteLogo name={sitename} />
              <Text scale={4} weight="bold">
                {sitename}
              </Text>
            </CardTitle>
            <Tooltip
              content={tooltipText}
              arrow={true}
              placement="top"
              animation="scale"
              animateFill={false}
              duration={[250, 175]}
              delay={[150, 0]}
              distance={8}
            >
              <div>
                <AddRemoveSite sitename={sitename} starred={starred} />
              </div>
            </Tooltip>
          </Header>
          <RepoLink
            href={`https://github.com/${repositoryName}`}
            target="_blank"
            rel="noopener noreferrer"
          >
            <Text scale={6}>{repositoryName}</Text>
          </RepoLink>
          <Stack gap={0.5}>
            <Row>
              <Text tone="secondary" scale={6}>
                Last Commit
              </Text>
              <Text tone="secondary" scale={6}>
                {lastCommit ? relativeDate(lastCommit) : 'No commits'}
              </Text>
            </Row>
            <Row>
              <Text tone="secondary" scale={6}>
                Site created
              </Text>
              <Text tone="secondary" scale={6}>
                {siteCreated ? relativeDate(siteCreated) : 'Unknown'}
              </Text>
            </Row>
          </Stack>
          <ViewButton to={`/sites/${sitename}`}>View</ViewButton>
        </Stack>
      </Card>
    )
  }
}

export default SiteCard
