import { useState, useEffect } from 'react'

export const useEnvironmentSettings = initialState => {
  const [settings, setSettings] = useState(initialState)
  const [isValid, setIsValid] = useState(false)

  const addSetting = () => {
    const newSetting = { name: '', value: '' }
    setSettings([...settings, newSetting])
  }

  const deleteSetting = index => {
    const newSettings = [].concat(settings)
    newSettings.splice(index, 1)
    setSettings(newSettings)
  }

  const setSettingName = (val, index) => {
    const newSettings = [].concat(settings)
    const legalName = val
      .replace(/[^a-zA-Z0-9\\_\-\s]/g, '')
      .replace(/\s+/g, '_')
    newSettings[index].name = legalName
    setSettings(newSettings)
  }

  const setSettingValue = (val, index) => {
    const newSettings = [].concat(settings)
    newSettings[index].value = val
    setSettings(newSettings)
  }

  const reset = () => {
    setSettings(initialState)
  }

  const getAll = () => {
    // strips __typename
    return settings.map(setting => ({
      name: setting.name,
      value: setting.value,
    }))
  }

  useEffect(() => {
    if (
      settings.every(({ name, value }) => name.length > 0 && value.length > 0)
    ) {
      setIsValid(true)
    } else {
      setIsValid(false)
    }
  }, [settings])

  return {
    add: addSetting,
    reset,
    delete: deleteSetting,
    getAll,
    setSettingName: setSettingName,
    setSettingValue: setSettingValue,
    isValid,
  }
}
