import React from 'react'
import styled from 'styled-components'
import { format, isToday, isYesterday } from 'date-fns'

import { flex, spacing } from 'styles/traits'

import { Text } from 'components'

const Wrapper = styled.div`
  ${flex.vertical}
  align-items: center;
`

const HR = styled.hr`
  width: 100%;
  border-top: 1px solid ${({ theme }) => theme.bg.border};
`

const TimeStamp = styled.div`
  z-index: 2;
  margin-bottom: -1rem;
  ${spacing.ph05};
  background: rgb(252, 252, 253);
`

export default function Divider({ date }) {
  const [year, month, day] = date.split('-')
  const newDate = new Date(year, month - 1, day)
  const isDateToday = isToday(newDate)
  const isDateYesterday = isYesterday(newDate)
  const fullDate = format(newDate, 'PPPP')
  return (
    <Wrapper>
      <TimeStamp>
        <Text weight="bold">
          {isDateToday ? 'Today' : isDateYesterday ? 'Yesterday' : fullDate}
        </Text>
      </TimeStamp>
      <HR />
    </Wrapper>
  )
}
