import React, { useState } from 'react'

import { useMutation } from '@apollo/react-hooks'

import { GET_DATA, UPDATE_FEEDBACK_SETTINGS } from './graphql'

import { CheckSquare, Square } from 'react-feather'

import Inline from 'components/Inline'
import {
  Form,
  FormHeader,
  FormBody,
  FormFooter,
  CheckboxRowStyle,
} from 'components/FormElements'
import { TextButton, Button } from 'components/Buttons'
import { Text, StatusMessage } from 'components'

const Row = ({ subHeading, value, setValue, setDirty, readOnly }) => {
  return (
    <div>
      <CheckboxRowStyle
        type="button"
        disabled={readOnly}
        onClick={() => {
          setDirty(true)
          setValue(!value)
        }}
      >
        {value === true && <CheckSquare />}
        {value === false && <Square />}
        <Text>{subHeading}</Text>
      </CheckboxRowStyle>
    </div>
  )
}

export default function FeedbackSettings(props) {
  const { sitename, settings } = props
  const [feedbackEnabled, setFeedbackEnabled] = useState(
    settings.feedback_enabled
  )

  const [dirty, setDirty] = useState(false)
  const { admin } = props.siteAccess

  const resetForm = () => {
    setFeedbackEnabled(settings.feedback_enabled)
    setDirty(false)
  }

  const mutationVariables = {
    sitename,
    input: { feedback_enabled: feedbackEnabled },
  }

  const [updateFeedbackSettings, { loading, error }] = useMutation(
    UPDATE_FEEDBACK_SETTINGS,
    {
      variables: mutationVariables,
      refetchQueries: [{ query: GET_DATA, variables: { name: sitename } }],
      onCompleted: () => setDirty(false),
    }
  )

  return (
    <Form
      id="feedback_settings"
      onSubmit={e => {
        updateFeedbackSettings(mutationVariables)
        e.preventDefault()
      }}
    >
      <FormHeader>
        <Text scale={4} weight="medium">
          Beta Feedback program
        </Text>
      </FormHeader>
      <FormBody gap={2}>
        <Row
          heading="Feedback"
          subHeading="Enable feedback beta on site"
          value={feedbackEnabled}
          setValue={setFeedbackEnabled}
          setDirty={setDirty}
          readOnly={!admin}
        />
        <Text scale={6}>
          Get the{' '}
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="https://chrome.google.com/webstore/detail/linc-feedback/hepgmgeaphkfkocdjbanggnnldlbaakm"
          >
            Linc Feedback Chrome extension
          </a>
        </Text>
        {error && (
          <StatusMessage status="critical">
            <Text>{error.message}</Text>
          </StatusMessage>
        )}
      </FormBody>
      {admin && (
        <FormFooter>
          <Inline gap={1}>
            {dirty && (
              <TextButton type="button" onClick={resetForm} disabled={loading}>
                Cancel
              </TextButton>
            )}
            <Button type="submit" disabled={!dirty} loading={loading}>
              Save
            </Button>
          </Inline>
        </FormFooter>
      )}
    </Form>
  )
}
