import React from 'react'
import DefaultForm from './components/DefaultForm'
import WizardForm from './components/WizardForm'

const BugHerdSettings = props => {
  const { sitename, settings } = props
  if (settings) {
    return (
      <DefaultForm
        siteAccess={props.siteAccess}
        sitename={sitename}
        settings={settings}
      />
    )
  } else {
    return <WizardForm siteAccess={props.siteAccess} sitename={sitename} />
  }
}

export default BugHerdSettings
