import immutable from 'object-path-immutable'
import {
  GET_COMMITS_FEED,
  GET_RELEASE_STATUS_CARD,
  GET_SIDEBAR_SITES_LIST,
  GET_ALL_SITES_LIST,
} from 'graphql/queries'

export const updateCommitsFeed = (sitename, client, subscriptionData) => {
  try {
    const cachedData = client.readQuery({
      query: GET_COMMITS_FEED,
      variables: { name: sitename, limit: 20 },
    })

    const newCommit = subscriptionData.data.commit_events

    // TEMP FIX: Add pull_request key/val pair (prevents buggy cache reconciliation)
    newCommit.pull_request =
      newCommit.pull_request === undefined ? null : newCommit.pull_request

    if (cachedData) {
      const matchingCommitIndex = cachedData.view.site.commitsFeed.commits.findIndex(
        item => item.commit_sha === newCommit.commit_sha
      )
      if (matchingCommitIndex !== -1) {
        client.writeQuery({
          query: GET_COMMITS_FEED,
          variables: { name: sitename, limit: 20 },
          data: immutable.set(
            cachedData,
            `view.site.commitsFeed.commits.${matchingCommitIndex}`,
            newCommit
          ),
        })
      } else {
        client.writeQuery({
          query: GET_COMMITS_FEED,
          variables: { name: sitename, limit: 20 },
          data: immutable.insert(
            cachedData,
            `view.site.commitsFeed.commits`,
            newCommit,
            0
          ),
        })
      }
    }
  } catch (e) {
    return
  }
}

// If we get Deployment Status updates for a ReleaseStatus before the RS event comes through,
// we'll store them in this Map with structure:
//   bundle_id => { deploy_target: deploy_status }
const deploymentStatusEventCache = new Map()

// Handles setting the cache
function storeDeployStatus(bundle_id, deploy_target_name, deployment_status) {
  if (!deploymentStatusEventCache.has(bundle_id))
    deploymentStatusEventCache.set(bundle_id, {})
  const bundle_id_cache = deploymentStatusEventCache.get(bundle_id)

  bundle_id_cache[deploy_target_name] = deployment_status
}

export const updateReleaseStatus = (sitename, client, subscriptionData) => {
  try {
    const { releaseStatusV2Updated } = subscriptionData.data

    // If we have deploy statuses cached for this RS bundle_id...
    const bundle_id = releaseStatusV2Updated?.current_release?.bundle_id
    const cache = deploymentStatusEventCache.get(bundle_id)
    if (cache) {
      // ..replace the statuses we get from the server with our cache
      releaseStatusV2Updated.deployment_statuses = releaseStatusV2Updated.deployment_statuses.map(
        status => cache[status.deploy_target_name] || status
      )
    }
    // Always clear the cache after each new RS to avoid memory leaks
    deploymentStatusEventCache.clear()

    const cachedData = client.readQuery({
      query: GET_RELEASE_STATUS_CARD,
      variables: { name: sitename },
    })

    client.writeQuery({
      query: GET_RELEASE_STATUS_CARD,
      variables: { name: sitename },
      data: immutable.set(
        cachedData,
        `view.site.releaseStatusV2`,
        releaseStatusV2Updated
      ),
    })
  } catch (e) {
    return
  }
}

export const updateDeploymentStatus = (sitename, client, subscriptionData) => {
  try {
    const cachedData = client.readQuery({
      query: GET_RELEASE_STATUS_CARD,
      variables: { name: sitename },
    })

    const {
      bundle_id,
      deployment_status,
    } = subscriptionData.data.deploymentStatusEvents

    const { deploy_target_name } = deployment_status

    const released_bundle_id =
      cachedData.view.site.releaseStatusV2?.current_release?.bundle_id || {}

    if (bundle_id !== released_bundle_id) {
      storeDeployStatus(bundle_id, deploy_target_name, deployment_status)
      return
    }

    const deployment_status_index = cachedData.view.site.releaseStatusV2.deployment_statuses
      .map(status => status.deploy_target_name)
      .indexOf(deploy_target_name)

    // If this deploy status is for a target we don't have in our RS, warnu
    if (deployment_status_index === -1) {
      console.warn(
        `Unexpected deployment status for bundle_id ${bundle_id} and target ${deploy_target_name}`
      )
      return
    }

    client.writeQuery({
      query: GET_RELEASE_STATUS_CARD,
      variables: { name: sitename },
      data: immutable.set(
        cachedData,
        `view.site.releaseStatusV2.deployment_statuses.${deployment_status_index}`,
        deployment_status
      ),
    })
  } catch (e) {
    return
  }
}

export const insertNewCommits = (previousResult, fetchMoreResult) => {
  const oldCommits = previousResult.view.site.commitsFeed.commits
  const newCommits = fetchMoreResult.view.site.commitsFeed.commits
  const updatedCommitsFeed = {
    cursor: fetchMoreResult.view.site.commitsFeed.cursor,
    hasNextPage: fetchMoreResult.view.site.commitsFeed.hasNextPage,
    commits: [...oldCommits, ...newCommits],
  }
  return immutable.assign(
    previousResult,
    'view.site.commitsFeed',
    updatedCommitsFeed
  )
}

const getSiteIndexByName = (sites, targetName) =>
  sites.findIndex(item => item.name === targetName)

// adds or removes site from relevant objects in Apollo cache
export const addRemoveSiteCacheUpdate = (cache, sitename, action) => {
  const data = cache.readQuery({ query: GET_ALL_SITES_LIST })
  const targetIndex = getSiteIndexByName(data.view.allSites, sitename)
  const newSite = { ...data.view.allSites[targetIndex] }
  newSite.starred = !newSite.starred
  cache.writeQuery({
    query: GET_ALL_SITES_LIST,
    data: {
      ...data,
      view: {
        allSites: [
          ...data.view.allSites.slice(0, targetIndex),
          newSite,
          ...data.view.allSites.splice(targetIndex + 1),
        ],
        __typename: 'View',
      },
    },
  })

  if (action === 'add') {
    const sideBarData = cache.readQuery({ query: GET_SIDEBAR_SITES_LIST })
    cache.writeQuery({
      query: GET_SIDEBAR_SITES_LIST,
      data: {
        ...sideBarData,
        view: {
          sites: [...sideBarData.view.sites, newSite],
          __typename: 'View',
        },
      },
    })
  }

  if (action === 'remove') {
    const sideBarData = cache.readQuery({ query: GET_SIDEBAR_SITES_LIST })
    const targetSite = getSiteIndexByName(sideBarData.view.sites, sitename)
    const newSites = [...sideBarData.view.sites]
    newSites.splice(targetSite, 1)
    cache.writeQuery({
      query: GET_SIDEBAR_SITES_LIST,
      data: {
        ...sideBarData,
        view: {
          sites: [...newSites],
          __typename: 'View',
        },
      },
    })
  }
}
