import React, { useState } from 'react'
import Stack from 'components/Stack'
import Inline from 'components/Inline'
import { Text, StatusMessage } from 'components'
import { Clipboard, Check, Square } from 'react-feather'
import styled from 'styled-components'
import { flex, spacing, border } from 'styles/traits'

import { CopyToClipboard } from 'react-copy-to-clipboard'

const KeyBoxStyle = styled.div`
  ${flex.justify_space_btw.align_center};
  ${border.br2};
  ${spacing.p066};
  background: ${({ theme }) => theme.bg.wash};
  color: ${({ theme }) => theme.text.default};
  max-width: 32rem;
`

const IconWrapper = styled.div`
  cursor: ${({ copied }) => (copied ? 'auto' : 'pointer')};
  svg {
    height: 1.2rem;
  }
`

const KeyBox = ({ apiKey }) => {
  const [copied, setCopied] = useState(false)
  return (
    <KeyBoxStyle>
      <Text scale={6}>{apiKey}</Text>
      <CopyToClipboard text={apiKey} onCopy={() => setCopied(true)}>
        {copied ? (
          <IconWrapper copied={copied}>
            <Check />
          </IconWrapper>
        ) : (
          <IconWrapper copied={copied} title="copy to clipboard">
            <Clipboard />
          </IconWrapper>
        )}
      </CopyToClipboard>
    </KeyBoxStyle>
  )
}

const StyledSquare = styled(Square)`
  margin: -5px 0 -5px 0;
`

const Row = ({ children }) => (
  <Inline gap={0.5}>
    <StyledSquare />
    {children}
  </Inline>
)

const Instructions = ({ apiKey }) => (
  <StatusMessage>
    <Stack gap={2}>
      <Text scale={3} weight="bold">
        You{`'`}re almost done!
      </Text>
      <Stack>
        <Text weight="bold">
          Before you can start generating previews, you{`'`}ll need to:
        </Text>
        <Row>
          <Text>
            <a
              rel="noopener noreferrer"
              href="https://fab.dev/"
              target="_blank"
            >
              Configure the build step of your frontend repository
            </a>
            .
          </Text>
        </Row>
        <Row>
          <Text>
            <a
              rel="noopener noreferrer"
              href="https://linc.sh/docs/using-external-ci-with-linc"
              target="_blank"
            >
              Configure your frontend repository to upload build artifacts to
              Linc
            </a>
            .
          </Text>
        </Row>
      </Stack>
      <Stack gap={0.5}>
        <Text weight="medium">API key to upload build artifacts to Linc:</Text>
        <KeyBox apiKey={apiKey} />
      </Stack>
    </Stack>
  </StatusMessage>
)

export default Instructions
