import React from 'react'
import { StatusMessage, Text } from 'components'

export function SiteAccessError({ sitename, item }) {
  return (
    <StatusMessage status="critical">
      <Text.P>
        We had some trouble retrieving {item && `${item} for `}
        <code>{sitename}</code>.
        <br />
        This could mean that the site does not exist or that you do not have
        sufficient access to the underlying repository.
      </Text.P>
    </StatusMessage>
  )
}
