import styled from 'styled-components'
import { AlertTriangle } from 'react-feather'
import { standardPreviewLink, trialReleaseUrl } from 'helpers/previewLinks'
import { getDeploymentName } from 'helpers/constants'
import { StatusMessage, Text } from 'components'
import { Link } from 'react-router-dom'
import React from 'react'
import Stack from 'components/Stack'

const ExternalLink = styled.a.attrs({
  target: '_blank',
  rel: 'noreferrer noopener',
})`
  display: inline-block;
`

const AlertIcon = styled(AlertTriangle)`
  margin-top: -4px;
  margin-bottom: -6px;
  padding-right: 4px;
  path {
    fill: ${({ theme }) => theme.warn.default};
    stroke: ${({ theme }) => theme.warn.default};
  }
  line {
    stroke: white;
    stroke-width: 2.5px;
  }
`

const ReleaseFailedMessage = ({
  deployment_status,
  current_release,
  sitename,
}) => {
  const bundle_url = standardPreviewLink({
    sitenameSlug: sitename,
    bundleIdSlug: current_release.bundle_id,
  })
  const deployment_name = getDeploymentName(
    deployment_status.deploy_target_name
  )

  return (
    <StatusMessage status="critical">
      <Stack>
        <Text weight="bold" as="h3">
          <AlertIcon /> Error releasing to {deployment_name}
        </Text>
        <Text.P>
          There was an error releasing bundle{' '}
          <code>{current_release.bundle_id.substr(0, 9)}</code> to{' '}
          {deployment_name}. We received the following error:
        </Text.P>
        <Text scale={6}>
          <code>{deployment_status.message}</code>
        </Text>
        <Text.P>
          To help diagnose the problem, first confirm that this FAB is working
          at its preview url:{' '}
          <ExternalLink href={bundle_url}>{bundle_url}</ExternalLink>.
        </Text.P>
        <Text.P>
          If your FAB appears valid, check your{' '}
          <Link to={`/sites/${sitename}/settings`}>
            {deployment_name} Release Config
          </Link>{' '}
          against our guides for{' '}
          <ExternalLink href="https://linc.sh/knowledge-base/releasing">
            Releasing in Linc
          </ExternalLink>
          .
        </Text.P>
        <Text.P>
          Feel free to get in touch at{' '}
          <ExternalLink href="mailto:help@linc.sh">help@linc.sh</ExternalLink>{' '}
          or by{' '}
          <ExternalLink href="https://github.com/bitgenics/linc/issues">
            raising an issue
          </ExternalLink>{' '}
          if you{`'`}re still having trouble.
        </Text.P>
      </Stack>
    </StatusMessage>
  )
}

export const NoReleasesMessage = ({ sitename }) => {
  const trial_url = trialReleaseUrl({ sitenameSlug: sitename })
  return (
    <StatusMessage>
      <Stack>
        <Text weight="bold" as="h3">
          No production release config
        </Text>
        <Text.P>
          Your commit has been deployed and successfully released on Linc{`'`}s
          demonstration infrastructure at{' '}
          <ExternalLink href={trial_url}>{trial_url}</ExternalLink>.
        </Text.P>
        <Text.P>
          To host on your own infrastructure using your own URL, read our{' '}
          <ExternalLink href="https://linc.sh/knowledge-base/releasing">
            Releasing Documentation
          </ExternalLink>{' '}
          then head to the{' '}
          <Link to={`/sites/${sitename}/settings`}>Settings</Link> to set things
          up.
        </Text.P>
      </Stack>
    </StatusMessage>
  )
}

export default function DeploymentStatusMessages({ release_status }) {
  const { deployment_statuses, current_release, sitename } = release_status
  return deployment_statuses.length === 0 ? (
    <NoReleasesMessage sitename={sitename} />
  ) : (
    deployment_statuses
      .filter(({ status }) => status === 'failed')
      .map(deployment_status => (
        <ReleaseFailedMessage
          key={deployment_status.deploy_target_name}
          {...{ deployment_status, current_release, sitename }}
        />
      ))
  )
}
