import React from 'react'
import { Text, TextField } from 'components'
import { useFilterableRepos } from '../utils'
import { TextButton } from 'components/Buttons'
import { StyledSelect, Avatar, RepoListStyle, CenteredFlexRow } from '../style'
import { CONFIGURE_INSTALLATIONS } from 'helpers/externalRedirects'
import Inline from 'components/Inline'
import Stack from 'components/Stack'
import RepoCard from './RepoCard'
import NoRepos from './NoRepos'

const orgOption = (org, avatar_url) => {
  return {
    value: org,
    label: (
      <CenteredFlexRow>
        <Avatar src={avatar_url} alt="avatar" />
        <Text>{org}</Text>
      </CenteredFlexRow>
    ),
  }
}

const standardOption = (value, label) => ({
  value: value,
  label: (
    <Inline style={{ height: '1rem' }}>
      <Text>{label}</Text>
    </Inline>
  ),
})

export default function RepoList({ view }) {
  const {
    repoFilter,
    repositories,
    setOrgFilter,
    setRepoFilter,
  } = useFilterableRepos(view.github.installations)
  const orgOptions = [
    standardOption('', 'All orgs'),
    ...view.github.installations.map(({ account }) =>
      orgOption(account.login, account.avatar_url)
    ),
  ]
  return (
    <Stack gap={1}>
      <Inline>
        <StyledSelect
          name="select-org"
          options={orgOptions}
          onChange={setOrgFilter}
          defaultValue={orgOptions[0]}
        />
        <TextField
          inputProps={{
            onChange: setRepoFilter,
            placeholder: 'Filter repositories',
          }}
        />
      </Inline>
      <RepoListStyle>
        {repositories.length > 0 &&
          repositories.map((repository, index) => (
            <RepoCard key={index} repository={repository} />
          ))}
      </RepoListStyle>
      {repositories.length > 0 && (
        <TextButton onClick={CONFIGURE_INSTALLATIONS}>
          Can{"'"}t find a repository? Click here to configure your Linc
          installation(s)
        </TextButton>
      )}
      {repositories.length === 0 && (
        <NoRepos
          message={`No repostories found matching '${repoFilter}'`}
          buttonText={'Configure GitHub repositories'}
        />
      )}
    </Stack>
  )
}
