import React, { forwardRef } from 'react'
import PropTypes from 'prop-types'
import styled, { css, keyframes } from 'styled-components'
import {
  AlertTriangle,
  Square,
  Loader,
  XSquare,
  CheckSquare,
  ExternalLink,
} from 'react-feather'
import { flex, typography } from 'styles/traits'
import { HashLink as Link } from 'react-router-hash-link'
import Tooltip from 'components/Tooltip'

const Middot = styled.div`
  ${flex.fg1.align_center};
  text-decoration: none;
  ${typography.f2};
  color: hsl(0, 0%, 85%);
`

const colors = {
  available: 'hsl(213, 0%, 52%)',
  success: 'hsl(120, 52%, 41%)',
  ignored: 'hsl(0, 70%, 56%)',
  failed: 'hsl(0, 70%, 56%)',
  pending: 'hsl(46, 89%, 40%)',
  running: 'hsl(46, 89%, 40%)',
  empty: 'hsl(0, 0%, 85%)',
  queued: 'hsl(0, 0%, 85%)',
}

const animation = keyframes`
  0% {transform: rotate(0);}
  100% {transform: rotate(360deg);}
`

const statusSquareStyle = css`
  ${flex.center_both};
  svg {
    height: 2rem;
    ${({ status }) =>
      (status === 'pending' || status === 'running') &&
      css`
        animation-name: ${animation};
        animation-duration: 2000ms;
        animation-iteration-count: infinite;
        animation-timing-function: linear;
      `};
    ${({ status }) =>
      status === 'available' &&
      css`
        transform: translateY(-1.2px);
        svg {
          path {
            stroke-width: 1.6px;
          }
        }
      `};
    /* color: ${({ status, color }) => (color ? color : colors[status])}; */
    color: ${({ status }) => colors[status]};
  }

  @media only screen and (max-width: 768px) {
    svg {
      height: 1.2rem;
    }
  }
`

const Anchor = styled.a`
  ${statusSquareStyle};

  &::after {
    content: '';
    position: absolute;
    width: 0.5rem;
    height: 0.5rem;
    background: ${({ color }) => color};
    border-radius: 50%;
    margin-left: -0.5rem;
    margin-top: 0.4rem;
    border-style: solid;
    border-width: 0.25rem;
    border-color: white;
  }
`
const StyledLink = styled(Link)`
  ${statusSquareStyle};
`
const DefaultWrapper = styled.div`
  ${statusSquareStyle};
`

const statusResolver = {
  none: <Middot>&middot;</Middot>,
  unknown: <AlertTriangle />,
  ignored: <AlertTriangle />,
  empty: <Square />,
  queued: <Square />,
  pending: <Loader />,
  running: <Loader />,
  failed: <XSquare />,
  success: <CheckSquare />,
  available: <ExternalLink />,
}

const StyledLinkWithRef = forwardRef((props, ref) => (
  <StyledLink innerRef={ref} smooth status={props.status} to={props.to}>
    {props.statusIcon}
  </StyledLink>
))

const StatusSquare = ({ color, status, title, href, to }) => {
  const handleClick = e => e.stopPropagation()
  const statusIcon = statusResolver[status]

  if (href) {
    return (
      <Tooltip
        content={title}
        arrow={true}
        placement="top"
        animation="fade"
        animateFill={false}
        duration={[250, 175]}
        delay={[150, 0]}
        distance={8}
      >
        <Anchor
          color={color}
          rel="noopener noreferrer"
          onClick={handleClick}
          target="_blank"
          status={status}
          href={href}
        >
          {statusIcon}
        </Anchor>
      </Tooltip>
    )
  }

  if (to) {
    return (
      <Tooltip
        content={title}
        arrow={true}
        placement="top"
        animation="fade"
        animateFill={false}
        duration={[250, 175]}
        delay={[150, 0]}
        distance={8}
      >
        <StyledLinkWithRef statusIcon={statusIcon} status={status} to={to} />
      </Tooltip>
    )
  } else {
    return (
      <Tooltip
        content={title}
        arrow={true}
        placement="top"
        animation="fade"
        animateFill={false}
        duration={[250, 175]}
        delay={[150, 0]}
        distance={8}
      >
        <DefaultWrapper status={status}>{statusIcon}</DefaultWrapper>
      </Tooltip>
    )
  }
}

StatusSquare.propTypes = {
  status: PropTypes.oneOf([
    'none',
    'unknown',
    'ignored',
    'empty',
    'queued',
    'pending',
    'running',
    'failed',
    'success',
    'available',
  ]),
}

StatusSquare.defaultProps = {
  status: 'empty',
}

export default StatusSquare
