import React from 'react'
import { Text, StatusMessage } from 'components'
import Stack from 'components/Stack'

import { useParams } from 'react-router'
import { useMutation } from '@apollo/react-hooks'

import { UPDATE_FEEDBACK_SETTINGS } from 'graphql/mutations'

import { Button } from 'components/Buttons'
import { FEEDBACK_OVERVIEW } from '../graphql'
import example from 'assets/extension.webp'

import styled from 'styled-components'
import { hexa } from 'styles/globals'

const Img = styled.img`
  max-width: 70%;
  box-shadow: 0 1px 20px 1px ${({ theme }) => hexa(theme.bg.reverse, 0.2)};
  border-radius: 0.25rem;
`

export default function Welcome(props) {
  const { sitenameSlug } = useParams()
  const { admin } = props.siteAccess

  const mutationVariables = {
    sitename: sitenameSlug,
    input: { feedback_enabled: true },
  }

  const [updateFeedbackSettings, { loading, error }] = useMutation(
    UPDATE_FEEDBACK_SETTINGS,
    {
      variables: mutationVariables,
      refetchQueries: [
        { query: FEEDBACK_OVERVIEW, variables: { name: sitenameSlug } },
      ],
      onCompleted: () => console.log('completed!'),
    }
  )

  const enableFeedback = () => {
    updateFeedbackSettings(mutationVariables)
  }
  return (
    <Stack gap={2}>
      <Stack>
        <Text.P scale={4} weight="medium">
          Linc Feedback is a new tool that lets you leave feedback directly on
          preview URLs.
        </Text.P>
        <Img style={{ width: '80%' }} src={example} />
      </Stack>
      <Stack gap={0.5}>
        <Text.P scale={4} weight="medium">
          Feedback left on preview URLs goes to three places:
        </Text.P>
        <Text.P>
          1. <strong>GitHub Pull Requests</strong> - if the preview URL is part
          of a GitHub Pull Request, Linc will post your feedback into the Pull
          Request.
        </Text.P>
        <Text.P>
          2. <strong>Linc Feedback Overview</strong> - all feedback left on
          preview URLs for <code>{sitenameSlug}</code> can be found on this
          page.
        </Text.P>
        <Text.P>
          3. <strong>Linc Commit Overview</strong> - feedback left on a preview
          URL that matches a given commit can be found on the commit overview
          page in Linc.
        </Text.P>
      </Stack>
      <Stack>
        <Text.P scale={4} weight="medium">
          To get started, first add the{' '}
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="https://chrome.google.com/webstore/detail/linc-feedback/hepgmgeaphkfkocdjbanggnnldlbaakm"
          >
            Linc Feedback browser extension
          </a>{' '}
          to Chrome.
        </Text.P>
        <Text.P scale={4} weight="medium">
          Then, enable Linc Feedback on <code>{sitenameSlug}</code> by clicking
          the <strong>Enable Feedback</strong> button below.
        </Text.P>
      </Stack>
      <div
        title={
          admin
            ? `Enable feedback on ${sitenameSlug}`
            : `You must be an administrator of ${sitenameSlug} to enable feedback`
        }
      >
        <Button
          large
          loading={loading}
          onClick={enableFeedback}
          disabled={loading || !admin}
        >
          Enable Feedback
        </Button>
      </div>
      {error && (
        <StatusMessage status="critical">
          <Text>{error.message}</Text>
        </StatusMessage>
      )}
    </Stack>
  )
}
