import React from 'react'
import { StatusMessage, Text, Spinner } from 'components'
import { StripeProvider, Elements } from 'react-stripe-elements'
import CheckoutForm from 'views/CheckoutForm'
import { useScript } from 'hooks'
import Stack from "components/Stack"

const configurations = {
  development: { stripeApiKey: process.env.REACT_APP_STRIPE_PK_TEST },
  production: { stripeApiKey: process.env.REACT_APP_STRIPE_PK_LIVE },
  test: { stripeApiKey: process.env.REACT_APP_STRIPE_PK_TEST },
}

const environment = process.env.NODE_ENV || 'production'
const config = configurations[environment]

export default function Stripe({ sitename }) {
  const stripeScript = 'https://js.stripe.com/v3/'
  const [scriptLoaded, scriptError] = useScript(stripeScript)
  if (scriptLoaded) {
    return (
      <StripeProvider apiKey={config.stripeApiKey}>
        <Elements>
          <CheckoutForm sitename={sitename} />
        </Elements>
      </StripeProvider>
    )
  }
  if (scriptError) {
    return (
      <Stack gap={2}>
        <StatusMessage status="critical">
          <Text>An error occured</Text>
        </StatusMessage>
      </Stack>
    )
  } else {
    return (
      <Stack gap={2}>
        <Spinner />
      </Stack>
    )
  }
}
