export const itemsByDay = data => {
  const groups = data.reduce((groups, feedback) => {
    const tzoffset = new Date().getTimezoneOffset() * 60000 //offset in milliseconds
    const localISOTime = new Date(parseInt(feedback.created_at) - tzoffset)
      .toISOString()
      .slice(0, -1)

    const date = localISOTime.split('T')[0]
    if (!groups[date]) {
      groups[date] = []
    }
    groups[date].push(feedback)
    return groups
  }, {})

  const groupArrays = Object.keys(groups).map(date => {
    return {
      date,
      items: groups[date],
    }
  })
  return groupArrays
}

export const getBranches = allFeedback => {
  let branches = []
  allFeedback.forEach(feedback => {
    const { associated_branches } = feedback

    const newBranches = [...branches, ...associated_branches]
    branches = newBranches
  })
  return [...new Set(branches)]
}
