import React from 'react'
import styled from 'styled-components'
import { flex, spacing } from 'styles/traits'
import { GitCommit, Calendar, MessageSquare } from 'react-feather'
import { Text } from 'components'
import Avatar from 'components/Avatar'
import relativeDate from 'relative-date'
import MetaCell from './MetaCell'
import { useHistory, useParams } from 'react-router'
import { TextPlaceholder, ImagePlaceholder } from 'components/Placeholders'

const LoadingPlaceholder = () => (
  <Wrapper>
    <MetaCell>
      <TextPlaceholder />
    </MetaCell>
    <MetaCell>
      <ImagePlaceholder height={1.3} width={1.3} />
      <TextPlaceholder maxWidth={3} />
    </MetaCell>
    <MetaCell>
      <TextPlaceholder minWidth={6} />
    </MetaCell>
    <MetaCell>
      <TextPlaceholder />
    </MetaCell>
  </Wrapper>
)

const Wrapper = styled.div`
  min-height: 3.5rem;
  display: grid;
  position: relative;
  cursor: pointer;
  grid-template-columns: 1fr 1fr 2fr 1fr;
  grid-template-areas: 'commit-sha author message timestamp';

  &:hover::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100vw;
    right: -100vw;
    bottom: 0;
    background: ${({ theme }) => theme.space.wash};
    z-index: -1;
  }
  border-style: solid;
  border-width: 1px 0 0 1px;
  border-color: ${({ theme }) => theme.bg.border};

  &:first-of-type {
    background: red;
    border-width: none;
  }

  &:last-of-type {
    background: red;
    border-width: none;
  }

  @media only screen and (max-width: 1450px) {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-areas:
      'commit-sha timestamp'
      'author message';
  }

  @media only screen and (max-width: 768px) {
    grid-column-start: 1;
    grid-column-end: 4;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 3rem auto;
    grid-template-areas:
      'author timestamp commit-sha'
      'message message message';
  }

  span {
    white-space: nowrap;
    text-overflow: ellipsis;
  }
`

const Anchor = styled.a`
  ${flex.align_center};
  > * + * {
    ${spacing.ml05};
  }
  text-decoration: none;

  &:hover {
    span {
      color: ${({ theme }) => theme.space.default};
      text-decoration: underline;
    }
    svg {
      color: ${({ theme }) => theme.space.default};
    }
  }
`

export default function CommitMetaData({
  loading,
  author,
  timestamp,
  commit_sha,
  message,
  url,
}) {
  const { sitenameSlug } = useParams()
  const history = useHistory()
  if (loading) {
    return <LoadingPlaceholder />
  } else {
    const redirectToCommitOverview = () => {
      const commitOverview = `/sites/${sitenameSlug}/commit/${commit_sha}`
      history.push(commitOverview)
    }

    const handleClick = event => {
      // stop child onClick event bubbling up and
      // triggering parent onClick
      event.stopPropagation()
    }

    return (
      <Wrapper onClick={redirectToCommitOverview}>
        <MetaCell gridArea="author">
          {author && (
            <>
              <Avatar name={author.name} username={author.username} />
              <Text scale={6}>{author.name || author.username}</Text>
            </>
          )}
        </MetaCell>
        <MetaCell gridArea="timestamp">
          {timestamp && (
            <>
              <Calendar />
              <Text tone="secondary" scale={6}>
                {relativeDate(new Date(timestamp))}
              </Text>
            </>
          )}
        </MetaCell>
        <MetaCell gridArea="commit-sha">
          {commit_sha && (
            <>
              <Anchor
                target="_blank"
                rel="noopener noreferrer"
                onClick={handleClick}
                href={url}
              >
                <GitCommit />
                <Text scale={6}>{commit_sha.slice(0, 9)}</Text>
              </Anchor>
            </>
          )}
        </MetaCell>
        <MetaCell gridArea="message">
          <>
            <MessageSquare />
            <Text.P scale={6} weight="medium">
              {message.slice(0, 50)}
            </Text.P>
          </>
        </MetaCell>
      </Wrapper>
    )
  }
}
