import { spacing, flex, typography } from 'styles/traits'
import styled, { css } from 'styled-components'

export const Wrapper = styled.div`
  ${flex};
`

const disabledStyle = css`
  color: ${({ theme, active }) =>
    active ? theme.text.reverse : theme.text.placeholder};

  background: ${({ theme, active }) =>
    active ? theme.brand.wash : theme.bg.default};

  &:hover {
    cursor: default;
    background: ${({ theme, active }) =>
      active ? theme.brand.wash : theme.bg.default};
  }
`

const baseButtonStyle = css`
  ${flex.center_both};
  ${spacing.pv05.ph1};
  ${typography.f6};

  cursor: pointer;
  text-transform: capitalize;
  transition: 200ms ease all;

  background: ${({ theme, active }) =>
    active ? theme.brand.default : theme.bg.default};

  color: ${({ theme, active }) =>
    active ? theme.text.reverse : theme.text.default};

  border-style: solid;
  border-width: 1px 0 1px 0;
  border-color: ${({ theme }) => theme.bg.border};

  &:first-of-type {
    border-radius: 0.25rem 0 0 0.25rem;
    border-width: 1px 1px 1px 1px;
  }

  &:last-of-type {
    border-radius: 0 0.25rem 0.25rem 0;
    border-width: 1px 1px 1px 1px;
  }

  &:hover {
    ${props =>
      props.active === false &&
      css`
        background: ${({ theme }) => theme.brand.wash};
      `}
  }
`

export const OptionStyle = styled.button`
  ${baseButtonStyle};
  ${({ isDisabled }) => isDisabled && disabledStyle};
`
