import styled from 'styled-components'
import PropTypes from 'prop-types'

const Box = styled.div`
  margin: ${({ margin }) => margin || 0}rem;
  padding: ${({ padding }) => padding || 0}rem;
`

Box.defaultProps = {
  margin: 0,
  padding: 0,
}

Box.propTypes = {
  margin: PropTypes.oneOf([0, 0.25, 0.5, 1, 1.5, 2, 3, 4]),
  padding: PropTypes.oneOf([0, 0.25, 0.5, 1, 1.5, 2, 3, 4]),
}

export default Box
