import React from 'react'
import { useQuery } from '@apollo/react-hooks'
import { useParams } from 'react-router'

import { Text, StatusMessage } from 'components'

import LoadingPlaceholder from './components/LoadingPlaceholder'
import Welcome from './components/Welcome'
import NoFeedback from './components/NoFeedback'
import List from './components/List'

import { FEEDBACK_OVERVIEW } from './graphql'

export default function Feedback() {
  const { sitenameSlug } = useParams()
  const { loading, error, data } = useQuery(FEEDBACK_OVERVIEW, {
    variables: { name: sitenameSlug },
  })

  if (loading) {
    return <LoadingPlaceholder />
  }

  if (error) {
    return (
      <StatusMessage>
        <Text>Error retrieving Feedback page</Text>
      </StatusMessage>
    )
  }

  if (data) {
    const site = data?.view?.site
    const feedback_settings = site?.settings?.feedback_settings
    const siteAccess = data?.view?.site?.access

    if (feedback_settings && feedback_settings.feedback_enabled) {
      const allSiteFeedback = data?.view?.site?.allSiteFeedback

      if (allSiteFeedback.length > 0) {
        return <List allSiteFeedback={allSiteFeedback} />
      } else {
        return <NoFeedback sitename={sitenameSlug} />
      }
    } else {
      return (
        <Welcome
          siteAccess={siteAccess}
          feedback_settings={feedback_settings}
        />
      )
    }
  }
}
