import React from 'react'
import FlipMove from 'react-flip-move'
import ReleaseStatus from 'views/ReleaseStatus'
import { useQuery } from '@apollo/react-hooks'
import { GET_RELEASE_STATUS_CARD } from 'graphql/queries'
import { SiteAccessError } from 'components/Errors'
import Stack from 'components/Stack'
import DeploymentStatusMessages from './DeploymentStatusMessages'
import { observer } from 'mobx-react'
import { getSiteFlags } from 'stores/flags'
import DeploymentConfigPrompt from './DeploymentConfigPrompt'
import { enterReleaseCardAnimation, leaveReleaseCardAnimation } from './style'
import { useParams } from 'react-router'

const Content = observer(({ site, sitename }) => {
  const siteFlags = getSiteFlags(sitename)
  const release_status = site?.releaseStatusV2
  if (release_status) {
    const hasCurrentRelease = release_status?.current_release?.bundle_id
    const hasDeploymentConfig = release_status.is_configured
    if (hasCurrentRelease && hasDeploymentConfig) {
      return (
        <Stack gap={2}>
          <FlipMove
            duration={1000}
            enterAnimation={enterReleaseCardAnimation}
            leaveAnimation={leaveReleaseCardAnimation}
          >
            <ReleaseStatus
              key={release_status.modified_at}
              release_status={release_status}
            />
          </FlipMove>
          <DeploymentStatusMessages release_status={release_status} />
        </Stack>
      )
    }
    if (siteFlags && siteFlags.showDeploymentConfigPrompt === false) {
      return null
    }
    if (siteFlags && siteFlags.showDeploymentConfigPrompt === true) {
      return (
        <DeploymentConfigPrompt
          sitename={sitename}
          releaseBranch={release_status.release_branch}
        />
      )
    }
  } else {
    return null
  }
})

export default function ActiveReleases({ fetchPolicy }) {
  const { sitenameSlug } = useParams()
  const { loading, error, data } = useQuery(GET_RELEASE_STATUS_CARD, {
    variables: { name: sitenameSlug },
    fetchPolicy,
  })

  if (loading) {
    return (
      <Stack gap={2}>
        <ReleaseStatus loading />
      </Stack>
    )
  }

  if (error) {
    return <SiteAccessError sitename={sitenameSlug} item="active releases" />
  }

  const site = data?.view?.site

  if (site) {
    return <Content site={site} sitename={sitenameSlug} />
  }
  return <SiteAccessError sitename={sitenameSlug} item="active releases" />
}
