import React from 'react'
import { Text } from 'components'
import { TextButton } from 'components/Buttons'
import {
  Avatar,
  ListItem,
  PlaceholderListItem,
  CenteredFlexRow,
} from '../style'
import relativeDate from 'relative-date'
import { TextPlaceholder, ImagePlaceholder } from 'components/Placeholders'
import Inline from 'components/Inline'
import Stack from 'components/Stack'

const generatePath = (repo, branch) =>
  `/create/github/${encodeURIComponent(repo)}/${encodeURIComponent(branch)}`

export default function RepoCard({ repository }) {
  if (repository) {
    const { full_name, avatar_url, pushed_at, default_branch } = repository
    return (
      <ListItem key={full_name} to={generatePath(full_name, default_branch)}>
        <Inline>
          <Stack gap={0.5}>
            <CenteredFlexRow internalSpacing={0.5}>
              <Avatar src={avatar_url} alt="avatar" />
              <Text weight="bold">{full_name}</Text>
            </CenteredFlexRow>
            <Text tone="secondary" scale={6}>
              Last pushed {relativeDate(new Date(pushed_at))}
            </Text>
          </Stack>
        </Inline>
        <TextButton>Select</TextButton>
      </ListItem>
    )
  } else {
    return (
      <PlaceholderListItem>
        <Inline>
          <Stack gap={0.5}>
            <CenteredFlexRow internalSpacing={0.5}>
              <ImagePlaceholder height={1.5} width={1.5} />
              <TextPlaceholder />
            </CenteredFlexRow>
            <TextPlaceholder minWidth={6} maxWidth={11} />
          </Stack>
        </Inline>
        <TextButton>
          <TextPlaceholder />
        </TextButton>
      </PlaceholderListItem>
    )
  }
}
