import gql from 'graphql-tag'

export const GET_DATA = gql`
  query($name: String!) {
    view {
      site(name: $name) {
        access {
          admin
          write
          read
          no_access
        }
        settings {
          feedback_settings {
            feedback_enabled
          }
        }
      }
    }
  }
`

export const UPDATE_FEEDBACK_SETTINGS = gql`
  mutation($sitename: String!, $input: FeedbackSettingsInput!) {
    updateFeedbackSettings(sitename: $sitename, input: $input)
  }
`
