import React, { useState } from 'react'
import { useQuery } from '@apollo/react-hooks'
import { GET_ALL_SITES_LIST } from 'graphql/queries'
import { StatusMessage, Text } from 'components'
import { Container, NoSites } from './style'
import SitesList from './SitesList'
import Header from './Header'
import { useDocumentTitle } from 'hooks'

export default function AllSites() {
  useDocumentTitle('Sites | Linc')
  const [filter, setFilter] = useState('')

  const { loading, error, data } = useQuery(GET_ALL_SITES_LIST, {
    fetchPolicy: 'cache-first',
  })

  const filterSites = sites => {
    const lowerCaseFilter = filter.toLocaleLowerCase()
    return sites.filter(site => {
      const matchSet = [site.name, site.repository_name || ""]
      return matchSet.some(item => item.toLowerCase().includes(lowerCaseFilter))
    })
  }

  if (loading) {
    return (
      <Container gap={2}>
        <Header loading />
        <SitesList loading />
      </Container>
    )
  }

  if (error) {
    return (
      <Container gap={2}>
        <Header loading />
        <StatusMessage>
          <Text>No sites found for user</Text>
        </StatusMessage>
      </Container>
    )
  }

  const sites = data?.view?.allSites

  if (sites && sites.length > 0) {
    const filteredSites = filterSites(sites)
    return (
      <Container gap={2}>
        <Header setFilter={setFilter} />
        {filteredSites.length === 0 ? (
          <NoSites>
            <Text tone="placeholder" weight="bold">
              No sites found matching {`'${filter}'`}
            </Text>
          </NoSites>
        ) : (
          <SitesList sites={filteredSites} />
        )}
      </Container>
    )
  } else {
    return (
      <Container gap={2}>
        <Header setFilter={setFilter} />
        <NoSites>
          <Text tone="placeholder" weight="bold">
            You currently have no sites
          </Text>
        </NoSites>
      </Container>
    )
  }
}
