import React, { useState } from 'react'
import { Text, TextField, StatusMessage } from 'components'
import { Button } from 'components/Buttons'
import { injectStripe, CardElement } from 'react-stripe-elements'
import { Form, FormHeader, FormBody, FormFooter } from 'components/FormElements'
import { useApolloClient } from '@apollo/react-hooks'
import gql from 'graphql-tag'

const UPDATE_SITE_PAYMENT_INFO = gql`
  mutation($input: SitePaymentInput!) {
    updateSitePaymentInfo(input: $input)
  }
`

const CheckoutForm = props => {
  const client = useApolloClient()
  const [complete, setComplete] = useState(false)
  const [loading, setLoading] = useState(false)
  const [email, setEmail] = useState('')
  const [err, setErr] = useState(null)

  const submit = async e => {
    e.preventDefault()
    const { stripe, sitename } = props
    const { source } = await stripe.createSource({
      type: 'card',
      owner: {
        email,
      },
    })

    if (source === undefined) {
      return
    }

    if (source.error) {
      setErr(source.error)
    }

    if (source.id) {
      setLoading(true)
      client
        .mutate({
          mutation: UPDATE_SITE_PAYMENT_INFO,
          variables: { input: { email: email, token: source.id, sitename } },
        })
        .then(() => {
          setComplete(true)
          setLoading(false)
        })
        .catch(e => {
          setErr(e)
          setLoading(false)
        })
    }
  }

  return (
    <Form onSubmit={submit}>
      <FormHeader>
        <Text weight="bold" scale={4}>
          Checkout
        </Text>
      </FormHeader>
      <FormBody gap={1}>
        <Text weight="medium">Email Address</Text>
        <TextField
          inputProps={{
            type: 'email',
            required: true,
            placeholder: 'Email',
            value: email,
            onChange: e => setEmail(e.target.value),
          }}
        />
        <Text weight="medium">Payment</Text>
        <CardElement />
        {err && (
          <StatusMessage status="critical">
            <Text>{err.message}</Text>
          </StatusMessage>
        )}
      </FormBody>
      <FormFooter>
        {complete && (
          <StatusMessage status="positive">
            <Text tone="reverse">Billing information updated!</Text>
          </StatusMessage>
        )}
        {!complete && (
          <Button loading={loading} disabled={loading} type="submit">
            Save
          </Button>
        )}
      </FormFooter>
    </Form>
  )
}

export default injectStripe(CheckoutForm)
