import styled, { css, keyframes } from 'styled-components'
import { hexa } from 'styles/globals'
import { theme } from 'styles/theme'

const spin = keyframes`
  from {
    transform: rotate(45deg);
  }
  to {
    transform: rotate(405deg);
  }
`

const monochrome = css`
  border-color: ${hexa(theme.bg.reverse, 0.25)};
  border-top-color: ${hexa(theme.bg.default, 0.75)};
`

const brand = css`
  border-color: ${hexa(theme.bg.reverse, 0.1)};
  border-top-color: ${hexa(theme.brand.alt, 0.75)};
`

export const SpinnerStyle = styled.div`
  height: ${({ size }) => size}rem;
  width: ${({ size }) => size}rem;
  background: none;
  border-style: solid;
  border-width: 0.2rem;
  border-radius: 50%;
  transition: all 0.2s ease 0s;
  animation: ${spin} 0.7s cubic-bezier(0.56, 0.11, 0.22, 0.865) 0s infinite;
  ${({ color }) => color === 'monochrome' && monochrome};
  ${({ color }) => color === 'brand' && brand};
`
