import React from 'react'
import { useQuery } from '@apollo/react-hooks'

import { StatusMessage, Text } from 'components'
import { useDocumentTitle } from 'hooks'
import { ReleaseConfigs } from 'helpers/constants'
import Stack from 'components/Stack'

import Placeholder from '../Placeholder'

import ConfigForm from './ConfigForm'
import { fieldDefs } from './utils'
import {
  GET_DATA,
  UPDATE_LAMBDA_CONFIG,
  UPDATE_CLOUDFLARE_CONFIG,
  DELETE_DEPLOY_CONFIG,
} from './graphql'

const getInitialState = (fields, field_defs) => {
  if (fields === null) {
    const initial_state = {}
    Object.keys(field_defs).forEach(
      key => (initial_state[key] = field_defs[key].initial_value)
    )
    return initial_state
  } else {
    delete fields.__typename
    return fields
  }
}

const DeployConfig = ({ sitename }) => {
  useDocumentTitle(`Deploy settings`)

  const { loading, error, data } = useQuery(GET_DATA, {
    variables: { name: sitename },
    errorPolicy: 'all',
  })

  if (loading) {
    return <Placeholder />
  }

  if (error) {
    return (
      <StatusMessage status="critical">
        <Text>An error occured while retrieving settings data</Text>
      </StatusMessage>
    )
  }

  const site = data?.view?.site
  if (site) {
    const siteAccess = site?.access
    const release_config = site?.settings?.release_config
    const { lambda_edge, cloudflare } = release_config?.deploy_config || {}

    const lambdaFields = fieldDefs[ReleaseConfigs.LAMBDA_EDGE]
    const cloudflareFields = fieldDefs[ReleaseConfigs.CF_WORKERS]
    const initialLambdaState = getInitialState(lambda_edge, lambdaFields)
    const initialCloudflareState = getInitialState(cloudflare, cloudflareFields)
    return (
      <Stack gap={2} id="deploy_config">
        <Stack>
          <Text scale={4} weight="bold">
            Deploy Config
          </Text>
          <Text scale={6}>
            Visit the{' '}
            <a
              href="https://linc.sh/knowledge-base/releasing"
              target="_blank"
              rel="noopener noreferrer"
            >
              Docs
            </a>{' '}
            to learn more about enabling Continuous Delivery
          </Text>
        </Stack>
        <ConfigForm
          sitename={sitename}
          siteAccess={siteAccess}
          heading={ReleaseConfigs.LAMBDA_EDGE}
          nullStateMessage={`${ReleaseConfigs.LAMBDA_EDGE} configuration not set`}
          fieldDefs={lambdaFields}
          initialState={initialLambdaState}
          updateMutation={UPDATE_LAMBDA_CONFIG}
          deleteMutation={DELETE_DEPLOY_CONFIG}
          deploy_target_name="lambda_edge"
        />
        <ConfigForm
          sitename={sitename}
          siteAccess={siteAccess}
          heading={ReleaseConfigs.CF_WORKERS}
          nullStateMessage={`${ReleaseConfigs.CF_WORKERS} configuration not set`}
          fieldDefs={cloudflareFields}
          initialState={initialCloudflareState}
          updateMutation={UPDATE_CLOUDFLARE_CONFIG}
          deleteMutation={DELETE_DEPLOY_CONFIG}
          deploy_target_name="cloudflare"
        />
      </Stack>
    )
  } else {
    return (
      <StatusMessage status="critical">
        <Text>Error: unable to retrieve site settings data</Text>
      </StatusMessage>
    )
  }
}

export default DeployConfig
