import React from 'react'
import styled from 'styled-components'

import useSiteName from 'hooks/useSiteName'
import { useMutation } from '@apollo/react-hooks'
import { useHistory } from 'react-router'

import { GET_SIDEBAR_SITES_LIST } from 'graphql/queries'

import Stack from 'components/Stack'
import Inline from 'components/Inline'
import { Button, TextButton } from 'components/Buttons'
import { Text, StatusMessage } from 'components'

import { CREATE_NEW_SITE } from 'graphql/mutations'
import SiteField from './SiteField'

const LI = styled.li`
  list-style-type: disc;
  text-indent: 1rem;
`

const SupportedServices = () => {
  const services = ['CircleCI', 'Travis CI', 'Buildkite', 'GitLab']
  return (
    <>
      <Stack as="ul" gap={0.5}>
        {services.map(service => (
          <LI key={service}>
            <Text scale={6}>{service}</Text>
          </LI>
        ))}
      </Stack>
    </>
  )
}

const Info = () => (
  <StatusMessage status="info">
    <Stack>
      <Text.P scale={6} weight="medium" lineHeight={1.3}>
        <strong>NOTE:</strong> We do not currently have native support for your
        Git provider.
      </Text.P>
      <Text.P scale={6} lineHeight={1.3}>
        But you can still use Linc by using our CI/CD integration tool. It has
        been tested in:
      </Text.P>
      <SupportedServices />
      <Text.P scale={6} lineHeight={1.3}>
        If you are using another CI platform, please get in touch with us at{' '}
        <a href="mailto:support@linc.sh">support@linc.sh</a>
      </Text.P>
    </Stack>
  </StatusMessage>
)

export default function OtherConf() {
  const history = useHistory()
  const sitename = useSiteName('')

  const mutationVariables = () => ({
    input: {
      release_branch: 'master',
      name: sitename.legalValue,
      path: '/',
    },
  })

  const [createNewSite, { loading }] = useMutation(CREATE_NEW_SITE, {
    variables: mutationVariables(),
    onCompleted: () => history.push(`/sites/${sitename.legalValue}`),
    refetchQueries: [{ query: GET_SIDEBAR_SITES_LIST }],
  })

  const goBack = () => history.push('/create')

  return (
    <Stack
      as="form"
      onSubmit={e => {
        createNewSite()
        e.preventDefault()
      }}
    >
      <Info />
      <SiteField sitename={sitename} />
      <Inline style={{ justifyContent: 'flex-end' }}>
        <TextButton type="button" onClick={goBack}>
          Go back
        </TextButton>
        <Button
          disabled={!sitename.isValid || loading}
          loading={loading}
          type="submit"
        >
          Create site
        </Button>
      </Inline>
    </Stack>
  )
}
