import styled, { css } from 'styled-components'
import { flex, spacing, border } from 'styles/traits'

const baseStyle = css`
  ${flex.align_center};
  ${border.ba.br2};
  ${spacing.p05};
  cursor: pointer;
  background: none;
  transition: 200ms ease all;
  border-color: ${({ theme }) => theme.bg.hairline};
`

const hoverStyle = css`
  border-color: ${({ theme }) => theme.brand.default};
  span {
    color: ${({ theme }) => theme.brand.default};
  }
`

const selectedStyle = css`
  border-color: ${({ theme }) => theme.brand.default};
  span {
    color: ${({ theme }) => theme.brand.default};
  }
  background: ${({ theme }) => theme.brand.wash};
`

const BranchTag = styled.a`
  ${baseStyle};
  &:hover {
    ${hoverStyle};
  }
  ${({ selected }) => selected && selectedStyle};
`

export default BranchTag
