import gql from 'graphql-tag'
import { Tests, HeadCommit, Build } from './fragments'

export const COMMIT_EVENTS = gql`
  ${HeadCommit}
  ${Build}
  ${Tests}
  subscription($sitename: String!, $tree_id: String) {
    commit_events(sitename: $sitename, tree_id: $tree_id) {
      tree_id
      branch
      commit_sha
      repository
      ...Tests
      ...HeadCommit
      ...Build
    }
  }
`

export const RELEASE_STATUS_V2_UPDATE = gql`
  subscription($sitename: String!) {
    releaseStatusV2Updated(sitename: $sitename) {
      is_configured
      sitename
      modified_at
      release_branch
      current_release {
        bundle_id
        deployed_at
      }
      commit {
        repo_url
        timestamp
        author {
          name
          username
        }
        commit_sha
        branch
        message
      }
      deployment_statuses {
        deploy_target_name
        message
        status
        timestamp
      }
    }
  }
`

export const DEPLOYMENT_STATUS_EVENTS = gql`
  subscription($sitename: String!) {
    deploymentStatusEvents(sitename: $sitename) {
      sitename
      bundle_id
      deployment_status {
        deploy_target_name
        timestamp
        message
        status
      }
    }
  }
`
