import React from 'react'
import PropTypes from 'prop-types'
import styled, { css, keyframes } from 'styled-components'
import { flex } from 'styles/traits'
import { Check, X, AlertTriangle, Info, Loader } from 'react-feather'

const animation = keyframes`
  0% {transform: rotate(0);}
  100% {transform: rotate(360deg);}
`

const getColor = (status, theme) => {
  const options = {
    positive: theme.success.default,
    critical: theme.warn.default,
    warning: theme.special.default,
    info: theme.space.default,
    loading: theme.text.default,
  }
  return options[status]
}

const Container = styled.div`
  ${flex.center_both};
  svg {
    color: ${({ status, theme }) => getColor(status, theme)};
    ${props =>
      props.status === 'loading' &&
      css`
        animation-name: ${animation};
        animation-duration: 600ms;
        animation-iteration-count: infinite;
        animation-timing-function: linear;
      `};
  }
  ${props => props.customCss};
`

const StatusIcon = ({ status, scale, customCss }) => {
  const statusIcons = {
    positive: <Check />,
    critical: <X />,
    warning: <AlertTriangle />,
    info: <Info />,
    loading: <Loader />,
  }
  return (
    <Container status={status} scale={scale} customCss={customCss}>
      {statusIcons[status]}
    </Container>
  )
}

StatusIcon.propTypes = {
  status: PropTypes.oneOf([
    'positive',
    'critical',
    'warning',
    'info',
    'loading',
  ]),
  scale: PropTypes.oneOf(['f1', 'f2', 'f3', 'f4', 'f5', 'f6']),
  customCss: PropTypes.array,
}

export default StatusIcon
