import React, { useState } from 'react'
import { useMutation } from '@apollo/react-hooks'
import { GET_DATA, UPDATE_GENERAL_SETTINGS } from '../graphql'
import { Form, FormHeader, FormBody, FormFooter } from 'components/FormElements'
import Inline from 'components/Inline'
import { Text, StatusMessage, TextField } from 'components'
import { TextButton, Button } from 'components/Buttons'
import { generalSettingsVariables } from 'graphql/variableFormatters'
import usePackagePath, { PATH_ERRORS } from 'hooks/usePackagePath'

function Row({ heading, subHeading, inputProps, errors }) {
  return (
    <>
      <Text weight="medium">{heading}</Text>
      {subHeading && (
        <Text scale={6} tone="secondary">
          {subHeading}
        </Text>
      )}
      <TextField inputProps={inputProps} />
      {errors &&
        errors.map(error => {
          if (error === PATH_ERRORS.MISSING_EXTENSION) {
            return (
              <StatusMessage key={error} status="critical">
                <Text>
                  Path must end in <code>/package.json</code>
                </Text>
              </StatusMessage>
            )
          } else {
            return null
          }
        })}
    </>
  )
}

export default function BuildSettings(props) {
  const { sitename, settings } = props

  const [relBranch, setRelBranch] = useState(settings.release_branch)
  const [fabPath, setFabPath] = useState(settings.fab_path)
  const path = usePackagePath(settings.path)
  const [dirty, setDirty] = useState(false)

  const { admin } = props.siteAccess

  const resetForm = () => {
    setRelBranch(settings.release_branch)
    setFabPath(settings.fab_path)
    path.setPath(settings.path)
    setDirty(false)
  }

  const isValid = relBranch.length > 0 && fabPath.length > 0 && path.isValid

  const mutationVariables = generalSettingsVariables(
    sitename,
    relBranch,
    path.legalPathName(),
    fabPath
  )

  const [updateGeneralSettings, { loading, error }] = useMutation(
    UPDATE_GENERAL_SETTINGS,
    {
      variables: mutationVariables,
      refetchQueries: [{ query: GET_DATA, variables: { name: sitename } }],
      onCompleted: () => setDirty(false),
    }
  )

  return (
    <Form
      onSubmit={e => {
        updateGeneralSettings(mutationVariables)
        e.preventDefault()
      }}
    >
      <FormHeader>
        <Text scale={4} weight="medium">
          Build Settings
        </Text>
      </FormHeader>
      <FormBody>
        <Row
          heading="Release Branch"
          subHeading="Your designated repository release branch"
          inputProps={{
            required: true,
            value: relBranch,
            readOnly: !admin,
            onChange: e => {
              setDirty(true)
              setRelBranch(e.target.value)
            },
          }}
        />
        <Row
          heading="package.json path"
          subHeading="We look for your package.json at this path"
          inputProps={{
            value: path.value,
            placeholder: '/package.json',
            readOnly: !admin,
            onChange: e => {
              setDirty(true)
              path.setPath(e.target.value)
            },
            type: 'text',
            required: true,
            maxLength: 120,
          }}
          errors={path.errors}
        />
        <Row
          heading="FAB path"
          subHeading="This is where npm run build:fab outputs"
          inputProps={{
            required: true,
            value: fabPath,
            readOnly: !admin,
            onChange: e => {
              setDirty(true)
              setFabPath(e.target.value)
            },
          }}
        />
        {error && (
          <StatusMessage status="critical">
            <Text>{error.message}</Text>
          </StatusMessage>
        )}
      </FormBody>
      {admin && (
        <FormFooter>
          <Inline gap={1}>
            {dirty && (
              <TextButton type="button" onClick={resetForm} disabled={loading}>
                Cancel
              </TextButton>
            )}
            <Button
              type="submit"
              disabled={!isValid || !dirty}
              loading={loading}
            >
              Save
            </Button>
          </Inline>
        </FormFooter>
      )}
    </Form>
  )
}
