import React, { forwardRef } from 'react'
import styled, { css } from 'styled-components'
import { flex, spacing } from 'styles/traits'
import Tooltip from 'components/Tooltip'

import Text from 'components/Text'
import { PlusCircle, Layers } from 'react-feather'
import { Link } from 'react-router-dom'
import { SIDE_BAR_HEADER_HEIGHT, SIDE_BAR_TABLET_HEADER_HEIGHT } from '../style'

const Wrapper = styled.div`
  grid-area: side-bar-header;
  ${spacing.ph1};
  ${flex.justify_space_btw.align_center};
  height: ${SIDE_BAR_HEADER_HEIGHT};
  @media only screen and (min-width: 993px) and (max-width: 1170px) {
    height: ${SIDE_BAR_TABLET_HEADER_HEIGHT};
  }
`

const StyledLink = styled(Link)`
  text-decoration: none;
  color: ${({ theme }) => theme.text.default};
  &:hover {
    color: ${({ theme }) => theme.space.default};
    span {
      color: ${({ theme }) => theme.space.default};
    }
  }
  color: ${({ theme }) => theme.text.hairline};
  svg {
    width: 1.2rem;
  }

  ${props =>
    props.hiddenOnTablet &&
    css`
      @media only screen and (max-width: 1170px) {
        display: none;
      }
      @media only screen and (max-width: 992px) {
        display: flex;
      }
    `}
`

const VisibleTabletOnly = styled.div`
  display: none;
  color: ${({ theme }) => theme.text.hairline};
  svg {
    color: ${({ theme }) => theme.text.hairline};
    height: 1.2rem;
  }

  @media only screen and (min-width: 993px) and (max-width: 1170px) {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    width: 100%;
    padding-top: 0.5rem;

    > * + * {
      margin-top: 0.6667rem;
    }
  }
`

/*
  The following forwardRefs ensure the wrapping Tippy components work.
  Here, we're forwarding refs to react-router-dom Links which have
  been styled with styled-components. Ordinarily, styled-components
  forwards refs to DOM nodes, except, it seems when it comes to
  styled react-router-dom Links.
*/
const SitesLink = forwardRef((props, ref) => {
  return (
    <StyledLink innerRef={ref} to="/sites" {...props}>
      <Text weight="medium">Sites</Text>
    </StyledLink>
  )
})
SitesLink.displayName = 'SitesLink'

const AltAllSitesLink = forwardRef((props, ref) => {
  return (
    <StyledLink innerRef={ref} to="/sites" {...props}>
      <Layers />
    </StyledLink>
  )
})
AltAllSitesLink.displayName = 'AltAllSitesLink'

const CreateNewSiteLink = forwardRef((props, ref) => {
  return (
    <StyledLink innerRef={ref} to="/create" {...props}>
      <PlusCircle />
    </StyledLink>
  )
})
CreateNewSiteLink.displayName = 'CreateNewSiteLink'

const AltSitesLink = () => (
  <VisibleTabletOnly>
    <Tooltip
      arrow={true}
      content="View all sites"
      placement="right"
      animation="scale"
      animateFill={false}
      duration={[250, 175]}
      delay={[150, 0]}
      distance={8}
    >
      <AltAllSitesLink />
    </Tooltip>
    <Tooltip
      arrow={true}
      content="Create a site"
      placement="right"
      animation="scale"
      animateFill={false}
      duration={[250, 175]}
      delay={[150, 0]}
      distance={8}
    >
      <CreateNewSiteLink />
    </Tooltip>
  </VisibleTabletOnly>
)

export default function Header() {
  return (
    <Wrapper>
      <Tooltip
        content="View all sites"
        arrow={true}
        placement="right"
        animation="scale"
        animateFill={false}
        duration={[250, 175]}
        delay={[150, 0]}
        distance={8}
      >
        <SitesLink hiddenOnTablet />
      </Tooltip>
      <Tooltip
        arrow={true}
        content="Create a site"
        placement="right"
        animation="scale"
        animateFill={false}
        duration={[250, 175]}
        delay={[150, 0]}
        distance={8}
      >
        <CreateNewSiteLink hiddenOnTablet />
      </Tooltip>
      <AltSitesLink />
    </Wrapper>
  )
}
