import React from 'react'
import { Wrapper } from './style'
import { Route } from 'react-router-dom'
import Nav from './components/Nav'
import SiteOverview from 'views/SiteOverview'
import Environments from 'views/Environments'
import Commit from 'views/Commit'
import CommitsByBranch from 'views/CommitsByBranch'
import SiteSettings from 'views/SiteSettings'
import Feedback from 'views/Feedback'
import config from 'config'
import { useSubscription } from '@apollo/react-hooks'
import { useParams } from 'react-router'

import {
  RELEASE_STATUS_V2_UPDATE,
  DEPLOYMENT_STATUS_EVENTS,
  COMMIT_EVENTS,
} from 'graphql/subscriptions'
import {
  updateReleaseStatus,
  updateDeploymentStatus,
  updateCommitsFeed,
} from 'helpers/updateQueryMethods'

function useSiteSubscriptions({ sitename }) {
  useSubscription(RELEASE_STATUS_V2_UPDATE, {
    variables: { sitename },
    onSubscriptionData: ({ client, subscriptionData }) =>
      updateReleaseStatus(sitename, client, subscriptionData),
  })
  useSubscription(DEPLOYMENT_STATUS_EVENTS, {
    variables: { sitename },
    onSubscriptionData: ({ client, subscriptionData }) =>
      updateDeploymentStatus(sitename, client, subscriptionData),
  })
  useSubscription(COMMIT_EVENTS, {
    variables: { sitename },
    onSubscriptionData: ({ client, subscriptionData }) =>
      updateCommitsFeed(sitename, client, subscriptionData),
  })
}

let lastSitename = null

export default function Site() {
  const { sitenameSlug } = useParams()
  const isDemo = config.DEMO_MODE
  useSiteSubscriptions({ sitename: sitenameSlug })
  const fetchPolicy =
    sitenameSlug === lastSitename ? 'cache-first' : 'network-only'
  lastSitename = sitenameSlug
  return (
    <Wrapper>
      <Nav isDemo={isDemo} />
      <Route exact path="/sites/:sitenameSlug">
        <SiteOverview fetchPolicy={fetchPolicy} />
      </Route>
      <Route path="/sites/:sitenameSlug/settings">
        <SiteSettings />
      </Route>
      <Route exact path="/sites/:sitenameSlug/environments">
        <Environments />
      </Route>
      <Route path="/sites/:sitenameSlug/commits/:branchSlug">
        <CommitsByBranch />
      </Route>
      <Route exact path="/sites/:sitenameSlug/commit/:commitShaSlug">
        <Commit />
      </Route>
      <Route exact path="/sites/:sitenameSlug/feedback">
        <Feedback />
      </Route>
    </Wrapper>
  )
}
