import React, { useState } from 'react'
import { Text, TextField, StatusMessage } from 'components'
import { Button, TextButton } from 'components/Buttons'
import { useMutation } from '@apollo/react-hooks'
import { CREATE_NEW_ENVIRONMENT } from 'graphql/mutations'
import { GET_ENVIRONMENTS } from 'graphql/queries'
import styled from 'styled-components'
import { flex, spacing, border } from 'styles/traits'
import { PlusCircle } from 'react-feather'
import { Shadow, hexa } from 'styles/globals'
import { useToggle } from 'hooks'
import { FlexRow } from 'components/FlexSections'
import Stack from 'components/Stack'

const Form = styled.form`
  ${flex.vertical};
  ${border.ba.br2};
  max-width: 640px;
  border-color: ${({ theme }) => theme.bg.border};
  background: ${({ theme }) => theme.bg.default};
  box-shadow: ${Shadow.low} ${({ theme }) => hexa(theme.bg.reverse, 0.2)};
`

const CenteredFlexRow = styled(FlexRow)`
  ${flex.center_both};
`

const NewFormButton = styled.button`
  ${border.bn.br2}
  ${flex.center_both};
  ${spacing.ph2.pv1};
  width: 640px;
  background: none;
  cursor: pointer;
  color: ${({ theme }) => theme.text.default};
  svg {
    height: 1rem;
  }
  &:hover {
    background: ${({ theme }) => theme.bg.wash};
  }
`

const useEnvName = (initialState = '') => {
  const [state, setState] = useState(initialState)
  const setName = text => {
    if (text.length < 40) {
      const legalName = text
        .replace(/\W+/g, '')
        .replace(/_/, '')
        .toUpperCase()
      setState(legalName)
    }
  }
  return [state, setName]
}

const NewEnvironment = props => {
  const [showForm, toggle] = useToggle(false)
  const [name, setName] = useEnvName('')

  const sitename = props.sitename

  const mutationVariables = () => ({
    input: {
      env_name: name,
      sitename: props.sitename,
      settings: [],
    },
  })

  const reset = () => {
    toggle(false)
    setName('')
  }

  const isDuplicate = props.environments.some(
    item => item.env_name === name.toLowerCase()
  )
  const isValid = !isDuplicate && name.length > 0

  const [mutation, { loading, error }] = useMutation(CREATE_NEW_ENVIRONMENT, {
    variables: mutationVariables(),
    onCompleted: reset,
    refetchQueries: [
      { query: GET_ENVIRONMENTS, variables: { name: sitename } },
    ],
  })

  if (showForm) {
    return (
      <Form
        onSubmit={event => {
          mutation(mutationVariables())
          event.preventDefault()
        }}
      >
        <Stack padding={1}>
          <CenteredFlexRow internalSpacing={2}>
            <Text>Environment name</Text>
            <TextField
              inputProps={{
                maxLength: 10,
                value: name,
                type: 'text',
                isRequired: true,
                onChange: event => setName(event.target.value),
              }}
            />
            <TextButton onClick={reset} type="button" disabled={loading}>
              Cancel
            </TextButton>
            <Button type="submit" disabled={!isValid} loading={loading}>
              Create
            </Button>
          </CenteredFlexRow>
          {error && <StatusMessage>{error.message}</StatusMessage>}
          {!isDuplicate === false && (
            <StatusMessage status="critical">
              <Text>{`Environment with name: '${name}' already exists on '${sitename}'`}</Text>
            </StatusMessage>
          )}
        </Stack>
      </Form>
    )
  } else {
    return (
      <NewFormButton onClick={toggle}>
        <PlusCircle /> <Text>Add new environment</Text>
      </NewFormButton>
    )
  }
}

export default NewEnvironment
