import React from 'react'
import { useToggle } from 'hooks'
import Header from './components/Header'
import Block from './components/Block'
import { Container, Content } from './style'

const TestLog = props => {
  const [logVisible, toggle] = useToggle(true)
  const { description, logs, status } = props.result
  return (
    <Container id="test_log">
      <Header
        expanded={logVisible}
        status={status}
        description={description}
        toggleExpanded={toggle}
      />
      <Content expanded={logVisible}>
        {logs &&
          logs.map((block, index) => <Block key={index} block={block} />)}
        {logs && logs.length === 0 && <Block block={{ log: 'No test log' }} />}
      </Content>
    </Container>
  )
}

export default TestLog
