import React from 'react'
import { TextPlaceholderStyle, ImagePlaceholderStyle } from './style'

const randomInterval = (min, max) =>
  Math.floor(Math.random() * (max - min + 1)) + min

export const TextPlaceholder = ({ minWidth, maxWidth }) => {
  const width = randomInterval(minWidth, maxWidth)
  const duration = randomInterval(1, 5)
  return <TextPlaceholderStyle width={width} duration={duration} />
}

TextPlaceholder.defaultProps = {
  maxWidth: 6,
  minWidth: 2,
}

export const ImagePlaceholder = ({ width, height }) => {
  const duration = randomInterval(1, 5)
  return (
    <ImagePlaceholderStyle width={width} height={height} duration={duration} />
  )
}

ImagePlaceholder.defaultProps = {
  width: 2,
  height: 2,
}
