import React from 'react'
import CommitsTable from 'views/CommitsTable'
import { NoBuilds, AttemptedBuild } from './OnboardingMessages'
import {
  commitsFeedOnboardingLevel,
  ATTEMPTED_BUILD,
  NO_BUILDS,
} from 'helpers/onboardingLevel'
import { useQuery } from '@apollo/react-hooks'
import { GET_COMMITS_FEED } from 'graphql/queries'
import {
  enterAnimation,
  leaveAnimation,
  StyledFlipMove,
  StyledFlexRow,
} from './style'
import { SiteAccessError } from 'components/Errors'
import Stack from 'components/Stack'
import { Text } from 'components'
import { formatCommitsByBranch } from './utils'
import { useParams } from 'react-router'
import Badge from 'components/Badge'
import NoCommits from './NoCommits'
import ExternalCIMessage from './ExternalCIMessage'

const buildInProgress = commits => {
  return commits.some(({ build }) => {
    if (build) {
      return (
        build.status === null ||
        build.status === 'queued' ||
        build.status === 'pending' ||
        build.status === 'running'
      )
    } else {
      return false
    }
  })
}

export default function CommitsFeed(props) {
  const { sitenameSlug } = useParams()
  const { loading, error, data } = useQuery(GET_COMMITS_FEED, {
    variables: { name: sitenameSlug, limit: 20 },
    fetchPolicy: props.fetchPolicy,
  })

  if (loading) {
    return (
      <Stack gap={2}>
        <Text scale={4} weight="bold">
          Recent Commits
        </Text>
        <Stack gap={3}>
          <CommitsTable loading />
        </Stack>
      </Stack>
    )
  }

  if (error) {
    return (
      <Stack gap={2}>
        <Text scale={4} weight="bold">
          Recent Commits
        </Text>
        <SiteAccessError sitename={sitenameSlug} item="recent commits" />
      </Stack>
    )
  }

  const site = data?.view?.site
  if (site) {
    const external_ci_settings = site?.settings?.external_ci_settings
    const external_ci_enabled = external_ci_settings?.external_ci_enabled
    const fab_upload_api_key = external_ci_settings?.fab_upload_api_key

    const environments = site?.environments
    const commits = site?.commitsFeed?.commits || []

    const onbording_level = commitsFeedOnboardingLevel(commits)
    const hideOnboardingMessages = !buildInProgress(commits)

    const options = {
      [NO_BUILDS]: <NoBuilds id="nobuilds" />,
      [ATTEMPTED_BUILD]: (
        <AttemptedBuild id="attemptedbuild" sitename={sitenameSlug} />
      ),
    }

    const commitsByBranch = formatCommitsByBranch(commits)

    return (
      <Stack gap={2}>
        {external_ci_enabled && commits.length === 0 ? (
          <ExternalCIMessage apiKey={fab_upload_api_key} />
        ) : (
          <StyledFlexRow>
            <Text scale={4} weight="bold">
              Recent Commits
            </Text>
            {external_ci_enabled && (
              <Badge color="blue">External CI enabled</Badge>
            )}
          </StyledFlexRow>
        )}

        {(commits.length > 0 &&
          hideOnboardingMessages &&
          options[onbording_level]) ||
          null}

        {!external_ci_enabled && commits.length === 0 && <NoCommits />}

        <StyledFlipMove
          enterAnimation={enterAnimation}
          leaveAnimation={leaveAnimation}
          easing="cubic-bezier(.24,.7,.13,1.08)"
          duration={900}
        >
          {commits.length > 0 &&
            commitsByBranch.map(item => {
              const { branch, pullRequest, commits } = item
              return (
                <CommitsTable
                  key={branch}
                  branch={branch}
                  commits={commits}
                  sitename={sitenameSlug}
                  pullRequest={pullRequest}
                  environments={environments}
                />
              )
            })}
        </StyledFlipMove>
      </Stack>
    )
  } else {
    return (
      <Stack gap={2}>
        <Text scale={4} weight="bold">
          Recent Commits
        </Text>
        <SiteAccessError sitename={sitenameSlug} item="recent commits" />
      </Stack>
    )
  }
}
