import React from 'react'
import styled from 'styled-components'
import { Text } from 'components'
import { flex, spacing, typography, grid } from 'styles/traits'
import { Transition, Truncate } from 'styles/globals'
import { standardPreviewLink } from 'helpers/previewLinks'
import { GitCommit, GitBranch, Hash, ExternalLink } from 'react-feather'
import Avatar from 'components/Avatar'
import { FlexRow } from 'components/FlexSections'
import Inline from 'components/Inline'
import Stack from 'components/Stack'
import getEnvColor from 'helpers/envColors'
import RebuildButton from './RebuildButton'
import { useParams } from 'react-router'
import Card from 'components/Card'

const Inner = styled.div`
  ${flex.justify_space_btw};
  @media only screen and (max-width: 490px) {
    ${flex.vertical};
    > * + * {
      ${spacing.mt1};
    }
  }
`

const Anchor = styled.a`
  ${flex.align_center};
  text-decoration: none;
  color: inherit;
  &:hover {
    span {
      color: ${({ theme }) => theme.space.default};
    }
    svg {
      color: ${({ theme }) => theme.space.default};
    }
  }
  > * + * {
    ${spacing.ml05};
  }
  cursor: pointer;
`

const MessageContainer = styled.div`
  ${grid.auto_cols.g1.align_center};
  ${Truncate};
  max-width: 50rem;
`

const PreviewLinkContainer = styled(FlexRow)`
  ${flex.align_center};
  flex-wrap: wrap;
`

const PreviewLinkStyle = styled.a`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border-style: solid;
  border-width: 2px;
  border-color: ${({ color }) => color};
  color: ${({ color }) => color};
  border-radius: 0.25rem;
  ${spacing.ph05.pv025};
  text-decoration: none;
  transition: ${Transition.hover.on};

  svg {
    width: 1rem;
    height: 1rem;
  }

  span {
    ${typography.f6};
    color: inherit;
  }

  &:hover {
    background: ${({ color }) => color};
    color: white;
  }

  > * + * {
    ${spacing.ml05};
  }
`

const PreviewLinks = ({ sitename, bundle_id, environments }) => {
  return (
    <PreviewLinkContainer internalSpacing={0.5}>
      <Text weight="medium">Preview Links:</Text>
      {environments.map((item, index) => {
        const previewUrl = standardPreviewLink({
          sitenameSlug: sitename,
          bundleIdSlug: bundle_id,
          environmentSlug: item.env_name,
        })
        return (
          <PreviewLinkStyle
            key={index}
            rel="noopener noreferrer"
            href={previewUrl}
            color={getEnvColor(item.env_name, index)}
            target="_blank"
          >
            <Text weight="bold">{item.env_name}</Text>
            <ExternalLink />
          </PreviewLinkStyle>
        )
      })}
    </PreviewLinkContainer>
  )
}

const IconAndText = styled.div`
  ${typography.no_underline.bold.f5};
  ${flex.align_center};
  color: ${({ theme }) => theme.text.default};
  svg {
    color: ${({ theme }) => theme.text.default};
    width: 1.1rem;
    padding-right: 0.5rem;
  }
`

export default function Header({
  tree_id,
  environments,
  branch,
  head_commit,
  build,
  repository,
}) {
  const { sitenameSlug, commitShaSlug } = useParams()
  const { message, author } = head_commit
  const bundle_id = build?.bundle_id
  // const { bundle_id } = build
  const githubUserUrl = author.username
    ? `https://github.com/${author.username}`
    : ''
  const branchLink = `${repository}/tree/${branch}`
  const shaLink = `${repository}/commit/${commitShaSlug}`
  return (
    <Card>
      <Inner>
        <Stack>
          <Inline gap={1}>
            <IconAndText
              title="Commit SHA"
              as="a"
              href={shaLink}
              target="_blank"
            >
              <GitCommit /> {commitShaSlug.slice(0, 9)}
            </IconAndText>
            <IconAndText
              title="Branch"
              as="a"
              href={branchLink}
              target="_blank"
            >
              <GitBranch /> {branch}
            </IconAndText>
            <IconAndText title="Tree ID">
              <Hash /> {tree_id.slice(0, 9)}
            </IconAndText>
          </Inline>
          <MessageContainer>
            {author.name && (
              <Anchor href={githubUserUrl} target="_blank">
                <Avatar name={author.name} username={author.username} />
                <Text weight="bold">{author.name}</Text>
              </Anchor>
            )}
            {message && (
              <Text weight="medium" fontStyle="italic">
                {message}
              </Text>
            )}
          </MessageContainer>
          {sitenameSlug && environments && bundle_id && (
            <PreviewLinks
              sitename={sitenameSlug}
              environments={environments}
              bundle_id={bundle_id}
            />
          )}
        </Stack>
        {build && (
          <div>
            <RebuildButton
              sitename={sitenameSlug}
              tree_id={tree_id}
              buildStatus={build.status}
            />
          </div>
        )}
      </Inner>
    </Card>
  )
}
