import React, { useState } from 'react'
import { Text, StatusMessage, TextField } from 'components'
import { TextButton, Button } from 'components/Buttons'
import { GET_DATA, UPDATE_BUILD_CONFIG } from '../../graphql'
import { Form, FormHeader, FormBody, FormFooter } from 'components/FormElements'
import Inline from 'components/Inline'
import { AddVariableButton, DeleteVariableButton } from './style'
import { X, PlusCircle } from 'react-feather'
import { useBuildEnvVariables } from './utils'
import { useMutation } from '@apollo/react-hooks'

// strips out __typename
const mapToInitialState = variables =>
  variables.map(variable => ({
    name: variable.name,
    value: variable.value,
  }))

export default function VariablesForm(props) {
  const { sitename } = props
  const initialVariablesState = mapToInitialState(props.variables)
  const variables = useBuildEnvVariables(initialVariablesState)
  const [readOnly, setReadOnly] = useState(true)
  const [dirty, setDirty] = useState(false)
  const { admin } = props.siteAccess

  const editForm = () => setReadOnly(false)

  const [updateBuildConfig, { loading, error }] = useMutation(
    UPDATE_BUILD_CONFIG,
    {
      variables: {
        sitename,
        input: { variables: variables.all },
      },
      refetchQueries: [{ query: GET_DATA, variables: { name: sitename } }],
      onCompleted: () => {
        setReadOnly(true)
        setDirty(false)
      },
    }
  )

  const reset = () => {
    setReadOnly(true)
    variables.reset()
    setDirty(false)
  }

  return (
    <Form
      onSubmit={e => {
        updateBuildConfig()
        e.preventDefault()
      }}
    >
      <FormHeader>
        <Text scale={4} weight="medium">
          Environment Variables
        </Text>
      </FormHeader>
      <FormBody>
        <Text scale={6} tone="secondary">
          These variables are used during build time
        </Text>
        {variables.all.length === 0 && (
          <Text scale={6} tone="secondary">
            No build environment variables are set
          </Text>
        )}
        {variables.all.length > 0 &&
          variables.all.map((variable, index) => {
            return (
              <Inline key={index} internalSpacing={0.5}>
                <TextField
                  size={16}
                  inputProps={{
                    required: true,
                    placeholder: 'name',
                    value: variable.name,
                    readOnly: readOnly || !admin,
                    onChange: e => {
                      variables.setVariableName(e.target.value, index)
                      setDirty(true)
                    },
                  }}
                />
                <TextField
                  masked={readOnly}
                  canBeRevealed={admin}
                  inputProps={{
                    required: true,
                    placeholder: 'value',
                    value: variable.value,
                    readOnly: readOnly || !admin,
                    onChange: e => {
                      variables.setVariableValue(e.target.value, index)
                      setDirty(true)
                    },
                  }}
                />
                {!readOnly && admin && (
                  <DeleteVariableButton
                    onClick={() => {
                      variables.deleteVariable(index)
                      setDirty(true)
                    }}
                    type="button"
                  >
                    <X />
                  </DeleteVariableButton>
                )}
              </Inline>
            )
          })}
        {!readOnly && admin && (
          <AddVariableButton
            onClick={() => {
              variables.addVariable()
              setDirty(true)
            }}
            type="button"
          >
            <PlusCircle /> <Text>Add variables</Text>
          </AddVariableButton>
        )}
        {error && (
          <StatusMessage status="critical">
            <Text>{error.message}</Text>
          </StatusMessage>
        )}
      </FormBody>
      {admin && (
        <FormFooter>
          {readOnly ? (
            <Button onClick={editForm}>Edit</Button>
          ) : (
            <Inline gap={1}>
              {dirty && (
                <TextButton type="button" onClick={reset} disabled={loading}>
                  Cancel
                </TextButton>
              )}
              <Button
                loading={loading}
                type="submit"
                disabled={loading || !variables.isValid || !dirty}
              >
                Save
              </Button>
            </Inline>
          )}
        </FormFooter>
      )}
    </Form>
  )
}
