import gql from 'graphql-tag'

export const GET_DATA = gql`
  query($name: String!) {
    view {
      site(name: $name) {
        access {
          admin
        }
        settings {
          access_control {
            collaborators {
              name
              login
              user_id
              avatar_url
              access_level
              is_current_user
            }
          }
        }
      }
    }
  }
`

export const ADD_SITE_COLLABORATOR = gql`
  mutation(
    $sitename: String!
    $user_id: String!
    $access_level: SiteAccessEnum!
  ) {
    addSiteCollaborator(
      sitename: $sitename
      user_id: $user_id
      access_level: $access_level
    )
  }
`

export const UPDATE_SITE_COLLABORATOR = gql`
  mutation(
    $sitename: String!
    $user_id: String!
    $access_level: SiteAccessEnum!
  ) {
    updateSiteCollaborator(
      sitename: $sitename
      user_id: $user_id
      access_level: $access_level
    )
  }
`

export const REMOVE_SITE_COLLABORATOR = gql`
  mutation($sitename: String!, $user_id: String!) {
    removeSiteCollaborator(sitename: $sitename, user_id: $user_id)
  }
`
