import React, { useState, Fragment } from 'react'
import { useMutation } from '@apollo/react-hooks'
import Select from 'react-select'

import { Form, FormHeader, FormBody, FormFooter } from 'components/FormElements'
import { Text, StatusMessage, TextField } from 'components'
import { TextButton, Button } from 'components/Buttons'
import Inline from 'components/Inline'
import Stack from 'components/Stack'

import { GET_DATA, UPDATE_BUGHERD_SETTINGS } from '../../graphql'
import Projects from './Projects'

const mapProjectsTpOptions = projects =>
  projects.map(project => ({
    value: project.id,
    label: project.name,
  }))

const DefaultForm = props => {
  const { sitename, settings } = props
  const { authentication_token, selected_project, projects } = settings

  const [dirty, setDirty] = useState(false)
  const [authToken, setAuthToken] = useState(authentication_token)
  const [selectedProject, setSelectedProject] = useState(selected_project.id)

  const { admin } = props.siteAccess

  const resetForm = () => {
    setAuthToken(authentication_token)
    setSelectedProject(selected_project.id)
    setDirty(false)
  }

  const mutationVariables = () => ({
    sitename,
    input: {
      authentication_token: authToken,
      project_id: selectedProject,
    },
  })

  const refetchQueries = [{ query: GET_DATA, variables: { name: sitename } }]
  const [mutate, { loading, error }] = useMutation(UPDATE_BUGHERD_SETTINGS, {
    variables: mutationVariables(),
    refetchQueries: refetchQueries,
    onCompleted: () => setDirty(false),
  })

  const changeAuthToken = e => {
    setDirty(true)
    setAuthToken(e.target.value)
  }

  const selectProject = option => {
    setDirty(true)
    setSelectedProject(option.value)
  }

  const submit = e => {
    mutate()
    e.preventDefault()
  }

  const options = mapProjectsTpOptions(projects)
  const defaultValue = options.find(
    option => option.value === selected_project.id
  )

  const authTokenModified = authentication_token !== authToken
  const isValid = authToken.length === 22 && selectedProject

  return (
    <Form onSubmit={submit}>
      <FormHeader>
        <Text scale={4} weight="medium">
          BugHerd
        </Text>
      </FormHeader>
      <FormBody>
        <Stack>
          <Text weight="bold">Authentication Token</Text>
          <Text scale={6}>
            Find this token in your BugHerd organization settings
          </Text>
          <TextField
            inputProps={{
              readOnly: !admin || loading,
              value: authToken,
              onChange: changeAuthToken,
            }}
          />

          {authTokenModified ? (
            <Projects authToken={authToken} selectProject={selectProject} />
          ) : (
            <Fragment>
              <Text weight="bold">Selected Project</Text>
              <Select
                options={options}
                onChange={selectProject}
                defaultValue={defaultValue}
              />
            </Fragment>
          )}
          {error && (
            <StatusMessage status="critical">
              <Text>{error.message}</Text>
            </StatusMessage>
          )}
        </Stack>
      </FormBody>
      {admin && (
        <FormFooter>
          <Inline gap={1}>
            {dirty && (
              <TextButton type="button" onClick={resetForm} disabled={loading}>
                Cancel
              </TextButton>
            )}
            <Button
              type="submit"
              loading={loading}
              disabled={!dirty || !isValid}
            >
              Save
            </Button>
          </Inline>
        </FormFooter>
      )}
    </Form>
  )
}

export default DefaultForm
