import styled, { css } from 'styled-components'
import { RadialGradient, Shadow, Transition, hexa } from 'styles/globals'
import { typography, spacing, flex, border } from 'styles/traits'

const baseButton = css`
  ${typography.bold};
  ${flex.center_both};
  ${border.br1};
  ${spacing.pv05.ph1};
  ${border.bn};
  transition: ${Transition.hover.off};
  flex: none;
  align-self: center;
  white-space: nowrap;
  word-break: keep-all;
  cursor: pointer;
  line-height: 1;
  position: relative;
  text-align: center;
  ${props => (props.large ? typography.f4 : typography.f5)};
  padding: ${props => (props.large ? '.66rem 1.5rem' : '0.5rem 1rem')};

  &:hover {
    transition: ${Transition.hover.on};
    box-shadow: ${props =>
      props.disabled
        ? 'none'
        : `${Shadow.high} ${hexa(props.theme.bg.reverse, 0.15)}`};
    opacity: ${props => (props.disabled ? '0.5' : '1')};
  }
`

export const Label = styled.span`
  display: block;
  flex: 0 0 auto;
  line-height: inherit;
  color: inherit;
  ${props => (props.loading ? 'opacity: 0;' : 'opacity: 1;')};
  align-self: center;
  margin: auto;
`

export const StyledSolidButton = styled.button`
  ${baseButton};
  background-color: ${props =>
    props.disabled ? props.theme.bg.inactive : props.theme.brand.alt};
  background-image: ${props =>
    props.disabled
      ? 'none'
      : RadialGradient(props.theme.brand.alt, props.theme.brand.default)};

  ${({ color }) =>
    color &&
    css`
      background: ${color};
    `};

  color: ${({ theme }) => theme.text.reverse};
  &:hover {
    background-color: ${props =>
      props.disabled ? props.theme.bg.inactive : props.theme.brand.alt};
  }
  &:active {
    box-shadow: ${props =>
      props.disabled
        ? 'none'
        : `${Shadow.low} ${hexa(props.theme.bg.reverse, 0.15)}`};
  }
`

export const StyledTextButton = styled(StyledSolidButton)`
  background: transparent;
  background-image: none;
  color: ${props =>
    props.disabled ? props.theme.bg.inactive : props.theme.text.alt};
  transition: color 0.1s ease-out, box-shadow 0.2s ease-out 0.1s, border-radius 0.2s ease-out, padding: 0.2s ease-out;
  ${props =>
    props.loading
      ? css`
          justify-content: center;
        `
      : css`
          justify-content: flex-start;
        `}
  &:hover {
    background-color: transparent;
    box-shadow: none;
    color: ${props =>
      props.disabled ? props.theme.bg.inactive : props.theme.brand.alt};
    transition: color 0.1s ease-in, box-shadow 0.2s ease-in 0.1s, padding 0.2s ease-in;
  }
`

export const StyledOutlineButton = styled(StyledTextButton)`
  box-shadow: ${props =>
    props.large ? 'inset 0 0 0 .15rem' : 'inset 0 0 0 .1rem'};
  background: transparent;
  background-image: none;

  color: ${props =>
    props.disabled ? props.theme.bg.inactive : props.theme.text.alt};
  transition: color 0.1s ease-out, box-shadow 0.2s ease-out 0.1s, border-radius 0.2s ease-out, padding: 0.2s ease-out;
  ${props =>
    props.loading
      ? css`
          justify-content: center;
        `
      : css`
          justify-content: flex-start;
        `}
  &:hover {
    box-shadow: ${props =>
      props.large ? 'inset 0 0 0 .15rem' : 'inset 0 0 0 .1rem'};
    background-color: transparent;
    color: ${props =>
      props.disabled ? props.theme.bg.inactive : props.theme.brand.alt};
    transition: color 0.1s ease-in, box-shadow 0.2s ease-in 0.1s, padding 0.2s ease-in;
  }

  ${({ color }) =>
    color &&
    css`
      color: ${color};
    `};
`
