import styled, { css } from 'styled-components'

const StatusCell = styled.div`
  /* background: ${({ theme }) => theme.bg.default}; */
  border-style: solid;
  border-color: ${({ theme }) => theme.bg.border};
  border-width: 1px 0 0 1px;
  padding: 0.5rem;
  position: relative;

  display: flex;
  flex-direction: row;
  align-items: center;

  &:hover::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100vw;
    right: -100vw;
    bottom: 0;
    z-index: -1;
    background: ${({ theme }) => theme.space.wash};
  }

  > span {
    display: none;
  }

  > * + * {
      margin-left: 0.5rem;
    }

  @media only screen and (max-width: 768px) {
    min-height: 3rem;
    flex-direction: row;
    align-items: center;
    border: none;
    span {
      display: block;
    }
    > * + * {
      margin-left: 0.5rem;
    }
    ${({ gridArea }) =>
      gridArea === 'build' &&
      css`
        grid-column-start: 1;
        grid-column-end: 2;
      `};
    ${({ gridArea }) =>
      gridArea === 'tests' &&
      css`
        grid-column-start: 2;
        grid-column-end: 3;
      `};
    ${({ gridArea }) =>
      gridArea === 'preview' &&
      css`
        grid-column-start: 3;
        grid-column-end: 4;
      `};
  }
`

export default StatusCell
