import React, { useState } from 'react'
import Select from 'react-select'

import { useMutation } from '@apollo/react-hooks'
import { GET_DATA, UPDATE_SLACK_SETTINGS } from '../../graphql'

import Inline from 'components/Inline'
import Stack from 'components/Stack'
import { Text, StatusMessage } from 'components'
import { Button, TextButton } from 'components/Buttons'
import {
  Form,
  FormHeader,
  FormBody,
  FormFooter,
  CheckboxRowStyle,
} from 'components/FormElements'
import { CheckSquare, Square } from 'react-feather'
import { useLocation } from 'react-router'

const OAuthSuccessMessage = () => (
  <StatusMessage status="positive">
    <Stack>
      <Text weight="bold">Linc Slack app added!</Text>
      <Stack gap={0}>
        <Text.P>
          You can now select a channel from the Slack channels list below.
        </Text.P>
        <Text.P>
          Linc will send preview links along with build status info to this
          channel.
        </Text.P>
      </Stack>
    </Stack>
  </StatusMessage>
)

const mapChannelsToSelectOptions = channels =>
  channels.map(channel => ({
    value: channel,
    label: channel.name,
  }))

const initalSelectValue = (selected_channel, options) =>
  selected_channel
    ? options.find(option => option.value.id === selected_channel.id)
    : null

export default function ChannelsConfig(props) {
  const { sitename } = props
  const { channels, selected_channel } = props
  const options = mapChannelsToSelectOptions(channels)

  const [dirty, setDirty] = useState(false)
  const [selectValue, setSelectValue] = useState(
    initalSelectValue(selected_channel, options)
  )
  const [messagesDisabled, setMessagesDisabled] = useState(
    props.messages_disabled || false
  )

  const resetForm = () => {
    const newSelectValue = initalSelectValue(selected_channel, options)
    setSelectValue(newSelectValue)
    setMessagesDisabled(props.messages_disabled)
    setDirty(false)
  }

  const selectChannel = option => {
    setDirty(true)
    setSelectValue(option)
  }

  const toggleMessagesDisabled = () => {
    setDirty(true)
    setMessagesDisabled(!messagesDisabled)
  }

  const refetchQueries = [{ query: GET_DATA, variables: { name: sitename } }]

  const mutationVariables = () => {
    if (selectValue) {
      return {
        sitename,
        input: {
          selected_channel: {
            id: selectValue.value.id,
            name: selectValue.value.name,
          },
          messages_disabled: messagesDisabled,
        },
      }
    }
  }

  const { search } = useLocation()
  const params = new URLSearchParams(search)
  const oauthSuccess = parseInt(params.get('oauth_success')) === 1

  const { admin } = props.siteAccess

  const [mutation, { loading, error }] = useMutation(UPDATE_SLACK_SETTINGS, {
    variables: mutationVariables(),
    refetchQueries: refetchQueries,
    onCompleted: () => setDirty(false),
  })

  return (
    <>
      {oauthSuccess && <OAuthSuccessMessage />}
      <Form
        id="slack"
        onSubmit={e => {
          mutation(mutationVariables())
          e.preventDefault()
        }}
      >
        <FormHeader>
          <Text scale={4} weight="medium">
            Slack integration
          </Text>
        </FormHeader>
        <FormBody>
          <Text weight="medium">Slack channel</Text>
          <Text.P scale={6} tone="secondary">
            We send build status events plus preview links for{' '}
            <code>{sitename}</code> to this Slack channel:
          </Text.P>
          <Select
            isDisabled={!admin || loading}
            name="select-org"
            value={selectValue}
            options={options}
            onChange={selectChannel}
          />
          <CheckboxRowStyle
            type="button"
            disabled={
              !admin ||
              selected_channel === null ||
              selected_channel === undefined ||
              loading
            }
            onClick={toggleMessagesDisabled}
          >
            {messagesDisabled ? <CheckSquare /> : <Square />}
            <Text>Disable Linc Slack messages</Text>
          </CheckboxRowStyle>
          {error && (
            <StatusMessage status="critical">
              <Text>{error.message}</Text>
            </StatusMessage>
          )}
        </FormBody>
        {admin && (
          <FormFooter>
            <Inline gap={1}>
              {dirty && (
                <TextButton
                  type="button"
                  onClick={resetForm}
                  disabled={loading}
                >
                  Cancel
                </TextButton>
              )}
              <Button type="submit" loading={loading} disabled={!dirty}>
                Save
              </Button>
            </Inline>
          </FormFooter>
        )}
      </Form>
    </>
  )
}
