import React, { Fragment } from 'react'
import PropTypes from 'prop-types'
import Header from './Header'
import BuildLog from 'views/BuildLog'
import TestLog from 'views/TestLog'
import immutable from 'object-path-immutable'
import { COMMIT_EVENTS } from 'graphql/subscriptions'
import Stack from 'components/Stack'
import { Text } from 'components'
import FeedbackCard from 'views/FeedbackCard'

export default class CommitPage extends React.Component {
  componentDidMount() {
    this.unsubscribe = this.subscribe()
  }

  componentWillUnmount() {
    if (this.unsubscribe) {
      this.unsubscribe()
    }
  }

  subscribe = () => {
    const { commit, sitename, subscribeToMore } = this.props
    const tree_id = commit?.tree_id
    if (tree_id && sitename) {
      return subscribeToMore({
        document: COMMIT_EVENTS,
        variables: { sitename: sitename, tree_id: tree_id },
        updateQuery: (prev, { subscriptionData }) => {
          if (!subscriptionData.data) return prev
          const commitEvent = subscriptionData.data.commit_events
          return immutable.set(prev, 'view.site.commit', commitEvent)
        },
      })
    }
  }
  render() {
    const { environments, commit } = this.props
    const { tree_id, branch, commit_sha, head_commit, repository } = commit
    const { message, author } = head_commit
    const allBundleFeedback = commit?.bundle?.allBundleFeedback
    const build = commit?.build
    const test_results = commit?.tests?.test_results
    return (
      <Fragment>
        <Header
          tree_id={tree_id}
          environments={environments}
          branch={branch}
          commit_sha={commit_sha}
          head_commit={head_commit}
          build={build}
          repository={repository}
        />
        <Stack gap={2}>
          {allBundleFeedback && allBundleFeedback.length > 0 && (
            <Stack>
              <Text scale={4} weight="medium">
                Feedback
              </Text>
              {allBundleFeedback.map((item, index) => (
                <FeedbackCard key={index} feedback={item} />
              ))}
            </Stack>
          )}

          {/* BUILD LOG */}
          <Stack gap={1}>
            <Text scale={4} weight="medium">
              Build log
            </Text>
            {build ? (
              <BuildLog
                commit_sha={commit_sha}
                message={message}
                author={author}
                build={build}
              />
            ) : (
              <Text tone="secondary">No build log to display</Text>
            )}
          </Stack>
        </Stack>

        {/* TEST LOGS */}
        <Stack gap={1}>
          <Text scale={4} weight="medium">
            Test logs
          </Text>
          {test_results ? (
            test_results.map((result, index) => (
              <TestLog key={index} result={result} />
            ))
          ) : (
            <Text tone="secondary">No test results to display</Text>
          )}
        </Stack>
      </Fragment>
    )
  }
}

CommitPage.propTypes = {
  view: PropTypes.object,
  sitename: PropTypes.string,
  subscribeToMore: PropTypes.func,
}
