export const SIGN_IN = () => {
  const LOGIN = 'https://graphql.linc.sh/login/github'
  const REDIRECT = encodeURIComponent(window.location.href)
  window.location = LOGIN + '?redirect=' + REDIRECT
}

export const CONFIGURE_INSTALLATIONS = () => {
  const TARGET = 'https://github.com/apps/linc/installations/new'
  const REDIRECT = encodeURIComponent(window.location.href)
  window.location = TARGET + '?redirect=' + REDIRECT
}
